import React, { Component } from 'react';
import Slider from 'react-slick';
import YouTube from 'react-youtube';
import Image from '../../../components/Image/Image';
import StampList from '../../../componentList/StampList/StampList';
import './TerminalFileImages.css';

class TerminalFileImages extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  customPagingFunc = (i) => {
    const { imgs, videos, name } = this.props;
    if (i < imgs.length) {
      const thumbnailImg = imgs[i];
      return (
        <div key={`thumbnail_${i}`} className="terminalFileImages_padding">
          <div className="terminalFileImages_wrapper">
            <Image
              url={thumbnailImg}
              alt={`${name} imagen ${i}`}
              wrapperClass="terminalFileImages_thumbnail"
              opacity="1"
            />
          </div>
        </div>
      );
    // eslint-disable-next-line no-else-return
    } else {
      const videoindex = (i - imgs.length);
      const videoId = videos[videoindex];
      const thumbnailImg = `https://img.youtube.com/vi/${videoId}/0.jpg`;
      return (
        <div key={`thumbnail_${i}`} className="terminalFileImages_padding">
          <div className="terminalFileImages_wrapper terminalFileImages_wrapper-opacity">
            <Image
              url={thumbnailImg}
              alt={`${name} imagen ${i}`}
              wrapperClass="terminalFileImages_thumbnail"
              opacity="1"
            />
            <div className="icon-play terminalFileImages_play" />
          </div>
        </div>
      );
    }
  }

  render() {
    const { imgs, stock, stamps, videos } = this.props;
    const settings = {
      dots: true,
      infinite: true,
      speed: 0,
      fade: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      customPaging: this.customPagingFunc,
      responsive: [
        {
          breakpoint: 768,
          settings: {
            customPaging: i => <button>{i + 1}</button>,
          },
        },
      ],
    };

    return (
      <div className="terminalFileImagesContainer">
        <StampList wrapperClass="stampsOnProductFile" stock={stock} stamps={stamps} opacity="1" />
        <Slider {...settings}>
          {imgs && imgs.map((img, index) => (
            <div key={`carousel_${index.toString()}`} className="terminalFileImages_main-wrapper">
              <Image dynamicResize url={img} alt="main image" wrapperClass="terminalFileImages_main" />
            </div>
          ))}
          {videos && videos.map((video, index) => (
            <div key={`carousel_video_${index.toString()}`} className="terminalFileImages_main-wrapper">
              <YouTube
                videoId={video}
                className="youtubeContent_wrapper"
              />
            </div>
          ))}
        </Slider>
      </div>
    )
  }
}


export default TerminalFileImages;
