export const addCustomCssRule = (selector, styleStr, desktopMediaquery, minAncho) => {
  let css = '';
  let minWidth = 768;

  if (minAncho && parseInt(minAncho, 10) !== Number.isNaN()) {
    minWidth = parseInt(minAncho, 10);
  }

  if (desktopMediaquery) {
    css += `@media (min-width :${minWidth}px){`;
    css += `${selector} {${styleStr}}`;
    css += '}';
  } else {
    css += `${selector} {${styleStr}}`;
  }

  const head = document.head || document.getElementsByTagName('head')[0];
  const style = document.createElement('style');

  style.type = 'text/css';
  if (style.styleSheet) {
    style.styleSheet.cssText = css;
  } else {
    style.appendChild(document.createTextNode(css));
  }
  head.appendChild(style);
}

export const bannerBuildBGImg = (currentBGImg, idBanner) => {
  /* Adding css rules for background image media queries */
  if (currentBGImg && currentBGImg.mobile) {
    const urlPicMobile = currentBGImg.mobile;
    addCustomCssRule(
      `.banner${idBanner}`,
      `background-image: url(${urlPicMobile})`,
      false
    );
  }
  if (currentBGImg && currentBGImg.desktop) {
    const urlPicPC = currentBGImg.desktop;
    addCustomCssRule(
      `.banner${idBanner}`,
      `background-image: url(${urlPicPC})`,
      true
    );
    addCustomCssRule(
      `.banner${idBanner}`,
      'background-size: cover',
      true
    )
  }
}

export const giveRandomNumber = (min, max) => Math.floor(Math.random() * max) + min;