import React from 'react';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import { createMarkup } from '@bit/gerenciatd.utils.react-utils';
import Image from '../../../components/Image/Image';
import './CommercialInfoCompleteOriginalStatic.css';

const CommercialInfoCompleteOriginalStatic = ({ commercialInfo }) => {
  const {
    title,
    mainDescription,
    subTitle,
    secondaryDescription,
    mediaURL,
    layout,
    domId,
  } = commercialInfo;
  return (
    <div id={domId} className="commercialInfoCompleteOriginalStatic_wrapper">
      <div className="container">
        <RowGrid>
          <ColGrid mobileCols={12}>
            <div className="commercialInfoCompleteOriginalStatic_block-wrapper">
              <h3>{title}</h3>
            </div>
            <div className="commercialInfoCompleteOriginalStatic_block-wrapper">
              { mainDescription.map((value, index) => {
                const key = `commercialInfoCompleteOriginalStatic_main_${layout}_${index}`;
                return (
                  <div
                    key={key}
                    dangerouslySetInnerHTML={createMarkup(value)}
                    className="commercialInfoCompleteOriginalStatic_paragraph"
                  />
                )
              })}
            </div>
            <div className="commercialInfoCompleteOriginalStatic_block-wrapper">
              <Image wrapperClass="commercialInfoCompleteOriginalStatic_image-wrapper" alt={title} url={mediaURL} />
            </div>
            <div className="commercialInfoCompleteOriginalStatic_block-wrapper">
              <div className="commercialInfoCompleteOriginalStatic_subtitle">
                {subTitle}
              </div>
            </div>
            <div className="commercialInfoCompleteOriginalStatic_block-wrapper">
              { secondaryDescription.map((value, index) => {
                const key = `commercialInfoCompleteOriginalStatic_secondary_${layout}_${index}`;
                return (
                  <div
                    key={key}
                    dangerouslySetInnerHTML={createMarkup(value)}
                    className="commercialInfoCompleteOriginalStatic_paragraph_small"
                  />
                )
              })}
            </div>
          </ColGrid>
        </RowGrid>
      </div>
    </div>
  )
}
export default CommercialInfoCompleteOriginalStatic;
