import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { renderHelmet, createMicrodataForList } from '../metadata';
import { actions as productActions } from '../../reducers/products';
import { actions as commonsActions } from '../../reducers/commons';
import { getProductCategory } from '../../utils/commonUtils';
import { ELEMENTS_TO_SHOW, GRID_SYSTEM, SEGMENTS } from '../../constants/constants';
import Banner from '../../containers/Banner/Banner'
import AdvantagesBar from '../../containers/AdvantagesBar/AdvantagesBar';
import TerminalListCore from '../../containers/TerminalList/TerminalListCore/TerminalListCore';
import Menu from '../../containers/Menu/Menu';
import { pillBarMenu, arrayBreadCrumbs } from '../../constants/setupBasics'

export const initTerminalList = (props) => {
  const url = window.location.pathname;
  const currentProductCategory = getProductCategory(url);
  
  const data = {
    segment: props.productSegment,
    type: currentProductCategory,
  };
  return { ...data }
}
export const chooseElementsToShow = (segment, props) => {
  if (segment === SEGMENTS.EMPLEADOS) {
    props.updateElementsToShow(ELEMENTS_TO_SHOW.EMPLOYES.DEFAULT);
  } else {
    props.updateElementsToShow(ELEMENTS_TO_SHOW.DEFAULT);
  }
}

class TerminalList extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  static getDerivedStateFromProps(props, state) {
    const { type, segment } = initTerminalList(props);
    if (state.productCategory !== type || state.productSegment !== segment) {
      chooseElementsToShow(segment, props);     
      if (type && segment) {
        props.getProductsGeneric({ type, segment });
      }
      props.updateProductCategory(type);
      props.setCurrentAlias(null);
      return {
        ...state,
        productCategory: type,
        productSegment: segment,
      }
    }
    return { ...state }
  }

  updateProductCategoryFromBar = (productCategory) => {
    const { productSegment } = this.props;
    const data = {
      segment: productSegment,
      type: productCategory,
    }
    this.props.getProductsGeneric(data);
    this.props.updateElementsToShow(ELEMENTS_TO_SHOW.DEFAULT);
  }

  render() {
    const { products, productSegment } = this.props;

    if (!products) {
      return null;
    }
    const { productCategory } = this.state;
    const currentPill = pillBarMenu.find(item => item.value === productCategory);
    const newArrayBreadCrumbs = [
      ...arrayBreadCrumbs,
      {
        id: 2,
        name: `${currentPill.label}`,
        url: `${currentPill.link}`,
      },
    ];
    const myCurrentGrid = GRID_SYSTEM[productSegment];
    if (!myCurrentGrid) {
      return null;
    }
    return (
      <Fragment>
        {myCurrentGrid.helmetFeatures && renderHelmet(productCategory)}
        {myCurrentGrid.pageInfo && (
          <Menu
            productCategory={productCategory}
            arrayBreadCrumbs={newArrayBreadCrumbs}
            productSegment={productSegment}
          />
        )}
        {myCurrentGrid.banner && (<Banner />)}
        {myCurrentGrid.advantagesBar && (<AdvantagesBar />)}
        <TerminalListCore />
        {myCurrentGrid.helmetFeatures && (productCategory && products[productCategory]) && (
          createMicrodataForList(productCategory, products[productCategory])
        )}
      </Fragment>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  getProductsGeneric: data => dispatch(productActions.getProductsGeneric(data)),
  updateProductCategory: category => dispatch(productActions.updateProductCategory(category)),
  updateProductSegment: segment => dispatch(productActions.updateProductSegment(segment)),
  updateElementsToShow: elementsToShow => dispatch(
    productActions.updateElementsToShow(elementsToShow),
  ),
  setCurrentAlias: alias => dispatch(productActions.setCurrentAlias(alias)),
  setGridVisible: visible => dispatch(commonsActions.setGridVisible(visible)),
})

const mapStateToProps = state => ({
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
  products: state.products.products,
  productsError: state.products.productsError,
  productsFetching: state.products.productsFetching,
  elementsToShow: state.products.elementsToShow,
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalList);
