import React, { Component } from 'react';
import { connect } from 'react-redux';
import { taggingButtonTypes  } from '@bit/gerenciatd.utils.tagging-utils';
import AnchorSmoothList from '@bit/gerenciatd.react.anchor-smooth-list';
import StickyBar from '@bit/gerenciatd.react.sticky-bar';
import Button, { buttonTypes, buttonThemes } from '@bit/gerenciatd.react.button';
import { modalActions } from '@bit/gerenciatd.react.modal';
import StickyPrice from '../../StickyPrice/StickyPrice';
import { launchIsClientModal } from '../../../utils/dataHandlers/purchaseDataHandler';
import './TerminalFileStickyBar.scss';

class TerminalFileStickyBar extends Component {
  constructor(props) {
    super(...props);
    this.state = {
      stickyOffset: 0,
    }
  }

  setStickyHeight = (heigth) => {
    this.setState({ stickyOffset: heigth });
  }

  handleClick = () => {
    const { offerSelected, terminal, isClient } = this.props;
    if (!launchIsClientModal(offerSelected, terminal, isClient)) {
      this.props.setModalVisibility('lbPaymentType');
    }
  }

  render() {
    const { options, terminal, offerSelected } = this.props;
    const alias = terminal.alias.replace('ntt_', '');
    if (!options) {
      return null;
    }

    const {
      stock,
    } = offerSelected;

    return (
      <StickyBar wrapperClass="stickyBar_wrapper shadow" setStickyHeigth={this.setStickyHeight}>
        <div className="stickyContainer">
          <AnchorSmoothList
            wrapperClass="stickyBar_AnchorSmoothList-font"
            anchorsArray={options}
            offset={(this.state.stickyOffset + 60)}
          />
          <div className="amount">
            <StickyPrice wrapperClass="mobile" />
            { stock > 0 && (
              <Button
                label="LO QUIERO"
                taggingType={taggingButtonTypes.BUTTON}
                id={`PA-ficha-movil-flotante-loquiero-${alias}`}
                theme={buttonThemes.GREEN} 
                type={buttonTypes.SOLID} 
                onClick={this.handleClick} 
              />
            )}
          </div>
          <StickyPrice wrapperClass="mobile" />
          <Button
            wrapperClass="mobile"
            label="LO QUIERO"
            taggingType={taggingButtonTypes.BUTTON}
            id={`PA-ficha-movil-flotante-loquiero-${alias}`}
            theme={buttonThemes.GREEN} 
            type={buttonTypes.SOLID} 
            onClick={this.handleClick} />
        </div>
      </StickyBar>
    )
  }
}

const mapStateToProps = state => ({
  offerSelected: state.products.offerSelected,
  isClient: state.products.isClient,
})

const mapDispatchToProps = dispatch => ({
  setModalVisibility: idModal => dispatch(modalActions.setModalVisibility(idModal)),
})


export default connect(mapStateToProps , mapDispatchToProps)(TerminalFileStickyBar);
