import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import './StickyPrice.scss';
import { connect } from 'react-redux';
import Label from '../../components/Label/Label';

/* jsx, proptypes SOY UN COMPONENTE, hazme escalable porfavor recibir el men por props... */
/* */
const StickyPrice = ({ productsSelected, isClient, wrapperClass }) => {
  const { clientPrice, currentPrice, quota, showFragmentedQuota, fragmentedQuota } = productsSelected;
  return (
    <div className={`menuWrapper sticyPrice_container ${wrapperClass}`}>
      {(isClient) && (
        <Fragment>
          <Label wrapperLabel="stickyPrice_wrapper" text="Desde" />
          <Label wrapperLabel="stickyPrice_wrapper-price" text={`${clientPrice}  `} />
          {(showFragmentedQuota) && (
            <Label wrapperLabel="stickyPrice_wrapper append" text={`(${fragmentedQuota} /mes)`} />
          )}
          {(!showFragmentedQuota) && (
            <Label wrapperLabel="stickyPrice_wrapper append" text={`(${quota} /mes)`} />
          )}
        </Fragment>
      )}
      {(!isClient) && (
        <Fragment>
          <Label wrapperLabel="stickyPrice_wrapper" text="Desde" />
          <Label wrapperLabel="stickyPrice_wrapper-price block" text={`${currentPrice} `} />
        </Fragment>
      )}
    </div>
  )
}

StickyPrice.propTypes = {
  labelText: PropTypes.string,
}

StickyPrice.defaultProps = {
  labelText: '',
}

const mapStateToProps = state => ({
  productsSelected: state.products.offerSelected,
  isClient: state.products.isClient,
})

export default connect(mapStateToProps)(StickyPrice);
