import axios from 'axios';
import { call, put, select } from 'redux-saga/effects';
import { actions as productsActions } from '../reducers/products';
import { actions as commonActions } from '../reducers/commons';
import { API } from '../constants/api';
import { normalizerAllTerminals } from './normalizers/productNormalizer';
import { normalizerProductDetailed } from './normalizers/productDetailedNormalizer';
import { LOCALHOST, SERVICES } from '../constants/constants';
import {
  allFilters as constantsBasicsFilters,
  extractFiltersFromUrl,
} from '../constants/filters';
import { getBasicFilter } from './handlers/products';
import {
  prepareGetParamsForSaga,
  makeItPlainArray,
  need2BeSaveTimeStamp,
  analyzeResponse,
  getHostFromUrl,
  getURIforBackup,
} from '../utils/commonUtils';
import { webServices, processName } from './webServices';
import {
  sendJSError,
  sendLoadingService,
  sendLoadedService,
  finishStates,
} from '../utils/monitoring/monitoring';
import terminalListBannerLocalJson from '../terminalListBanner.json';
import legalConditionsLocalJson from '../legalConditions.json';

export const fetchProducts = (data) => {
  const queryParams = prepareGetParamsForSaga(data);
  return axios({
    method: 'get',
    url: `${API.PRODUCTS.PHONES}${queryParams}`,
  })
}

export const fetchProductsBackup = (backupURI) => {
  let resourceURL = '';
  if (getHostFromUrl() === LOCALHOST) {
    resourceURL = API.PRODUCTS.ORIGIN_DOMAIN;
  }
  resourceURL = `${resourceURL}${backupURI}`;
  return axios({
    method: 'get',
    url: resourceURL,
  })
}

export const fetchPhoneByAlias = (data) => {
  const { alias, segment } = data;
  return axios({
    method: 'get',
    url: `${API.PRODUCTS.PHONE_DETAILS}?segment=${segment}&alias=${alias}`,
  })
}

export const fetchPhoneByAliasBackup = (backupURI) => {
  let resourceURL = '';
  if (getHostFromUrl() === LOCALHOST) {
    resourceURL = API.PRODUCTS.ORIGIN_DOMAIN;
  }
  resourceURL = `${resourceURL}${backupURI}`;
  return axios({
    method: 'get',
    url: resourceURL,
  })
}

export const fetchTerminalListBanner = () => {
  return axios({
    method: 'get',
    url: API.PRODUCTS.TERMINAL_LIST_BANNER,
  })
}

