/* Main method to get basic filters */

export const getAllFiltersFromRedux = state => state.products.filtersStatus;
export const getApiRecord = state => state.commons.apiRecords;
export const getProductSegment = state => state.products.productSegment;
export const getProductsFromStore = state => state.products.products;

export function* getProductsGeneric(payload) {
  try {
    // monitoring
    let myEvent = webServices.GET_ALL_ITEM;
    if (IC && IC.createEvent) {
      myEvent = IC.createEvent(webServices.GET_ALL_ITEM);
    }
    const { data } = payload;
    const { type, segment } = data;
    const service = SERVICES.LIST;
    /* Api record begins */
    const apiRecord = yield select(getApiRecord);
    const lastSeconds = (apiRecord.getAllItem && apiRecord.getAllItem[type])
      ? apiRecord.getAllItem[type] : null;
    const nowSeconds = Date.now();
    const need2BeSaveTimeStampFlag = need2BeSaveTimeStamp(nowSeconds, lastSeconds);
    /* Api record ends */
    const lastProducts = yield select(getProductsFromStore);
    let product2Response = null;
    if (!lastProducts[type] || need2BeSaveTimeStampFlag) {
      yield put(commonActions.setApiRecord(`getAllItem-|-${type}`, nowSeconds));
      // Prepare response object
      let response = {};
      sendLoadingService(processName, myEvent);
      try {
        response = yield call(fetchProducts, data);
        analyzeResponse(response);
        sendLoadedService(processName, myEvent, finishStates.FINISHED_OK);
      } catch (error) {
        const backupURI = getURIforBackup(service, segment);
        sendLoadedService(processName, myEvent, finishStates.FINISHED_KO, error);
        // Service fail
        const responseAux = yield call(fetchProductsBackup, backupURI);
        // If type is all, simply copy JSON to response var
        // otherwise fetch type into JSON output
        if (data.type !== 'all') {
          Object.keys(responseAux.data).forEach((key) => {
            if (key === data.type) {
              response.data = {};
              response.data[key] = responseAux.data[key];
            }
          })
        } else {
          response = responseAux;
        }
      }

      product2Response = normalizerAllTerminals(response.data);
      product2Response = makeItPlainArray(product2Response);
    } else {
      product2Response = lastProducts[type];
    }
    // getting specific filters
    const lastFilters = yield select(getAllFiltersFromRedux);
    let makeUpNewFilters = null;
    if (!lastFilters || !lastFilters[type]) {
      // those came from setupBasics
      const basicFilters = constantsBasicsFilters[type];
      // those we predict from results
      makeUpNewFilters = getBasicFilter(type, product2Response);
      makeUpNewFilters = {
        ...basicFilters,
        ...makeUpNewFilters,
      }
      /* we have to store them before modified with url params */
      yield put(productsActions.createProductFilterStatus(makeUpNewFilters, type, {
        beggining: true,
        current: false,
      }));
      // those we deduce from url
      const urlFilters = extractFiltersFromUrl(makeUpNewFilters);
      makeUpNewFilters = {
        ...makeUpNewFilters,
        ...urlFilters,
      }
      yield put(productsActions.createProductFilterStatus(makeUpNewFilters, type, {
        beggining: false,
        current: true,
      }));
    } else {
      // this means we have created those filters before, but exists on store
      makeUpNewFilters = lastFilters[type];
      yield put(productsActions.createProductFilterStatus(makeUpNewFilters, type, {
        beggining: false,
        current: true,
      }));
    }
    yield put(productsActions.sucessGetProductsGeneric(product2Response, type, makeUpNewFilters));
  } catch (err) {
    // monitoring
    sendJSError(processName, 'getProductsGeneric', err);
    // MONITORIZAR
    // yield put(productsActions.failureGetMyProducts(true));
  }
}

export const getAllDetailedItems = state => state.products.productsDetailed
export function* getProductByAlias(args) {
  try {
    let myEvent = webServices.GET_ITEM;
    if (IC && IC.createEvent) {
      myEvent = IC.createEvent(webServices.GET_ITEM);
    }
    // monitoring
    const { productInfo } = args;
    const { segment } = productInfo;
    const service = SERVICES.LIST;

    /* Api record */
    const apiRecord = yield select(getApiRecord);
    const lastSeconds = (apiRecord.getItem && apiRecord.getItem[productInfo.alias])
      ? apiRecord.getItem[productInfo.alias] : null
    const nowSeconds = Date.now();
    const need2BeSaveTimeStampFlag = need2BeSaveTimeStamp(nowSeconds, lastSeconds);
    const allDetailedItems = yield select(getAllDetailedItems);
    /* Fetching resources */
    let response = {};
    let productsNormalized = null;
    if (need2BeSaveTimeStampFlag || !allDetailedItems[productInfo.alias]) {
      yield put(commonActions.setApiRecord(`getItem-|-${productInfo.alias}`, nowSeconds));
      sendLoadingService(processName, myEvent);
      try {
        response = yield call(fetchPhoneByAlias, productInfo);
        sendLoadedService(processName, myEvent, finishStates.FINISHED_OK);
      } catch (error) {
        const backupURI = getURIforBackup(service, segment);
        sendLoadedService(processName, myEvent, finishStates.FINISHED_KO, error);
        const responseAux = yield call(fetchPhoneByAliasBackup, backupURI);
        const { data } = responseAux;
        const plainData = makeItPlainArray(data);
        const productDetailedFromBackup = plainData.find(item => item.alias === productInfo.alias);
        response.data = productDetailedFromBackup;
      }
      productsNormalized = normalizerProductDetailed(response.data);
    } else {
      productsNormalized = allDetailedItems[productInfo.alias];
    }
    yield put(productsActions.sucessGetProductByAlias(productsNormalized));
  } catch (err) {
    // monitoring
    sendJSError(processName, 'getProductByAlias', err);
    yield put(productsActions.failureGetProductByAlias(true))
  }
}

export function* getTerminalListBanner() {
  try {
    const responseAux = yield call(fetchTerminalListBanner);
    yield put(productsActions.sucessTerminalListBanner(responseAux));
  } catch (err) {
    const responseFailed = terminalListBannerLocalJson;
    yield put(productsActions.failureTerminalListBanner(responseFailed));
  }
}

export function* getLegalConditions() {
  const responseJson = legalConditionsLocalJson;
  yield put(productsActions.sucessLegalConditions(responseJson));
}
