import React from 'react';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import Label from '../../components/Label/Label';
import './SpecsList.scss';

const SpecsList = ({ filters, wrapperLabel }) => {
  const { camera, screen, battery } = filters;

  const cameraComposed = camera ? `Cmara ${camera} MPX` : null;
  const screenComposed = screen ? `Pantalla ${screen}"` : null;
  const batteryComposed = battery ? `Batera ${battery} mAh` : null;

  if (!cameraComposed && !screenComposed && !batteryComposed) {
    return null;
  }

  return (
    <div className="specsList_wrapper">
      <RowGrid>
        <ColGrid mobileCols={12}>
          {cameraComposed && (
            <Label text={cameraComposed} wrapperLabel={wrapperLabel} />
          )}
        </ColGrid>
        <ColGrid mobileCols={12}>
          {screenComposed && (
            <Label text={screenComposed} wrapperLabel={wrapperLabel} />
          )}
        </ColGrid>
        <ColGrid mobileCols={12}>
          {batteryComposed && (
            <Label text={batteryComposed} wrapperLabel={wrapperLabel} />
          )}
        </ColGrid>
      </RowGrid>
    </div>
  )
}

export default SpecsList;
