export const detectIfLayoutHasPresent = (layout) => {
  return layout.indexOf('promo_') > -1;
}
export const normalizeSingleCommercialInfo = (response, primaryId) => {
  let normalizedItem = {};
  if (response) {
    const {
      title,
      mainDescription,
      subTitle,
      secondaryDescription,
      mediaURL,
    } = response;
    let { layout } = response;
    const present = detectIfLayoutHasPresent(layout);
    const normalizedMainDescription = normalizeMainDescription(mainDescription);
    const normalizedSecondaryDescription = normalizeSecondaryDescription(secondaryDescription);

    if (!layout) {
      layout = 'compacto_original_estatico_izquierda';
    }
    normalizedItem = {
      title,
      domId: primaryId,
      subTitle,
      mediaURL,
      layout,
      present,
      mainDescription: normalizedMainDescription,
      secondaryDescription: normalizedSecondaryDescription,
    }
  }
  return normalizedItem;
}


export const normalizeAllCommercialInfo = (response) => {
  let responseNormalized = [];
  let normalizedItemCommercialInfo = {};

  if (response) {
    response.forEach((item, index) => {
      normalizedItemCommercialInfo = normalizeSingleCommercialInfo(item, `cmm_${index}`);
      responseNormalized = [
        ...responseNormalized,
        normalizedItemCommercialInfo,
      ]
    })
  }
  return responseNormalized;
}

export const getFriendlyDate = (date) => {
  let friendlyDate = '';
  if (date) {
    const day = date.getDay() + 1;
    const month = date.getMonth() + 1;
    const year = date.getYear() + 1900;
    friendlyDate = `${day.toString()}/${month.toString()}/${year.toString()}`;
  }
  return friendlyDate;
}
export const normalizeSingleDetail = (response) => {
  let normalizedItem = {};
  const {
    userName,
    epochTs,
    rating,
    comment,
  } = response;
  const date = new Date(epochTs * 1000);
  const friendlyDate = getFriendlyDate(date);
  normalizedItem = {
    name: userName,
    date: friendlyDate,
    rating,
    comment,
  }
  return normalizedItem;
}

export const normalizeAllDetail = (response) => {
  let responseNormalized = [];
  let singleDetailNormalized = {};
  if (response) {
    response.forEach((item) => {
      singleDetailNormalized = normalizeSingleDetail(item);
      responseNormalized = [
        ...responseNormalized,
        singleDetailNormalized,
      ]
    })
  }
  return responseNormalized;
}

export const normalizeCommentsFromTrustivityStructure = (trustivityOfferInfo) => {
  let commentsNormalized = [];
  if (trustivityOfferInfo) {
    trustivityOfferInfo.forEach((item) => {
      const { details } = item;
      const detailsNormalized = normalizeAllDetail(details);
      commentsNormalized = [
        ...commentsNormalized,
        ...detailsNormalized,
      ]
    })
  }
  return commentsNormalized;
}

export const normalizeSingleTerminal = (response) => {
  let normalizedItem = {};
  let offersNormalized = [];
  let technicalFileNormalized = [];
  let commentsNormalized = [];
  let ecoRatingNormalized = {};
  let phoneCaracsNormalized = {};
  let commercialInfoNormalized = [];
  let { fileLayout } = response;
  const {
    metadata,
    alias,
    name,
    technicalFile,
    trustivityProductInfo,
    ecoRating,
    phoneCaracs,
    views,
    is900,
    commercialInfo,
    offers,
  } = response;

  if (!fileLayout) {
    fileLayout = 'normal';
  }
  let numCommentsPlain = '';
  let rating = '';
  let comments = [];
  if (trustivityProductInfo) {
    const { averageRating, numComments, trustivityOfferInfo } = trustivityProductInfo;
    rating = averageRating;
    numCommentsPlain = numComments;
    if (trustivityOfferInfo) {
      comments = normalizeCommentsFromTrustivityStructure(trustivityOfferInfo);
    }
  }

  offersNormalized = normalizeAllOffers(offers);
  technicalFileNormalized = normalizeAllTechnicalFile(technicalFile);
  commentsNormalized = normalizeAllComments(comments);
  ecoRatingNormalized = normalizeEcoRating(ecoRating);
  phoneCaracsNormalized = normalizeAllPhoneCaracs(phoneCaracs);
  commercialInfoNormalized = normalizeAllCommercialInfo(commercialInfo);
  normalizedItem = {
    metadata,
    alias,
    name,
    fileLayout,
    technicalFile: technicalFileNormalized,
    numComments: numCommentsPlain,
    rating,
    comments: commentsNormalized,
    ecoRating: ecoRatingNormalized,
    phoneCaracs: phoneCaracsNormalized,
    views,
    is_900: is900,
    commercialInfo: commercialInfoNormalized,
    offers: offersNormalized,
  }
  return normalizedItem;
}

export const normalizerProductDetailed = (response) => {
  let normalizedItem = {};
  let globalResponseNormalized = {};
  if (response && response.alias) {
    normalizedItem = normalizeSingleTerminal(response);
    globalResponseNormalized = {
      ...globalResponseNormalized,
      ...normalizedItem,
    }
  }
  return globalResponseNormalized;
}
