import { combineReducers } from 'redux';
import { connectRouter } from 'connected-react-router';
import { ModalReducerUI } from '@bit/gerenciatd.react.modal';
import { FlyOutReducerUI } from '@bit/gerenciatd.react.fly-out';
import ProductsReducer from './products';
import CrossSellingReducer from './crossSelling';
import BackupReducer from './backupGenerator';
import CustomizerReducer from './customizer';
import commonsReducer from './commons';
import MyRouter from './myRouter';


const rootReducers = history => combineReducers({
  products: ProductsReducer,
  crossSelling: CrossSellingReducer,
  flyOut: FlyOutReducerUI,
  modal: ModalReducerUI,
  backup: BackupReducer,
  customizer: CustomizerReducer,
  myRouter: MyRouter,
  commons: commonsReducer,
  router: connectRouter(history),
});

export default rootReducers;
