import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import gtmParts from 'react-google-tag-manager';
import { addEvent2GTM, sendPriorityTagging } from '../../../../../utils/taggingUtils';

class GoogleTagManager extends React.Component {
  constructor(props) {
    super(...props);
    this.state = {
      productSegment: null,
      productCategory: null,
      currentAlias: null,
      mounted: false,
    }
  }

  componentDidMount() {
    const dataLayerName = this.props.dataLayerName || 'dataLayer';
    const scriptId = this.props.scriptId || 'react-google-tag-manager-gtm';

    if (!window[dataLayerName] && document) {
      const script = document.createElement('script')
      const gtmScriptNode = document.getElementById(scriptId);
      let scriptText;
      if (gtmScriptNode) {
        scriptText = document.createTextNode(gtmScriptNode.textContent)
        script.appendChild(scriptText)
        document.head.appendChild(script)
      }
    }
    this.setState({ mounted: true })
  }

  static getDerivedStateFromProps(props, state) {
    const { productCategory, productSegment, currentAlias } = props;
    if (state.mounted) {
      let string = window.location.pathname;
      const lastChar = string.substring(string.length - 1);
      if (lastChar === '/') {
        string = string.substring(0, string.length - 1);
      }
      const APpage = string;
      let APpageName = APpage.replace('/', '');
      APpageName = APpageName.replace(/\//g, ':');
      APpageName = APpageName.replace(/_/g, '-');
      if (window.s) {
        window.s.pagename = APpageName;
      }
      const event = {
        event: 'pageviewVirtual',
        APpage,
        APpageName,
      }
      if (dataLayerGTM && window.s && window.s.products) {
        addEvent2GTM(event);
      }
      return {
        ...state,
        productCategory,
        productSegment,
        currentAlias,
      }
    }
    return { ...state }
  }

  render() {
    const gtm = gtmParts({
      id: this.props.gtmId,
      dataLayerName: this.props.dataLayerName || 'dataLayer',
      additionalEvents: this.props.additionalEvents || {},
      previewVariables: this.props.previewVariables || false,
    });
    return (
      <Fragment>
        {gtm.noScriptAsReact()}
        <div id={this.props.scriptId || 'react-google-tag-manager-gtm'}>
          {gtm.scriptAsReact()}
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  currentLocation: state.router.location.pathname,
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
  currentAlias: state.products.currentAlias,
});

export default connect(mapStateToProps, null)(GoogleTagManager);
