import React from 'react';
import { caracsModule } from '../../../constants/terminalFileTexts';
import './TerminalFileCaracs.css';


const TerminalFileCaracs = ({ terminal }) => {
  const { elements, h3 } = caracsModule;
  const { phoneCaracs, name } = terminal;
  const h3Composed = `${h3} del ${name}`;
  return (
    <div id="fileCaracs" className="container terminalFileCaracs_wrapper">
      <div className="terminalFileCaracs_title-wrapper">
        <h3>{h3Composed}</h3>
      </div>
      <div className="terminalFileCaracs_caracs-list-wrapper">
        <div className="terminalFileCaracs-flex-list-wrapper">
          { elements.map((value, index) => {
            const key = `terminal_caracs_item_${index}`;
            if (phoneCaracs && phoneCaracs[value.key]) {
              const elementText = phoneCaracs[value.key];
              return (
                <div
                  key={key}
                  className="terminalFileCaracs-flex-element-wrapper">
                  <div className="terminalFileCaracs_element-wrapper">
                    <div className={`icon ${value.icon}`} />
                    <div className="terminalFileCaracs_element-title">{value.text}</div>
                    <div className="terminalFileCaracs_element-text">{elementText}</div>
                  </div>
                </div>
              )
            }
          })}
        </div>
      </div>
    </div>
  )
}
export default TerminalFileCaracs;
