import { applyFilterMaster } from './filtersDataHandler';
import { setRestOfStars } from '../commonUtils';
/* FOR CATEGORIES */
export const setRestOfPillsToDefault = (id, pillCategory) => {
  const res = [];
  pillCategory.forEach((pillItem) => {
    const newPillItem = { ...pillItem };
    if (newPillItem.id === id) {
      newPillItem.active = !newPillItem.active;
    }
    res.push(newPillItem);
  });
  return res;
}

export const handleClickOnCategoryPill = (params, props) => {
  const { id } = params;
  const { filtersStatus, productCategory } = props;
  const currentFilters = filtersStatus[productCategory];
  const { pillCategory } = currentFilters;
  const newMenu = setRestOfPillsToDefault(id, pillCategory);

  applyFilterMaster(
    'pillCategory',
    newMenu,
    props.products,
    productCategory,
    filtersStatus,
    props.updateProductsFiltered,
  );
}

/* STAR CLICK EVENT */
export const ratingFilter = (starSelected, props) => {
  const {
    products,
    productCategory,
    filtersStatus,
    updateProductsFiltered,
  } = props;
  const { rating } = filtersStatus[productCategory];
  const newStarsRating = setRestOfStars(rating, starSelected);
  applyFilterMaster(
    'rating',
    newStarsRating,
    products,
    productCategory,
    filtersStatus,
    updateProductsFiltered,
  )
}
