import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Slider from 'react-slick';
import './ImgSlider.css';

/**
 * Stateless component to draw an ImgSlider.
 * @param {array} arrayImgsObj Array of src and alt objects
 * @param {object} customSettings Settings from react-slider documentation to customize the slider.
 * @returns {JSX} Component JSX.
 */

class ImgSlider extends Component {
  constructor(...props) {
    super(...props);
    const { autoplay } = this.props;
    this.state = {
      autoplay,
    };
  }

  static getDerivedStateFromProps(props, state) {
    const { autoplay } = state;
    if (props.autoplay !== autoplay) {
      return {
        ...state,
        autoplay,
      }
    }
    return { ...state }
  }

  componentDidMount = () => {
    this.pause();
  }

  componentDidUpdate = () => {
    if (this.props.autoplay) {
      this.play();
    } else {
      this.slickGoTo(0);
      this.pause();
    }
  }

  play = () => {
    this.slider.slickPlay();
  }

  pause = () => {
    this.slider.slickPause();
  }

  slickGoTo = (slide) => {
    this.slider.slickGoTo(slide);
  }

  render() {
    const { arrayImgs, customSettings, wrapperClass } = this.props;
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: true,
      pauseOnHover: false,
      ...customSettings,
    };

    return (
      <Slider ref={(slider) => { this.slider = slider }} className="imgSlider" {...settings}>
        {
          arrayImgs.map((img, index) => (
            <div key={index.toString()} className={`imgSlide ${wrapperClass}`}>
              <img src={img.src} alt={img.alt} />
            </div>
          ))
        }
      </Slider>
    );
  }
}

ImgSlider.propTypes = {
  arrayImgs: PropTypes.array.isRequired,
  customSettings: PropTypes.object,
  wrapperClass: PropTypes.string,
  autoplay: PropTypes.bool,
};

ImgSlider.defaultProps = {
  customSettings: {},
  wrapperClass: '',
  autoplay: false,
}

export default ImgSlider;