// api record compare          new   redux
export const need2BeSaveTimeStamp = (timeA, timeB) => {
  if (!timeB && timeA) {
    return true;
  }
  if (timeB && timeA && ((timeA - timeB) >= 120000)) {
    return true
  }
  return false;
}

export const setRestOfStars = (rating, max) => {
  /* Check if the client has clicked on the same star */
  let newStars = rating;
  const selectedStars = rating.filter(item => item.active === true);
  const numberSelectedBefore = selectedStars.length;
  if (numberSelectedBefore === max) {
    // clear all stars
    newStars = basicRating;
  } else {
    // regular behaviour
    newStars = rating.map(star => ({
      ...star,
      active: false,
    }));
    newStars = newStars.map((star, index) => ({
      ...star,
      active: ((index + 1) <= max),
    }));
  }

  return newStars;
}
/* compare hex number */
export const cmpHexValues = (hex1, hex2) => {
  let res = 0;
  if (parseInt(`0x${hex1}`, 16) > parseInt(`0x${hex2}`, 16)) {
    res = 1;
  } else {
    res = -1;
  }
  return res;
}

export const deleteAccentsAndChars = (inputText) => {
  return inputText.normalize('NFD')
    .replace(/([^n\u0300-\u036f]|n(?!\u0303(?![\u0300-\u036f])))[\u0300-\u036f]+/gi,"$1")
    .normalize();
}
/* CAPITALIZE TEXT */
export const capitalizeString = (str) => {
  let res = '';
  const tokkens = str.split(' ');
  if (tokkens && tokkens.length > 1) {
    tokkens.forEach((tokken, index) => {
      const newTokken = `${tokken.charAt(0).toUpperCase()}${tokken.slice(1).toLocaleLowerCase()}`;
      res = `${index !== 0 ? `${res} ` : ''}${newTokken}`;
    })
  } else {
    res = `${str.charAt(0).toUpperCase()}${str.slice(1).toLocaleLowerCase()}`;
  }
  return res;
}
