export const createDeviceUrl = (params) => {

  //Seguir tocando aqui:
  
  let urlRes = '';
  const myParams = params;
  let alias = myParams && myParams.alias ? myParams.alias : '';
  const productCategory = myParams && myParams.productCategory ? getUrlByProductCategory(myParams.productCategory, myParams.productSegment) : '';
  alias = alias.replace('ntt_', '');
  urlRes = `${productCategory}/${alias}`;
  delete myParams.alias;
  delete myParams.productCategory;
  delete myParams.productSegment;
  // xiaomi_redmi_7?storage=128&colorHex=FFFF00
  const paramsKeys = Object.keys(myParams);
  paramsKeys.forEach((paramKey, index) => {
    if (index === 0) {
      urlRes = `${urlRes}?`;
    } else {
      urlRes = `${urlRes}&`;
    }
    urlRes = `${urlRes}${paramKey}=${params[paramKey]}`
  })

  return urlRes;
}

export const setCookie = (cName, value, exdays) => {
  const exdate = new Date();
  exdate.setDate(exdate.getDate() + exdays);
  const cValue = escape(value) + ((exdays === null) ? '' : `;expires=${exdate.toUTCString()};path=/`);
  document.cookie = `${cName}=${cValue}`;
}


export const getCookie = (cName) => {
  let cValue = document.cookie;
  let cStart = cValue.indexOf(` ${cName}=`);
  if (cStart === -1) {
    cStart = cValue.indexOf(`${cName}=`);
  }
  if (cStart === -1) {
    cValue = null;
  } else {
    cStart = cValue.indexOf('=', cStart) + 1;
    let cEnd = cValue.indexOf(';', cStart);
    if (cEnd === -1) {
      cEnd = cValue.length;
    }
    cValue = unescape(cValue.substring(cStart, cEnd));
  }
  return cValue;
}

export const deleteCookie = (name) => {
  document.cookie = `${name}=; expires=Thu, 01 Jan 1970 00:00:01 GMT;path=/`;
}

