export const getParams = (search) => {
  let searchClean = '';
  let searchSplitted = [];
  const searchSplittedClean = [];
  if (search) {
    searchClean = search.replace('?', '');
    if (searchClean) {
      searchSplitted = searchClean.split('&');
      searchSplitted.forEach((param) => {
        const paramSplitted = param.split('=');
        if (paramSplitted.length > 1) {
          const paramKey = paramSplitted[0];
          let paramValue = null;
          if (parseInt(paramSplitted[1], 10) >= 0) {
            paramValue = parseInt(paramSplitted[1], 10);
          } else {
            paramValue = paramSplitted[1];
          }
          const paramClean = {
            key: paramKey,
            value: paramValue,
          }
          searchSplittedClean.push(paramClean);
        }
      })
    }
  }
  return searchSplittedClean;
}
export const getAllProductType = (type) => {
  let composedType = '';
  if (PRODUCT_CATEGORY) {
    Object.keys(PRODUCT_CATEGORY).forEach((key) => {
      if (PRODUCT_CATEGORY[key] !== PRODUCT_CATEGORY.ALL) {
        if (composedType) {
          composedType = `${composedType}&`;
        }
        composedType = `${composedType}${type}=${PRODUCT_CATEGORY[key]}`;
      }
    })
  }
  return composedType;
}
export const prepareGetParamsForSaga = (data) => {
  let queryParams = '';
  let { segment } = data;
  let type = '';
  if (data.segment !== SEGMENTS.EMPLEADOS) {
    Object.keys(data).forEach((key) => {
      switch (key) {
        case 'segment': {
          if (data[key]) {
            segment = `${key}=${data[key]}`;
          }
          return true;
        }
        case 'type': {
          if (data[key]) {
            if (data[key] === 'all') {
              type = getAllProductType(key);
            } else {
              type = `${key}=${data[key]}`;
            }
          }
          return true;
        }
        default: {
          console.log(`Busqueda desconocida-${key}:${data[key]}`);
          return true;
        }
      }
    })
  } else {
    type = getAllProductType('type');
    segment = `segment=${segment}`;
  }

  if (segment) {
    queryParams = `?${queryParams}${segment}`;
    if (type) {
      queryParams = `${queryParams}&${type}`;
    }
  }

  /* ?segment=empleados&type=movil&type=tablet&type=smartwatch&type=equipamiento&type=seminuevo&type=tv&type=pack */
  return queryParams;
}

export const getHostFromUrl = () => {
  return window.location.hostname;
}

export const getSegment = (currentUrl) => {
  const url2cmp = currentUrl.toLowerCase();
  if (url2cmp) {
    if (url2cmp.indexOf(SEGMENTS.PARTICULARES) > -1) {
      sessionStorage.setItem(SESSIONSTORAGEKEY, SEGMENTS.PARTICULARES);
      return SEGMENTS.PARTICULARES;
    }
    if (url2cmp.indexOf(SEGMENTS.EMPRESAS) > -1) {
      sessionStorage.setItem(SESSIONSTORAGEKEY, SEGMENTS.EMPRESAS);
      return SEGMENTS.EMPRESAS;
    }
    if (url2cmp.indexOf(SEGMENTS.EMPLEADOS) > -1) {
      sessionStorage.setItem(SESSIONSTORAGEKEY, SEGMENTS.EMPLEADOS);
      return SEGMENTS.EMPLEADOS;
    }
    if (url2cmp.indexOf(SEGMENTS.APP) > -1) {
      sessionStorage.setItem(SESSIONSTORAGEKEY, SEGMENTS.APP);
      return SEGMENTS.APP;
    }
    if (url2cmp.indexOf(SEGMENTS.AUTONOMOS) > -1) {
      sessionStorage.setItem(SESSIONSTORAGEKEY, SEGMENTS.AUTONOMOS);
      return SEGMENTS.AUTONOMOS;
    }
  }
  sessionStorage.setItem(SESSIONSTORAGEKEY, SEGMENTS.PARTICULARES);
  return SEGMENTS.PARTICULARES;
}

export const getProductCategory = (currentUrl) => {
  let auxCurrent = `${currentUrl}`;
  if (auxCurrent) {
    if (auxCurrent.charAt(0) !== '/') {
      auxCurrent = `/${auxCurrent}`;
    }
    if (auxCurrent.indexOf(URLS_LIBRARY.URL_SMARTPHONES) > -1) {
      return PRODUCT_CATEGORY.SMARTPHONES;
    }
    if (auxCurrent.indexOf(URLS_LIBRARY.URL_SMARTWATCHES) > -1) {
      return PRODUCT_CATEGORY.SMARTWATCHES;
    }
    if (auxCurrent.indexOf(URLS_LIBRARY.URL_TVS) > -1) {
      return PRODUCT_CATEGORY.TVS;
    }
    if (auxCurrent.indexOf(URLS_LIBRARY.URL_EQUIPMENT) > -1) {
      return PRODUCT_CATEGORY.EQUIPMENT;
    }
    if (auxCurrent.indexOf(URLS_LIBRARY.URL_BUNDLES) > -1) {
      return PRODUCT_CATEGORY.BUNDLES;
    }
    if (auxCurrent.indexOf(URLS_LIBRARY.URL_TABLETS) > -1) {
      return PRODUCT_CATEGORY.TABLETS;
    }
    return PRODUCT_CATEGORY.ALL;
  }
  return '';
}

export const getIndexOfProductCategory = (data, productCategory) => {
  let index = '-1';
  if (data && productCategory) {
    data.forEach((item)=> {
      if (item.value === productCategory) {
        index = item.id;
      }
    })
  }
  return index;
}

export const getCommercialNameOfProductCategory = (productCategory, productSegment) => {
  let commercialNameOfProductCategory = 'Dispositivos';
  if (productCategory && productSegment !== SEGMENTS.EMPLEADOS) {
    pillBarMenu.forEach((item) => {
      if (item.value === productCategory && productCategory !== PRODUCT_CATEGORY.ALL) {
        commercialNameOfProductCategory = item.label;
      }
    })
  }
  return commercialNameOfProductCategory;
}

export const analyzeResponse = (response) => {
  if (response) {
    const { data } = response;
    let allEmpty = true;
    Object.keys(data).forEach((item) => {
      if (item && data[item]) {
        if (data[item].length > 0) {
          allEmpty = false;
        }
      }
    })
    if (allEmpty) {
      forcingExceptionHandling();
    }
  }
}

export const normalizeColorForBackgroundColor = (color) => {
  const normalizedColor = color.toString().toUpperCase();
  return normalizedColor;
}

export const getColorForBackgroundBanner = (color) => {
  let backgroundColor = '';
  if (color) {
    const normalizedColor = normalizeColorForBackgroundColor(color);
    Object.keys(BANNER_BACKGROUND_COLOR).forEach((singleBannerBackgroundColor) => {
      if (singleBannerBackgroundColor === normalizedColor) {
        backgroundColor = BANNER_BACKGROUND_COLOR[singleBannerBackgroundColor];
      }
    })
  }
  return backgroundColor;
}

export const getURIforBackup = (service, segment) => {
  let backupURI = '';
  if (service && segment) {
    switch (service) {
      case SERVICES.FILE: {
        if (segment === SEGMENTS.PARTICULARES) {
          backupURI = API.PRODUCTS.PHONE_DETAILS_BACKUP_PARTICULARES;
        }
        if (segment === SEGMENTS.EMPLEADOS) {
          backupURI = API.PRODUCTS.PHONE_DETAILS_BACKUP_EMPLEADOS;
        }
        if (segment === SEGMENTS.APP) {
          backupURI = API.PRODUCTS.PHONE_DETAILS_BACKUP_APP;
        }
        break;
      }
      case SERVICES.LIST: {
        if (segment === SEGMENTS.PARTICULARES) {
          backupURI = API.PRODUCTS.PHONES_BACKUP_PARTICULARES;
        }
        if (segment === SEGMENTS.EMPLEADOS) {
          backupURI = API.PRODUCTS.PHONES_BACKUP_EMPLEADOS;
        }
        if (segment === SEGMENTS.APP) {
          backupURI = API.PRODUCTS.PHONES_BACKUP_APP;
        }
        break;
      }
      default: {
        break;
      }
    }
  }
  return backupURI;
}
