import axios from 'axios';
import { call, put } from 'redux-saga/effects';
import { API } from '../constants/api'
import { normalizeSingleTerminal } from './normalizers/productNormalizer';
import { actions as csActions } from '../reducers/crossSelling';

export const fetchCSbyAlias = (data) => {
  return axios({
    method: 'get',
    url: `${API.PRODUCTS.PHONE_CROSS_SELLING}?type=${data.type}&segment=${data.segment}&alias=${data.alias}&`,
  })
}

export function* getCrossSelling(args) {
  try {
    const { data } = args.payload;
    const { productInfo } = data;
    const response = yield call(fetchCSbyAlias, productInfo);
    const crossNormalized = [];
    const cross = (response && response.data) ? response.data : [];
    cross.forEach((item, index) => {
      const normalized = item ? normalizeSingleTerminal(item, index, productInfo.type) : null;
      if (normalized) {
        crossNormalized.push(normalized);
      }
    })
    yield put(csActions.getCSSuccess(crossNormalized, productInfo.alias));
  } catch (err) {
    yield put(csActions.getCSError())
  }
}
