import { lowStorageFirst } from '../../utils/dataHandlers/filtersDataHandler';
import { capitalizeString } from '../../utils/commonUtils';
import { pillColor } from '../../constants/setupBasics';
import { PRODUCT_CATEGORY } from '../../constants/constants';

export const getAndNormalizePillProp = (
  referencedfilter,
  recognizedBefore,
  idTag,
  dataType = 'string',
) => {
  const newRecognized = [...recognizedBefore];
  referencedfilter.forEach((item) => {
    let itemLabel = null;
    let itemName = null;
    if (dataType === 'string') {
      itemLabel = capitalizeString(item);
      itemName = itemLabel.toLowerCase();
    } else if (dataType === 'number') {
      const maybeNewStorage = parseInt(item, 10);
      itemLabel = maybeNewStorage;
      itemName = maybeNewStorage;
    }
    const exist = recognizedBefore.find((itemExistsBefore) => {
      return `${itemExistsBefore.name}` === `${itemName}`
    });
    const hash = item.indexOf('#') > -1;
    const isNull = !itemName || itemName === '';

    if (!exist && !hash && !isNull) {
      // theres new category
      const newCategory = {
        id: `${idTag}-${itemName}`,
        name: `${itemName}`,
        label: `${itemLabel}`,
        active: false,
      }
      newRecognized.push(newCategory);
    }
  });
  // Sorting results
  return newRecognized;
}


export const createFiltersForDevice = (
  devices,
  properties2get = {
    category: true,
    brand: true,
    storage: true,
    color: true,
    os: true,
    connectivity: true,
  },
) => {
  let brandRes = [];
  let storageRes = [];
  let colorRes = [...pillColor];
  let soListRes = [];
  let categoryRes = [];
  let connectivityRes = [];

  devices.forEach((device) => {
    const { filters } = device;
    // Checking category
    if (properties2get.category && filters && filters.category) {
      categoryRes = getAndNormalizePillProp(
        filters.category,
        categoryRes,
        'PA-listado-terminales-filtros-categoria',
        'string',
      );
    }
    // Checking brand
    if (properties2get.brand && filters && filters.brand) {
      brandRes = getAndNormalizePillProp(
        [filters.brand],
        brandRes,
        'PA-listado-terminales-filtros-marca',
        'string',
      );
    }
    // checking storage
    if (properties2get.storage && filters && filters.storage) {
      storageRes = getAndNormalizePillProp(
        filters.storage,
        storageRes,
        'PA-listado-terminales-filtros-capacidad',
        'number',
      );
    }

    if (properties2get.color && filters && filters.colorHex) {
      colorRes = getAndNormalizePillProp(
        filters.colorHex,
        colorRes,
        'PA-listado-terminales-filtros-color',
        'string',
      );
    }
    // Checking operating sistem
    if (properties2get.os && filters && filters.os) {
      soListRes = getAndNormalizePillProp(
        [filters.os],
        soListRes,
        'PA-listado-terminales-filtros-sistemaoperativo',
        'string',
      );
    }
    // checking conectivity
    if (properties2get.connectivity && filters && filters.connectivity) {
      connectivityRes = getAndNormalizePillProp(
        filters.connectivity,
        connectivityRes,
        'PA-listado-terminales-filtros-conectividad',
        'string',
      );
    }
  })

  // creating response depends on 'properties2get'
  const res = {};
  const keysProp2Get = Object.keys(properties2get);
  keysProp2Get.forEach((key) => {
    const allowProp = properties2get[key];
    if (allowProp) {
      switch (key) {
        case 'category': res.pillCategory = categoryRes; break;
        case 'brand': res.brand = brandRes; break;
        case 'os': res.soList = soListRes; break;
        case 'storage': res.storage = storageRes.sort(lowStorageFirst);
          break;
        case 'color': res.color = colorRes; break;
        case 'connectivity': res.connectivity = connectivityRes; break;
        default: break;
      }
    }
  })

  return res;
}

/* Main method to get basic filters */
export const getBasicFilter = (type, productsNormalized) => {
  // depens on categoryd
  let allowProp = {}
  switch (type) {
    case PRODUCT_CATEGORY.TABLETS:
    case PRODUCT_CATEGORY.SMARTPHONES:
      allowProp = {
        category: true,
        brand: true,
        storage: true,
        color: true,
        os: false,
        connectivity: true,
      }
      break;
    case PRODUCT_CATEGORY.TVS:
      allowProp = {
        category: true,
        brand: true,
        storage: false,
        color: false,
        os: false,
        connectivity: false,
      }
      break;
    case PRODUCT_CATEGORY.SMARTWATCHES:
      allowProp = {
        category: true,
        brand: true,
        storage: true,
        color: true,
        os: true,
        connectivity: true,
      }
      break;
    case PRODUCT_CATEGORY.EQUIPMENT:
      allowProp = {
        category: false,
        brand: false,
        storage: false,
        color: false,
        os: false,
        connectivity: false,
      }
      break;
    case PRODUCT_CATEGORY.BUNDLES:
      allowProp = {
        category: true,
        brand: true,
        storage: false,
        color: false,
        os: true,
        connectivity: false,
      }
      break;
    default:
      allowProp = {
        category: true,
        brand: false,
        storage: false,
        color: false,
        os: false,
        connectivity: false,
      }
      break;
  }

  const auxFilter = createFiltersForDevice(productsNormalized, allowProp);
  return auxFilter;
}
