import React from 'react';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'connected-react-router';
import Core from './Core/Core';
import store, { history } from './store';
import '@bit/gerenciatd.react.ico-fonts';
import '@bit/gerenciatd.react.fonts';
import '../commonStyles/customProperties.scss';
import '@bit/gerenciatd.react.uxux';
import '../commonStyles/slick.css';
import '../commonStyles/slick-theme.css';
import './App.scss';

window.s = window.s || {};

/**
 * App component.
 * @returns {object} App rendered.
 */
const App = () => {
  return (
    <Provider store={store}>
      <ConnectedRouter history={history}>
        <Core history={history} />
      </ConnectedRouter>
    </Provider>
  )
};

export default App;
