import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Review from '../../components/Review/Review';
import { REVIEWS_TO_SHOW } from '../../constants/constants';
import './ReviewList.css'

class ReviewList extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  render() {
    const { reviewsList, elementsToShow } = this.props;
    const reviewsToShow = reviewsList.slice(0, elementsToShow);
    return (
      <div className="reviewList_wrapper">
        { reviewsToShow.map((review, index) => {
          const reviewListKey = `review_${index}`;
          return (
            <Review key={reviewListKey} review={review} />
          )
        })}
      </div>
    )
  }
}

ReviewList.propTypes = {
  reviewsList: PropTypes.array,
}
ReviewList.defaultProps = {
  reviewsList: [],
}

export default ReviewList;
