import {
  all,
  takeLatest,
  takeEvery,
} from 'redux-saga/effects';

import * as productSagas from './products';
import * as csSagas from './crossSelling';
import * as backupSagas from './backupGenerator';
import * as customizerSagas from './customizer';
import { types as productTypes } from '../reducers/products';
import { types as csTypes } from '../reducers/crossSelling';
import { types as backupTypes } from '../reducers/backupGenerator';
import { types as customizerTypes } from '../reducers/customizer';

export default function* rootSaga() {
  yield all([
    // backups
    takeLatest(
      customizerTypes.GET_BANNER_HOME,
      customizerSagas.getBannerHome,
    ),
    // backups
    takeLatest(
      backupTypes.GET_BACKUP,
      backupSagas.getBackup,
    ),
    // Get All Terminals
    takeLatest(
      productTypes.GET_PRODUCTS_GENERIC,
      productSagas.getProductsGeneric,
    ),
    // Get terminal list banner data
    takeLatest(
      productTypes.GET_TERMINAL_LIST_BANNER,
      productSagas.getTerminalListBanner,
    ),
    // Get terminal list banner data
    takeLatest(
      productTypes.GET_LEGAL_CONDITION,
      productSagas.getLegalConditions,
    ),
    // Get Terminal by alias
    takeEvery(
      productTypes.GET_PRODUCT_BY_ALIAS,
      productSagas.getProductByAlias,
    ),
    // Get CrossSelling
    takeEvery(
      csTypes.GET_CROSS_SELLING,
      csSagas.getCrossSelling,
    ),
  ])
}
