import React, { Component } from 'react';
import { FloatInfo, HomeA } from './bannerLayout';
import { bannerBuildBGImg, giveRandomNumber } from './bannerList';

class OfferBanner extends Component {
  constructor(...args) {
    super(...args);
    this.state = {
      idBanner: giveRandomNumber(1, 99),
    }
  }

  static getDerivedStateFromProps(props, state) {
    const { idBanner } = state;
    const { buttonProps, textInfo, layout, link, bgImg } = props;
    if (JSON.stringify(state.bgImg) !== JSON.stringify(bgImg)) {
      bannerBuildBGImg(bgImg, idBanner)
    }

    return {
      ...state,
      buttonProps,
      textInfo,
      layout,
      link,
      bgImg,
    }
  }

  render() {
    const {
      idBanner,
      buttonProps,
      textInfo,
      layout,
      link,
    } = this.state;

    switch (layout) {
      case 'FloatInfo': return (
        <FloatInfo
          idBanner={idBanner}
          textInfo={textInfo}
          buttonProps={buttonProps}
          link={link}
        />
      )
      case 'HomeA': return (
        <HomeA
          idBanner={idBanner}
          textInfo={textInfo}
          buttonProps={buttonProps}
          link={link}
        />
      )
      default: return null;
    }
  }
}


export default OfferBanner;