import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { modalActions } from '@bit/gerenciatd.react.modal';
import { getSegment, getProductCategory } from '../../utils/commonUtils';
import MainContent from './MainContent/MainContent';
import { actions as productActions } from '../../reducers/products';
import store from '../store';
import Header from './Header';
import Footer from './Footer';
import './Core.css';
import './Grid.css';
import { SEGMENTS, HOST_ALLOWED_TO_EMPLOYES, DEVELOPMENT_HOST } from '../../constants/constants';
// import Header from '../../../containers/Header/Header';

class Core extends Component {
  constructor(props) {
    super(...props);
    const url = window.location.pathname;
    let { productSegment, productCategory } = props;
    if (!productSegment || !productCategory) {
      productSegment = getSegment(url);
      productCategory = getProductCategory(url);
    }

    let on = true;
    if (SEGMENTS.EMPLEADOS === productSegment) {
      const serverReferrer = document.referrer.split('/')[2];
      const isAllowedToEmployes = HOST_ALLOWED_TO_EMPLOYES.indexOf(serverReferrer) > -1
      || process.env.NODE_ENV === 'development';
      const developmenthost = document.location.host.indexOf(DEVELOPMENT_HOST)>-1;
      if (!isAllowedToEmployes && !developmenthost) {
        on = false;
        alert('Error: Acceso denegado');
        window.location.href = 'https://edomus.tesa';
      }
    }

    this.state = {
      productSegment,
      on,
    }

    props.updateProductSegment(productSegment);
    props.updateProductCategory(productCategory);
  }

  render() {
    const { on } = this.state;
    const { history, setModalVisibility } = this.props;

    if (!on) {
      return null;
    }

    return (
      <div className="xux">
        <Header />
        <MainContent history={history} />
        <div id="pcOrMobile" />
        <Footer store={store} setModalVisibility={setModalVisibility} />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  /* basic info for app */
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
})

const mapDispatchToProps = (dispatch) => {
  return {
    setModalVisibility: id => dispatch(modalActions.setModalVisibility(id)),
    /* basic info for app */
    updateProductCategory: category => dispatch(productActions.updateProductCategory(category)),
    updateProductSegment: segment => dispatch(productActions.updateProductSegment(segment)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Core)
