import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { DEV } from '../../../../constants/constants'
import ErrorBoundary from './ErrorBoundary/ErrorBoundary';
import GoogleTagManager from './GoogleTagManager/GoogleTagManager';
import { pageList } from '../../../pages/pageList';
import { sendJSError } from '../../../../utils/monitoring/monitoring';

export const allSwitchedRoutes = () => {
  const PagesKeys = Object.keys(pageList);
  return (
    <Switch>
      {PagesKeys.map((pageKey) => {
        const currentPage = pageList[pageKey];
        const {
          id,
          hash,
          component,
          exact,
          to,
          onlyDev,
        } = currentPage;
        let allowToShow = true;
        if (onlyDev) {
          const { NODE_ENV } = process.env;
          allowToShow = NODE_ENV === DEV;
        }
        if (component && allowToShow) {
          return <Route exact={exact} key={`route${id}`} path={`${hash}`} component={component} />
        }
        if (!component && to) {
          return <Route key={`redirect${id}`} exact={exact} path={`${hash}`} render={() => (<Redirect to={to} />)} />
        }
        return null;
      })}
    </Switch>
  )
}

const createSProducts = () => {
  let string = window.location.pathname;
  const lastChar = string.substring(string.length - 1);
  if (lastChar === '/') {
    string = string.substring(0, string.length - 1);
  }

  const fragments = string.split('/');
  const lastFragment = fragments[fragments.length - 1];
  const previousFragment = fragments[fragments.length - 2];

  if (lastFragment === 'moviles'
    || lastFragment === 'smartwatches'
    || lastFragment === 'tablets'
    || lastFragment === 'smart-tv'
    || lastFragment === 'pack'
    || lastFragment === 'dispositivos'
  ) {
    if (window.s) {
      if (lastFragment === 'moviles') {
        window.s.products = `terminales-moviles;distributiva-${lastFragment}`;
      } else {
        window.s.products = `${lastFragment};distributiva-${lastFragment}`;
      }
    }
  } else if (window.s) {
    if (previousFragment === 'moviles') {
      window.s.products = `terminales-moviles;${lastFragment}`;
    } else {
      window.s.products = `${previousFragment};${lastFragment}`
    }
  }
}

class RoutesAndBoundary extends Component {
  constructor(props) {
    super(...props);
  }

  componentDidMount = () => {
    const { errorBoundaryReset } = this.props;
    if (errorBoundaryReset) {
      this.props.resetAll();
    }
  }

  render() {
    const {
      history,
      handlingCode,
    } = this.props;

    createSProducts();

    return (
      <ErrorBoundary history={history}>
        <GoogleTagManager error={handlingCode} gtmId="GTM-TQWMMR" dataLayerName="dataLayerGTM" />
        { allSwitchedRoutes() }
      </ErrorBoundary>
    )
    /*
    errorBoundaryReset += 1;
    return <Stopper handlingCode={-69} /> */
  }
}


export default RoutesAndBoundary;
