import React from 'react';
import InputRange from 'react-input-range';
import { sendButtonTagging } from '../../utils/taggingUtils';

const InputSlider = ({
  inputData,
  onChange,
  onChangeArgs,
}) => {
  const {
    idTagging,
    max,
    min,
    limits,
  } = inputData;

  const {
    idSlider,
    products,
    productCategory,
    filtersStatus,
    updateProductsFiltered,
  } = onChangeArgs

  const taggingEvent = (propertyValue) => {
    sendButtonTagging(idTagging, JSON.stringify(propertyValue));
  }

  const handleOnChange = (propertyValue) => {
    if (onChange) {
      onChange(
        idSlider,
        { ...propertyValue, limits },
        products,
        productCategory,
        filtersStatus,
        updateProductsFiltered,
      )
    }
  }

  return (
    <div className="inputSlider">
      <InputRange
        maxValue={limits.max}
        minValue={limits.min}
        formatLabel={label => `${label}`}
        value={{
          max,
          min,
        }}
        // onChange={value => this.priceSliderUpdate(value)}
        onChange={propertyValue => handleOnChange(propertyValue)}
        onChangeComplete={propertyValue => taggingEvent(propertyValue)}
      />
    </div>
  )
}

export default InputSlider;