import React from 'react';
import { Redirect } from 'react-router-dom';
import MainSentence from '../../../../Stopper/MainSentence'
import Stopper from '../../../../Stopper/Stopper';
import { sendJSError } from '../../../../../utils/monitoring/monitoring';
import { processName } from '../../../../../sagas/webServices';

/**
  * Stateful component to draw ErrorBoundary
  * @param {string} error
  * @param {string} errorInfo Detailed error info, renders the page.
  * @returns {JSX} Component JSX.
  */

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errorInfo: null,
      redirect: null,
    };
  }

  componentDidMount = () => {
    const { history } = this.props;
    history.listen((location) => {
      const { errorInfo } = this.state;
      if (errorInfo) {
        this.setState({ redirect: location })
      }
    })
  }

  componentWillUnmount = () => {
    const { history } = this.props;
    if (history) {
      try {
        history.removeEventListener('listen');
      } catch (err) {
        // console.log(`err: ${err}`);
      }
    }
  }

  static getDerivedStateFromError(error) {
    const errorsms = error.toString();
    return {
      errorInfo: errorsms,
    }
  }

  sendError = (errorInfo) => {
    sendJSError(processName, 'errorBoundary', errorInfo.errorMsg);
  }

  sendMonitoringInfo = (errorInfo, status) => {
    // const pageInfo = getActualPageName();
    const errorBody = {
      errorMsg: JSON.stringify(errorInfo),
      // page: actualPageName,
      status,
    };
    /* eslint no-empty: "error" */
    try {
      const { NODE_ENV } = process.env;
      if (NODE_ENV !== 'development') {
        this.sendError(errorBody);
      } else {
        console.warn('An error would be sent to MONITORING');
      }
    } catch (e) {
      // Non important error
    }
  }

  componentDidCatch = (error, errorInfo) => {
    // const { props } = this;
    this.setState({ errorInfo: true });
    this.sendMonitoringInfo(errorInfo.toString(), 'KO');
  }

  render() {
    if (this.state.redirect && this.state.errorInfo) {
      this.setState({ redirect: null, errorInfo: null });
      return <Redirect to={this.state.redirect} />
    }

    if (!this.state.errorInfo) {
      /* Normally, just render children */
      return this.props.children;
    }
    return (
      <Stopper handlingCode={-69}>
        <MainSentence />
      </Stopper>
    )
  }
}

export default ErrorBoundary;
