import React from 'react';
import YouTube from 'react-youtube';
import './CommercialInfoCompleteOriginalDynamic.scss';

const CommercialInfoCompleteOriginalDynamic = ({ commercialInfo }) => {
  const {
    mediaURL,
    domId,
  } = commercialInfo;
  return (
    <div id={domId} className="youtubeContent_wrapper">
      <YouTube
        videoId={mediaURL}
        className="youtubeContent_wrapper"
      />
    </div>
  )
}
export default CommercialInfoCompleteOriginalDynamic;
