import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './ThumbMenu.scss';

class ThumbMenu extends Component {
  constructor(props){
    super(...props);
    this.state = {

    }
  }

  render(){
    const { items } = this.props;
    return (
      <div className="ThumbMenu">
        {items.map(item => {
          const { srcPic, label, link } = item;
          return (
            <div className="ThumbItem">
              <Link to={link}>
                <p align="center"><img className="itemPic" src={srcPic} /></p>
                <p align="center"><span className="itemName">{label}</span></p>
              </Link>
            </div>
          )
        })}
      </div>
    )
  }
}

export default ThumbMenu;