import React from 'react';
import PropTypes from 'prop-types';
import { nomalizeRating } from '../../utils/commonUtils';
import './StarList.css';
import { sendButtonTagging } from '../../utils/taggingUtils';
import Star from '../../components/Star/Star'

/**
* Stateless component to draw an icon and a label.
* @param {string} text path of the image.
* @returns {JSX} Component image JSX.
*/

const StarList = ({
  idTagging,
  numComments,
  rating,
  starVisualization,
  starClickEventFunction,
}) => {
  const commentsVector = nomalizeRating(rating);

  let counterVisible = false;
  if (numComments > 0) {
    counterVisible = true;
  }

  const handleStarClick = (id) => {
    sendButtonTagging(idTagging, id);
    if (starClickEventFunction) {
      starClickEventFunction(id);
    }
  }

  return (
    <div className="starList_wrapper">
      { commentsVector.map((item, index) => {
        const starListKey = `star_${index}`;
        return (
          <Star
            index={(index + 1)}
            key={starListKey}
            starClass={`${item}`}
            starVisualization={starVisualization}
            onClick={handleStarClick}
          />
        )
      })}
      {(counterVisible && numComments) && (
        <div className={`starList_comments-color-${starVisualization}`}>{`(${numComments})`}</div>
      )}
    </div>
  )
}

StarList.propTypes = {
  rating: PropTypes.number,
  numComments: PropTypes.number,
  starVisualization: PropTypes.string,
  starClickEventFunction: PropTypes.func,
}

StarList.defaultProps = {
  rating: 0,
  numComments: 0,
  starClickEventFunction: null,
  starVisualization: 'inverted',
}

export default StarList;
