
import React from 'react';
import PropTypes from 'prop-types';
import './Image.css';

/**
* Stateless component to draw an icon.
* @param {string} url path of the image.
* @param {string} wrapperClass wrapper class for img
* @param {number} opacity opacity between 0 and 1
* @returns {JSX} Component image JSX.
*/

const Image = ({
  id,
  url,
  wrapperClass,
  opacity,
  alt,
  dynamicResize,
}) => {
  let dynamicCssClass = '';

  const currentOnLoad = (img) => {
    const { target } = img;
    const { width, height } = target;
    dynamicCssClass = (height > width) ? 'Img-vertical' : 'Img-horizontal';
    target.classList.add(dynamicCssClass);
  }

  return (
    <img
      id={id}
      style={{ opacity }}
      className={`${wrapperClass}`}
      alt={alt}
      src={url}
      onLoad={dynamicResize ? currentOnLoad : null}
    />
  )
}

Image.propTypes = {
  url: PropTypes.string,
  wrapperClass: PropTypes.string,
  opacity: PropTypes.string,
  alt: PropTypes.string,
  dynamicResize: PropTypes.bool,
}


Image.defaultProps = {
  url: '',
  wrapperClass: '',
  opacity: '1',
  alt: '',
  dynamicResize: false,
}

export default Image;
