export const getCombinedPath = (currentPath, hash) => {
  const hashTokkens = hash.split('/');
  // hashTokkens = { ...hashTokkens};
  const pathTokkens = currentPath.split('/');
  // pathTokkens = { ...pathTokkens};

  // solo tengo que combinar si el hash tiene :
  let merge = '/';
  hashTokkens.forEach((token, index) => {
    if (token.indexOf(':') > -1 && pathTokkens[index]) {
      merge = `${merge}/${pathTokkens[index]}`;
    } else if (token) {
      merge = `${merge}/${token}`;
    }
  })
  return merge;
}

export const getLoaders = () => {
  let bestResult = null;

  const currentPath = window.location.pathname;
  const loadersForThis = {};
  const routesKeys = Object.keys(pageList);
  routesKeys.forEach((routeKey) => {
    const currentRoute = pageList[routeKey];
    let { hash } = currentRoute;
    if (hash) {
      hash = hash.toLowerCase();
      const currentSimilarity = currentPath.indexOf(hash) > -1;
      if (currentSimilarity) {
        bestResult = currentRoute;
      }
    }
  });

  let keysConsole = '';
  let numberKeys = 0;
  if (bestResult) {
    const { loaderKeys } = bestResult;
    if (loaderKeys) {
      loaderKeys.forEach((key) => {
        loadersForThis[key] = modules[key];
        numberKeys += 1;
        keysConsole = `${key},${keysConsole}`;
      })
    }
  }
  if (keysConsole && process.env.NODE_ENV === 'development') {
    consoleDev(`On ${currentPath} we preload: ${keysConsole}`);
  }

  return {
    loadersForThis,
    numberKeys,
  };
}
