import React from 'react';
/* Used Components */
import ReactHtmlParser from 'react-html-parser';
import Button from '@bit/gerenciatd.react.button';
import './FloatInfo.css';

const FloatInfo = ({ ...props }) => {
  const {
    idBanner,
    textInfo,
    buttonProps,
    link,
  } = props;
  /*
    Layout skeleton:
    desktop
    [
      [[empty] [rightPanel]]
    ]
    mobile
    [
      [[rightPanel]]
    ]
    Notes:
    leftPanel: empty
    rightPanel: where there is product info
  */
  return (
    <div
      id={`offer-banner-${idBanner}`}
      className={`offerBanner FloatInfo banner${idBanner}`}
    >
      {(textInfo || buttonProps) && (
        <div className="container">
          <div className="box">
            {textInfo && textInfo.offerClaim && (
              <div className="offerClaim">{ReactHtmlParser(textInfo.offerClaim)}</div>
            )}
            {textInfo && textInfo.title && (<div className="title">{ReactHtmlParser(textInfo.title)}</div>)}
            {textInfo && textInfo.description && (
              <div className="description">
                {ReactHtmlParser(textInfo.description)}
              </div>
            )}
            {textInfo && textInfo.price && (
              <div className="price">
                {textInfo.price}
                /mes
              </div>
            )}
            {buttonProps && <Button id="boxButton" {...buttonProps} />}
          </div>
        </div>
      )}
      { (link && link.url) && (
        <a href={link.url} className="GW_linkOverBox" />
      )}
    </div>
  );
}

export default FloatInfo;