import React from 'react';
import Button from '@bit/gerenciatd.react.button';
/* Those Component Keeping clear this */
import BannerTextInfo from './BannerTextInfo/BannerTextInfo';
import BannerPriceInfo from './BannerPriceInfo/BannerPriceInfo';
/* Used Components */
// import Stamp from '../../../Stamp/Stamp';
import './HomeA.css';

const HomeA = ({ ...props }) => {
  const {
    idBanner,
    textInfo,
    buttonProps,
    link,
  } = props;
  /*
    Layout skeleton:
    [
      [[leftPanel] [rightPanel]]
      [Stamp Component]
    ]
    Notes:
    leftPanel: mainInfo of Banner
    rightPanel: desktop displayed, mobile display:none
  */
  return (
    <div
      id={`offer-banner-${idBanner}`}
      className={`offerBanner homeA banner${idBanner}`}
    >
      { (link && link.url) && <a href={link.url} className="GW_linkOverBox" target="_blank"  />}
      <div className="row margin0 layout">
        <div className="container">
          <div className="leftPanel col-sm-6">
            <div className="row margin0">
              <div className="col-xs-8 col-sm-12">
                {textInfo && (
                  <BannerTextInfo {...textInfo} />
                )}
              </div>
              <div className="col-xs-4 col-sm-12">
                {textInfo && (
                <BannerPriceInfo {...textInfo} />
                )}
              </div>
            </div>
            <div className="row margin0">
              <div className="col-xs-12">
                <Button {...buttonProps} />
              </div>
            </div>
          </div>
          <div className="rightPanel col-sm-6" />
        </div>
      </div>
      { (link && link.url) && <a href={link.url} className="GW_linkOverBox" />}
      {/* stamp &&
        <Stamp {...stamp} >
          <div dangerouslySetInnerHTML={text2html(stamp.text)} />
        </Stamp>
      */}
    </div>
  );
}

export default HomeA;