export const getOptionsForStickyBar = (fileProduct, myCrossSellingList) => {
  const options = [];
  const keysOfProduct = Object.keys(fileProduct);

  keysOfProduct.forEach((item, index) => {
    if (item === 'technicalFile' && fileProduct && fileProduct.technicalFile) {
      options.push({
        idToScroll: 'FileTechnicalInformation',
        text: 'Ficha tcnica',
        alt: 'Ficha tcnica',
        sort: 3,
      })
    }
    if (item === 'phoneCaracs') {
      const keysCaracs = Object.keys(fileProduct.phoneCaracs);
      if (keysCaracs && keysCaracs.length > 0) {
        options.push({
          idToScroll: 'fileCaracs',
          text: 'Caractersticas',
          alt: 'Caractersticas',
          sort: 1,
        })
      }
    }
  })
  if (myCrossSellingList && myCrossSellingList.length > 0) {
    options.push({
      idToScroll: 'complements',
      text: 'Complementos',
      alt: 'Complementos',
      sort: 4,
    })
  }
  const { comments } = fileProduct;
  if (comments && comments.length > 0) {
    options.push({
      idToScroll: 'opiniones',
      text: 'Opiniones',
      alt: 'Opiniones',
      sort: 5,
    })
  }
  // Looking for present
  const { commercialInfo } = fileProduct;
  if (commercialInfo && commercialInfo.length > 0) {
    const presentItems = commercialInfo.find(commercialItem => commercialItem.present === true);
    if (presentItems) {
      const { domId } = presentItems;
      options.push({
        idToScroll: domId,
        text: 'Promocin',
        alt: 'Promocion',
        sort: 2,
      })
    }
  }

  const optionsSorted = options.sort((itemA, itemB) => itemA.sort - itemB.sort);
  return optionsSorted;
}
