import { cloneStructure } from '../utils/commonUtils'

export const types = {
  GET_CROSS_SELLING: 'GET_CROSS_SELLING',
  GET_CROSS_SELLING_SUCCESS: 'GET_CROSS_SELLING_SUCCESS',
  GET_CROSS_SELLING_ERROR: 'GET_CROSS_SELLING_ERROR',
}

export const initialState = {
  list: null,
  crossFetching: false,
  crossError: false,
}

const CrossSelling = (state = initialState, action) => {
  switch (action.type) {
    case types.GET_CROSS_SELLING: {
      return {
        ...state,
        crossFetching: true,
        crossError: false,
      }
    }
    case types.GET_CROSS_SELLING_SUCCESS: {
      const { data, alias } = action.payload;
      const newList = state.list || {};
      newList[alias] = data;
      const finalList = cloneStructure(newList);
      return {
        ...state,
        list: finalList,
        crossFetching: false,
        crossError: false,
      }
    }
    case types.GET_CROSS_SELLING_ERROR: {
      return {
        ...state,
        crossFetching: true,
        crossError: true,
      }
    }
    default: return { ...state }
  }
}

export const actions = {
  getCS: data => ({ type: types.GET_CROSS_SELLING, payload: { data } }),
  getCSSuccess: (data, alias) => ({ type: types.GET_CROSS_SELLING_SUCCESS, payload: { data, alias } }),
  getCSError: () => ({ type: types.GET_CROSS_SELLING_ERROR }),
}

export default CrossSelling;
