/* Only works for particulares segment, oh no */
export const getUrlByProductCategory = (productCategory, productSegment) => {
  switch (productCategory) {
    case PRODUCT_CATEGORY.SMARTPHONES:
      return URLS_LIBRARY.URL_SMARTPHONES;
    case PRODUCT_CATEGORY.SMARTWATCHES:
      return URLS_LIBRARY.URL_SMARTWATCHES;
    case PRODUCT_CATEGORY.TABLETS:
      return URLS_LIBRARY.URL_TABLETS;
    case PRODUCT_CATEGORY.TVS:
      return URLS_LIBRARY.URL_TVS;
    case PRODUCT_CATEGORY.BUNDLES:
      return URLS_LIBRARY.URL_BUNDLES;
    case PRODUCT_CATEGORY.EQUIPMENT:
      return URLS_LIBRARY.URL_EQUIPMENT;
    default: return URLS_LIBRARY.ALL;
  }
}

