import React from 'react';
import { createMarkup, numberOfSpecificTag } from '@bit/gerenciatd.utils.react-utils';
import Label from '../../../components/Label/Label';
import './TerminalFilePricesModule.css';
import MonthlyPaymentConditions from '../../MonthlyPaymentConditions/MonthlyPaymentConditions';
import { DELIVERYDATETEXT } from '../../../constants/constants';


const TerminalFilePricesModule = ({
  noClientPrice,
  clientPrice,
  originalPrice,
  stock,
  isClient,
  quota,
  quotaMonths,
  deliveryHome,
  deliveryNoReserve,
  deliveryShop,
  terminal,
}) => {
  let stockAvailable = false;
  if (stock > 0) {
    stockAvailable = true;
  }

  let priceToPayLiteral;
  let originalPriceLiteral;
  let doublePriceActive = false;

  if (isClient) {
    priceToPayLiteral = `${clientPrice}`;
    originalPriceLiteral = `${originalPrice}`;
    if ((clientPrice !== originalPrice) && (originalPrice > 0)) {
      doublePriceActive = true;
    }
  } else {
    priceToPayLiteral = `${noClientPrice}`;
    originalPriceLiteral = `${originalPrice}`;
    if (noClientPrice !== originalPrice) {
      doublePriceActive = true;
    }
  }

  const fundingComposedText = `A plazos ${quotaMonths} meses ${quota}/mes`;

  return (
    <div className="terminalFilePricesModule_wrapper">
      <div className="terminalFilePriceModule_title">
        { stockAvailable && (
          <Label text="En stock" />
        )}
      </div>
      <div className="terminalFilePriceModule_prices-wrapper">
        { doublePriceActive && (
        <div className="terminalPrice_general-wrapper terminalPrice_original-price-large">
          {originalPriceLiteral}
        </div>
        )}
        <div className=" terminalPrice_general-wrapper terminalPrice_price-to-pay-large">
          {priceToPayLiteral}
        </div>
      </div>
      <div className="terminalFilePriceModule_quota-wrapper">
        <MonthlyPaymentConditions
          labelText={fundingComposedText}
          terminal={terminal}
          stock={stock}
        />
        <div className="terminalFilePriceModule_funding-text">
          { deliveryHome && (
            <span
              dangerouslySetInnerHTML={createMarkup(DELIVERYDATETEXT.deliveryHome)}
            />
          )}

          { (!deliveryHome && (deliveryShop || deliveryNoReserve)) && (
            <span
              dangerouslySetInnerHTML={createMarkup(DELIVERYDATETEXT.deliveryShop)}
            />
          )}
          { (deliveryHome && (deliveryShop || deliveryNoReserve)) && (
            <span
              dangerouslySetInnerHTML={createMarkup(" o " + DELIVERYDATETEXT.deliveryShop)}
            />
          )}


        </div>
      </div>
    </div>
  )
}

export default TerminalFilePricesModule;
