import React from 'react';
import PropTypes from 'prop-types';
import './Label.css';

const Label = ({
  text,
  basicTextStyle,
  alignText,
  wrapperLabel,
}) => {
  return (
    <span
      className={`${basicTextStyle} label_${alignText} label_${wrapperLabel}`}
    >
      {text}
    </span>
  )
}

Label.propTypes = {
  text: PropTypes.string,
  alignText: PropTypes.string,
  wrapperLabel: PropTypes.string,
  basicTextStyle: PropTypes.string,
}

Label.defaultProps = {
  text: ' ',
  alignText: 'left',
  wrapperLabel: 'black',
  basicTextStyle: 'label_text',
}

export default Label;
