export const categoryFilter = (pillCategory, productFeaturesForFilter) => {
  let res = false;
  const anyPillActive = pillCategory.filter(item => item.active === true);
  if (anyPillActive && anyPillActive.length > 0) {
    anyPillActive.forEach((item) => {
      const str1 = item.label.toLowerCase();
      const cnd1 = productFeaturesForFilter.category.includes(str1);
      res = res || cnd1;
    })
  }
  if (anyPillActive && anyPillActive.length === 0) {
    res = true;
  }
  return res;
}

export const ratingFilter = (ratingList, productFeaturesForFilter) => {
  let res = true;
  const starSelected = ratingList.filter(star => star.active === true);
  const score = starSelected.length;
  const { rating } = productFeaturesForFilter;
  if (score) {
    if (score >= Math.round(rating) || (rating === 5 && score === 5)) {
      res = false;
    }
    if (rating === 5 && score === 5) {
      res = true;
    }
  }

  return res;
}

export const priceFilter = (priceSliderStatus, productFeaturesForFilter) => {
  let res = true;
  if (productFeaturesForFilter.minPrice) {
    if (
      priceSliderStatus.min > productFeaturesForFilter.minPrice
      || priceSliderStatus.max < productFeaturesForFilter.minPrice) {
      res = false;
    }
  }
  return res;
}

export const batteryFilter = (batterySliderStatus, productFeaturesForFilter) => {
  let res = true;
  if (productFeaturesForFilter.battery) {
    if (
      !(batterySliderStatus.min < productFeaturesForFilter.battery
      && batterySliderStatus.max > productFeaturesForFilter.battery)) {
      res = false;
    }
    if (productFeaturesForFilter.battery === -1) {
      res = true
    }
  }
  return res;
}

export const screenFilter = (screenSliderStatus, productFeaturesForFilter) => {
  let res = true;
  if (productFeaturesForFilter.screen) {
    if (
      !(screenSliderStatus.min < productFeaturesForFilter.screen
      && screenSliderStatus.max > productFeaturesForFilter.screen)
      && productFeaturesForFilter.screen !== -1) {
      res = false;
    }
  }
  return res;
}

export const backSideCameraFilter = (backsideCameraSliderStatus, productFeaturesForFilter) => {
  let res = true;
  if (productFeaturesForFilter.camera) {
    if (
      !(backsideCameraSliderStatus.min < productFeaturesForFilter.camera
      && backsideCameraSliderStatus.max > productFeaturesForFilter.camera)
      && productFeaturesForFilter.camera !== -1) {
      res = false;
    }
  }
  return res;
}

export const connectivityFiler = (filters, productFeaturesForFilter) => {
  // extract those color that are active
  let res = false;
  const activeConnectivity = filters.filter(item => item.active === true);
  const cndConnectivity = activeConnectivity && activeConnectivity.length > 0;
  productFeaturesForFilter.connectivity.forEach((connectivityitem) => {
    const exist = activeConnectivity.find(item => item.name === connectivityitem.toLowerCase())
    if (exist && !res) {
      res = true;
    }
  });
  // sino hay ninguno activo
  if (!cndConnectivity) {
    res = true;
  }
  return res;
}

