export const colorFilter = (filters, productFeaturesForFilter) => {
  // extract those color that are active
  let res = false;
  const activeColor = filters.color.filter(itemColor => itemColor.active === true);
  const cndColor = activeColor && activeColor.length > 0;
  productFeaturesForFilter.colorHex.forEach((colorsito) => {
    const exist = activeColor.find(item => item.hex === colorsito.toLowerCase())
    if (exist && !res) {
      res = true;
    }
  });
  // sino hay ninguno activo
  if (!cndColor) {
    res = true;
  }
  return res;
}

export const storageFilter = (filters, productFeaturesForFilter) => {
  let res = false;
  const activeStorage = filters.storage.filter(itemStorage => itemStorage.active === true);
  const cndActiveStorage = activeStorage && activeStorage.length > 0;
  if (cndActiveStorage) {
    activeStorage.forEach((itemStorageActive) => {
      let { label } = itemStorageActive;
      label = parseInt(label, 10);
      const exist = productFeaturesForFilter.storage.find((amount) => {
        const auxAmount = parseInt(amount, 10);
        return auxAmount === label
      });
      if (exist) {
        res = true;
      }
      return res;
    });
  } else if (!cndActiveStorage) {
    res = true;
  }
  return res;
}


export const soFilter = (filters, productFeaturesForFilter) => {
  let res = false;
  const activeSo = filters.soList.filter(itemSO => itemSO.active === true);
  const cndActiveSo = activeSo && activeSo.length > 0;
  if (cndActiveSo) {
    activeSo.forEach((itemActiveSo) => {
      let { label } = itemActiveSo;
      label = label.toLowerCase();
      const deviceSo = productFeaturesForFilter.os.toLowerCase();
      if (label === deviceSo && deviceSo !== '') {
        res = true;
      }
    })
  } else if (!cndActiveSo) {
    res = true;
  }
  return res;
}

export const brandFilter = (brand, productFeaturesForFilter) => {
  let res = false;
  const activeBrand = brand.filter(itemBrand => itemBrand.active === true);
  const cndActiveBrand = activeBrand && activeBrand.length > 0;
  if (cndActiveBrand) {
    activeBrand.forEach((itemActiveBrand) => {
      let { label } = itemActiveBrand;
      label = label.toLowerCase();
      const deviceBrand = productFeaturesForFilter.brand.toLowerCase();
      if (label === deviceBrand && deviceBrand !== '') {
        res = true;
      }
    })
  } else if (!cndActiveBrand) {
    res = true;
  }
  return res;
}
