import React from 'react';
import TerminalFileTechnicalInformationElement from '../TerminalFileTechnicalInformationElement/TerminalFileTechnicalInformationElement';
import './TerminalFileTechnicalInformation.css';

const TerminalFileTechnicalInformation = ({ terminal }) => {
  const { technicalFile, name } = terminal;
  const h3Composed = `Ficha tcnica del ${name}`;
  return (
    <div className="container">
      <div id="FileTechnicalInformation" className="terminalFileTechnicalInformation_block-wrapper">
        <h3>{h3Composed}</h3>
      </div>
      <div className="terminalFileTechnicalInformation_wrapper">
        { technicalFile.map((technicalCategory, index) => {
          const key = `technicalCategory_${index}`;
          const { title, mainDescription } = technicalCategory;
          return (
            <TerminalFileTechnicalInformationElement
              key={key}
              category={title}
              categoryDetail={mainDescription}
            />
          )
        })}
      </div>
    </div>
  )
}
export default TerminalFileTechnicalInformation;