import React, { Component } from 'react';
import FlyOut, { flyOutActions } from '@bit/gerenciatd.react.fly-out';
import './MonthlyPaymentConditions.css';
import { connect } from 'react-redux';
import { actions as productActions } from '../../reducers/products';
import Button from '@bit/gerenciatd.react.button';
import Label from '../../components/Label/Label';
import LayerFlyout from '../LayerFlyout/LayerFlyout';
import Link from '../../components/Link/Link';
import store from '../../app/store';
import { URLSPURCHASEPROCESS, FRACTIONALLIMITPRICES } from '../../constants/constants';
import { launchIsClientModal } from '../../utils/dataHandlers/purchaseDataHandler';


/* jsx, proptypes SOY UN COMPONENTE, hazme escalable porfavor recibir el men por props... */
/* */
/* Si cliente y precio <= 600 --> Pago a plazos sin intereses con FlyOut y el precio depende de si se cataloga o no
   Si cliente > 600 --> Precio financiado y FlyOut
   No cliente --> Nada
 */
class MonthlyPaymentConditions extends Component {

  constructor(props) {
    super(...props);
  /*  const { titleFlyoutInterestFree } = FLYOUTINTERESFREE;
    this.state = {
      titleFlyoutInterestFree  = titleFlyoutInterestFree;
    };
    */
  }

  fundedButtonClicked = () => {
    const { productsSelected, terminal, isClient } = this.props;
    if (!launchIsClientModal(productsSelected, terminal, isClient)) {
      if (productsSelected.productSpecs === null
        || productsSelected.productSpecs === ''
        || productsSelected.productSpecs.storage === null
        || productsSelected.productSpecs.storage === ''
        || productsSelected.productSpecs.color === null
        || productsSelected.productSpecs.color === '') {
        window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-libre-cliente&codION=${productsSelected.codIon}&solicitarFinanciacion=si&codTerm=${terminal.metadata.cloudId}`;
      } else {
        window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-libre-cliente&codION=${productsSelected.codIon}&codTerm=${terminal.metadata.cloudId}&solicitarFinanciacion=si&productOffering=${productsSelected.productOffering}&idCapacidadTelco=${productsSelected.productSpecs.storage}&idColorTelco=${productsSelected.productSpecs.color}`;
      }
    }
  }

  launchIsClientFlyoutFinancing = () => {
    this.props.setFlyOutVisibility('idFlyoutFinancing');
  }

  launchIsClientFlyoutInteresFree = () => {
    this.props.setFlyOutVisibility('idFlyoutInterestFree');
  }

  render() {
    const { productsSelected, isClient, stock } = this.props;
    const { legalConditions } = this.props;
    const { clientPrice, quota, quotaMonths, showFragmentedQuota, fragmentedQuota, fragmentedQuotaMonths } = productsSelected;
    const { terminal } = this.props;
    const alias = terminal.alias.replace('ntt_', '');

    return (
      <div className="monthlyPaymentConditions_quota-wrapper">
        <LayerFlyout
           stock={stock}
           terminal={terminal}
           typeOfFlyout="flyoutFinancing">
        </LayerFlyout>
        
        <LayerFlyout
           terminal={terminal}
           stock={stock}
           typeOfFlyout="flyoutInterestFree">
        </LayerFlyout>     

        { (isClient === true || isClient === null) && showFragmentedQuota && (
          <div className="monthlyPaymentConditions_funding-text">
            <Label
              basicTextStyle="basic_monthly_conditions"
              text={`${fragmentedQuota}/mes en${fragmentedQuotaMonths} meses sin intereses`} 
            />
            <Link
              text="Ver condiciones"
              alignText="left"
              wrapperLabel="monthlyPaymentConditions_funding-conditions"
              handleClick={this.launchIsClientFlyoutInteresFree}
              idTagging={`PA-ficha-movil-ver-condiciones-pago-fraccionado-${alias}`}
            />
          </div>
        )}
        { (isClient === true || isClient === null) && clientPrice > FRACTIONALLIMITPRICES && (
          <div className="monthlyPaymentConditions_funding-text">
            <Label
              basicTextStyle="basic_monthly_conditions"
              text={`A plazos ${quotaMonths} meses ${quota}/mes`}
            />
            <Link
              text="Ver condiciones"
              alignText="left"
              wrapperLabel="monthlyPaymentConditions_funding-conditions"
              handleClick={this.launchIsClientFlyoutFinancing}
              idTagging ={`PA-ficha-movil-ver-condiciones-financiacion-${alias}`}
            />
          </div>
        )}
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  setFlyOutVisibility: idFlyOut => dispatch(flyOutActions.setFlyOutVisibility(idFlyOut)),
})

const mapStateToProps = state => ({
  productsSelected: state.products.offerSelected,
  isClient: state.products.isClient,
})

export default connect(mapStateToProps, mapDispatchToProps)(MonthlyPaymentConditions);
