import React from 'react'
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';

const MainStructure = ({ title, children }) => (
  <div className="terminalListFilterElementWrapper">
    <RowGrid>
      <ColGrid mobileCols={12}>
        <div className="terminalListFilter_title">{title}</div>
        <div className="terminalListFilter_slider-wrapper">
          {children}
        </div>
      </ColGrid>
    </RowGrid>
  </div>
)

export default MainStructure;