import React from 'react';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import Button from '@bit/gerenciatd.react.button';
import { addCustomCssRule } from '../../../utils/cssUtils';
import Stamp from '../../../components/Stamp/Stamp';
import Image from '../../../components/Image/Image';
import BannerTitle from '../../../components/BannerTitle/BannerTitle';
import Description from '../../../components/Description/Description';
import { getColorForBackgroundBanner } from '../../../utils/commonUtils';
import './BannerRegularGenericWide.css';

const BannerRegularGenericWide = ({ ...props }) => {
  const {
    backgroundImage,
    backgroundImageMobile,
    backgroundColor,
    bannerTextContentAlign,
    terminalImage,
    terminalAlt,
    stampText,
    stampAlign,
    stampStyle,
    stampSecondaryText,
    stampSecondaryAlign,
    stampSecondaryStyle,
    bannerTitle,
    bannerDescription,
    mainPrice,
    originalPrice,
    buttonText,
    buttonType,
    buttonTheme,
    link,
    conditions,
  } = props;

  const handleClicked = () => {
    window.location.href = link;
  }

  let { priceHeaderText } = props;
  if (!priceHeaderText) {
    priceHeaderText = 'Desde';
  }

  const backgroundClassColor = getColorForBackgroundBanner(backgroundColor);

  addCustomCssRule(".BannerRegularGenericWide_background", `background-image:url(${backgroundImageMobile})`, false);
  addCustomCssRule(".BannerRegularGenericWide_background", `background-image:url(${backgroundImage})`, true, 768);
  const bannerKey =  Math.floor(Math.random() * 1000);

  return (
    <div className={`${backgroundClassColor} containerBanner`}>
      <div className="container">
        <div className="bannerFlexGrid">
          <div className="bannerFlexText">
            <div className="bannerRegularGenericWide_wrapper_content">
              <div className={`banner-content-wrapper banner-content-align-${bannerTextContentAlign}`}>
                {stampText && (
                  <div className="BannerRegularGenericWide_stamp-wrapper">
                    <Stamp text={stampText} alignText={stampAlign} typeStamp={stampStyle} />
                  </div>
                )}
                {stampSecondaryText && (
                  <div className="BannerRegularGenericWide_stamp-wrapper">
                    <Stamp
                      text={stampSecondaryText}
                      alignText={stampSecondaryAlign}
                      typeStamp={stampSecondaryStyle}
                    />
                  </div>
                )}
                {bannerTitle && (
                  <BannerTitle text={bannerTitle} />
                )}
              </div>
              <div className="visible-xs">
                <div className="">
                  <img alt={terminalAlt} src={terminalImage} />
                </div>
              </div>
              <div className="BannerRegularGenericWide_button-wrapper">
                {bannerDescription && (
                  <div className="">
                    <Description text={bannerDescription} />
                  </div>
                )}
                {conditions && (
                  <div className="BannerRegularGenericWide_conditions">
                    {conditions}
                  </div>
                )}
                {mainPrice && (
                  <div className="BannerRegularGenericWide_price-module">
                    <div className="BannerRegularGenericWide_price_module_text">
                      {priceHeaderText}
                    </div>
                    <div className="BannerRegularGenericWide_price-module_wrapper">
                      {originalPrice && (
                        <div className="BannerRegularGenericWide_price-module_original">
                          {originalPrice}
                        </div>
                      )}
                      <div className="BannerRegularGenericWide_price-module_main">
                        {mainPrice}
                      </div>
                    </div>
                    <div className="BannerRegularGenericWide_price-module_taxes">
                      Impuestos incl.
                    </div>
                  </div>
                )}
                <Button wrapperClass="BannerRegularGenericWide_button" type={buttonType} theme={buttonTheme} onClick={handleClicked} label={buttonText} />
              </div>
            </div>
          </div>
          <div className="bannerFlexImg">
            <div className="BannerRegularGenericWide_wrapper">
              <div className="hidden-xs">
                <Image id={bannerKey} dynamicResize alt={terminalAlt} url={terminalImage} opacity="1" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default BannerRegularGenericWide;
