import axios from 'axios';
import { call, put, all } from 'redux-saga/effects';
import { API } from '../constants/api'
import { fetchProducts, fetchPhoneByAlias } from './products';
import { makeItPlainArray } from 'utils/commonUtils';
import { actions as backupActions } from '../reducers/backupGenerator';

function* loopFetchDevice(data) {
  try {
    return yield call(fetchPhoneByAlias, data);
  } catch (err) {
    console.log(err);
  }
}

export function* getBackup(args) {
  try {
    const { data } = args.payload;
    const res = {
      segment: data.segment,
      jsonList: null,
      jsonDetailed:null,
    }
    const response = yield call(fetchProducts, data);
    if (response && response.data) {
      res.jsonList = response.data;
    }
    /* Prepare structure for fetching */
    const prepareDetailFetching = [];
    if (res.jsonList) {
      const jsonListPlained = makeItPlainArray(res.jsonList)
      jsonListPlained.forEach(item => {
        prepareDetailFetching.push({
          segment: data.segment,
          alias: item.alias,
        });
      });
    }
    /* Now we fetch every product OMG */
    const responses = yield all(prepareDetailFetching.map(item => loopFetchDevice(item)));
    res.jsonDetailed = responses.map(item => item.data);
    yield put(backupActions.getBackupSuccess(res));
    
    // yield put(csActions.getCSSuccess(crossNormalized, productInfo.alias));
  } catch (err) {
    yield put(backupActions.getBackupError(res));
    // yield put(csActions.getCSError())
  }
}
