import React, { Component } from 'react';
import Button, { buttonThemes, buttonTypes } from '@bit/gerenciatd.react.button';
import { reviews } from '../../../constants/terminalFileTexts';
import { REVIEWS_TO_SHOW } from '../../../constants/constants';
import ReviewList from '../../../componentList/ReviewList/ReviewList';
import Image from '../../../components/Image/Image';
import './TerminalFileReviews.css';

class TerminalFileReviews extends Component {
  constructor(props) {
    super(...props);
    const { DEFAULT } = REVIEWS_TO_SHOW;
    this.state = {
      elementsToShow: DEFAULT,
    }
  }

  updateElementsToShow = () => {
    const { INCREMENT } = REVIEWS_TO_SHOW;
    const { elementsToShow } = this.state;
    const newElementsToShow = elementsToShow + INCREMENT;

    this.setState({ elementsToShow: newElementsToShow });
  }

  render() {
    const { terminal } = this.props;
    const { elementsToShow } = this.state;
    const { logo, logoAlt } = reviews;
    const { comments, name, numComments } = terminal;
    let seeMoreRewviews = true;
    if (numComments <= elementsToShow) {
      seeMoreRewviews = false;
    }
    if (!comments || !(comments.length > 0)) {
      return null;
    }
    return (
      <div id="opiniones" className="container terminalFileReviews_wrapper">
        <div id="terminalFileReviews" className="terminalFileReviews_block-wrapper">
          <h3>Opiniones sobre el {name}</h3>
        </div>
        <div className="terminalFileReviews_logo-wrapper">
          <Image url={logo} alt={logoAlt} />
        </div>
        <ReviewList reviewsList={comments} elementsToShow={elementsToShow} />
        <div className="terminalFileReview_button-wrapper">
          { seeMoreRewviews && (
            <Button wrapperClass="bannerRegularGeneric_button" type={buttonTypes.SOLID} theme={buttonThemes.BLUE} label="VER MS OPINIONES" onClick={this.updateElementsToShow} />
          )}
        </div>
      </div>
    )
  }
}
export default TerminalFileReviews;