import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { externalLink } from '../../utils/htmlUtils';
import './BreadCrumbs.css';

/**
* Stateless component to draw an icon.
* @param {string} text path of the image.
* @returns {JSX} Component image JSX.
*/

const BreadCrumbs = ({ arrayBreadCrumbs }) => {
  return (
    <Fragment>
      {
          arrayBreadCrumbs.map((item, index) => {
            const currentLink = item.url.toLowerCase().replace('ntt_', '');
            const external = externalLink(currentLink);
            if (!external) {
              return (
                <span key={item.id} className="BreadCrumbsItem">
                  <Link
                    className="breadCrumLink"
                    to={`${item.url.toLowerCase().replace('ntt_', '')}`}
                  >
                    <span>{`${item.name}${index < (arrayBreadCrumbs.length - 1) ? ' /' : ''}`}</span>
                  </Link>
                </span>
              );
            }
            return (
              <span key={item.id} className="BreadCrumbsItem">
                <a className="breadCrumLink" href={currentLink}>{item.name}</a>
                /
              </span>
            )
          })
      }
    </Fragment>
  )
}

BreadCrumbs.propTypes = {
  arrayBreadCrumbs: PropTypes.array,
}


BreadCrumbs.defaultProps = {
  arrayBreadCrumbs: [
    {
      id: 1,
      name: 'Inicio',
      url: 'www.movistar.es',
    },
    {
      id: 2,
      name: 'Fin',
      url: 'www.google.es',
    },
  ],
}

export default BreadCrumbs;
