import React, { Component } from 'react';
import { sendButtonTagging } from '../../utils/taggingUtils';
import './ColorSelector.css';

export const paletteColors = {
  rojo: 'rojo',
  amarillo: 'amarillo',
  azul: 'azul',
  negro: 'negro',
  blanco: 'blanco',
  gris: 'gris',
  none: 'none',
}

export const themeColor = {
  normal: 'regular',
  invertido: 'inverted',
}

class ColorSelector extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  handleClick = () => {
    const { onclick, colorHex } = this.props;
    sendButtonTagging(`PA-listado-termnales-selector-capacidad-dispositivo-${colorHex}`, JSON.stringify(colorHex));
    onclick(colorHex);
  }

  render() {
    const { colorHex, colorName, active } = this.props;
    return (
      <div className="colorSelector_wrapper" onClick={this.handleClick}>
        <div
          style={{ backgroundColor: colorHex }}
          title={colorName}
          className={`colorSelector_circle-border_regular colorSelector_circle-form_${active}`}
        />
      </div>
    )
  }
}

export default ColorSelector;
