import React, { Component } from 'react';
import { connect } from 'react-redux';
import ScrollMemory from 'react-router-scroll-memory';
import RoutesAndBoundary from './RoutesAndBoundary/RoutesAndBoundary';
import { actions as myRouterActions } from '../../../reducers/myRouter';
/** * Stateful component to draw MainContent.
 * @returns {JSX} Component MainContent JSX.
 */
/*
export const previousLocation = [];
*/
class MainContent extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  static getDerivedStateFromProps(props, state) {
    const { currentLocation } = props;
    if (currentLocation !== state.currentLocation) {
      props.setPreviousLocation(currentLocation);
      if (window.s) {
        window.s.products = '';
      }

      return {
        ...state,
        previousLocation: state.currentLocation,
        currentLocation,
      }
    }

    return {
      ...state,
    }
  }

  render() {
    const { history } = this.props;
    return (
      <div id="mainContent" className="mainContent">
        <RoutesAndBoundary
          history={history}
          location={this.state.currentLocation}
        />
        <ScrollMemory />
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    setPreviousLocation: location => dispatch(myRouterActions.setPreviousLocation(location)),
  }
}

const mapStateToProps = state => ({
  currentLocation: state.router.location.pathname,
  previousLocation: state.myRouter.previousLocation,
})

export default connect(mapStateToProps, mapDispatchToProps)(MainContent)
