import { cloneStructure } from '../utils/commonUtils';

export const types = {
  GET_BACKUP: 'GET_BACKUP',
  GET_BACKUP_SUCCESS: 'GET_BACKUP_SUCCESS',
  GET_BACKUP_ERROR: 'GET_BACKUP_ERROR',
}

export const initialState = {
  backup: {
  },
  backupFetching: false,
  backupError: false,
}

const CrossSelling = (state = initialState, action) => {
  switch (action.type) {
    case types.GET_BACKUP: {
      return {
        ...state,
        backupFetching: true,
        backupError: false,
      }
    }
    case types.GET_BACKUP_SUCCESS: {
      const { data } = action.payload;
      const res = {};
      res[data.segment] = {
        all: data.jsonList,
        detailed: data.jsonDetailed,
      }

      let newBackup =  { ...state.backup }
      newBackup = {
        ...newBackup,
        ...res,
      }
      
      return {
        ...state,
        backup: newBackup,
        backupFetching: false,
        backupError: false,
      }
    }
  
    default: return { ...state }
  }
}

export const actions = {
  getBackup: data => ({ type: types.GET_BACKUP, payload: { data } }),
  getBackupSuccess: data => ({ type: types.GET_BACKUP_SUCCESS, payload: { data } }),
  getBackupError: () => ({ type: types.GET_BACKUP_ERROR }),
}

export default CrossSelling;
