import React, { Component } from 'react';
import { connect } from 'react-redux';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import Button, { buttonTypes, buttonThemes } from '@bit/gerenciatd.react.button';
import { taggingButtonTypes  } from '@bit/gerenciatd.utils.tagging-utils';
import Modal, { modalActions } from '@bit/gerenciatd.react.modal';
import { flyOutActions } from '@bit/gerenciatd.react.fly-out';
import Advice, { adviceThemes } from '@bit/gerenciatd.react.advice';
import iconLibrary from '@bit/gerenciatd.react.icon-library';
import SegmentedInput from '@bit/gerenciatd.react.segmented-input';
import store from '../../../app/store';
import { actions as productActions } from '../../../reducers/products';
import ColorSelectorList from '../../../componentList/ColorSelectorList/ColorSelectorList';
import CapacityList from '../../../componentList/CapacityList/CapacityList';
import TerminalFilePricesModule from '../TerminalFilePricesModule/TerminalFilePricesModule';
import { findQueryOffer } from '../../../utils/commonUtils';
import './TerminalFileMainInfo.css';
import { URLSPURCHASEPROCESS } from '../../../constants/constants';
import { launchIsClientModal } from '../../../utils/dataHandlers/purchaseDataHandler';

class TerminalFileMainInfo extends Component {
  constructor(props) {
    super(...props);
    if (props.offerSelected.clientPrice === 0) {
      props.updateIsClient(false);
    } else {
      props.updateIsClient(true);
    }
    this.state = {
    };
  }

  handleClick = (value) => {
    let { isClient } = this.props;
    if (value === 'client') {
      isClient = true;
    } else {
      isClient = false;
    }
    this.props.updateIsClient(isClient);
  }

  tryToUpdateOfferSelected = (newProductSpecs) => {
    const { terminal } = this.props;
    const { offers } = terminal;
    const queryOffer = findQueryOffer(offers, newProductSpecs);
    if (queryOffer) {
      this.props.updateOfferSelected(queryOffer);
    }
  }

  changeColor = (colorHex) => {
    const { offerSelected } = this.props;
    const { storage } = offerSelected;
    const newProductSpecs = {
      storage,
      colorHex,
    }
    this.tryToUpdateOfferSelected(newProductSpecs);
  }

  changeStorage = (storage) => {
    const { offerSelected } = this.props;
    const { colorHex } = offerSelected;
    const newProductSpecs = {
      storage,
      colorHex,
    }
    this.tryToUpdateOfferSelected(newProductSpecs);
  }

  handleMainModalButton = () => {
    // A plazos
    const { offerSelected, terminal } = this.props;
    window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-financiado&codION=${offerSelected.codIon}&codTerm=${terminal.metadata.cloudId}&solicitarFinanciacion=si&productOffering=${offerSelected.productOffering}&idCapacidadTelco=${offerSelected.productSpecs.storage}&idColorTelco=${offerSelected.productSpecs.color}`;
  }

  handleSecondaryModalButton = () => {
    const { offerSelected, terminal } = this.props;
    window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-libre-cliente&codION=${offerSelected.codIon}&codTerm=${terminal.metadata.cloudId}&productOffering=${offerSelected.productOffering}&idCapacidadTelco=${offerSelected.productSpecs.storage}&idColorTelco=${offerSelected.productSpecs.color}`;
  }

  launchIsClientModalClick = () => {
    const { offerSelected, terminal } = this.props;
    const { isClient } = this.props;
    if (!launchIsClientModal(offerSelected, terminal, isClient)) {
      this.props.setModalVisibility('lbPaymentType');
    }
  }

  render() {
    const {
      offerSelected,
      views,
      storageAndColorList,
      terminal,
    } = this.props;
    const alias = terminal.alias.replace('ntt_', '');
    const {
      stock,
      clientPrice,
      originalPrice,
      quota,
      quotaMonths,
      colorHex,
      storage,
      deliveryHome,
      deliveryNoReserve,
      deliveryShop,
    } = offerSelected;
    const storageList = Object.keys(storageAndColorList);
    const colorList = storageAndColorList[storage];
    const noClientPrice = offerSelected.currentPrice;
    const terminalBarViews = `${views} personas se han interesado por este producto`;
    const terminalBarStock = `Se estn agotando, solo quedan ${stock} unidades!`;
    const terminalBarNoStock = 'Sin stock';
    const terminalBarStockLimited = 'Se est agotando! Quedan pocas unidades';

    const segmentedOptions = [
      {
        label: 'No soy Cliente mvil',
        value: 'anonymous',
        tagging: `PA-ficha-movil-selector-cliente-no-soy-cliente-${alias}`,
      },
      {
        label: 'Soy Cliente mvil',
        value:'client',
        tagging: `PA-ficha-movil-selector-cliente-no-soy-cliente-${alias}`,
      },
    ];
    let { isClient } = this.props;

    const mainButtonModal = {
      handleClick: this.handleMainModalButton,
      label: 'Pago a plazos',
      props: this.props,
    }

    const secondaryButtonModal = {
      handleClick: this.handleSecondaryModalButton,
      label: 'Pago con tarjeta',
    }

    const linksArray = [];
    
    const clientValue = 'client';
    let clientClass = '';
    if (offerSelected.clientPrice === 0) {
      isClient = false;
      clientClass = 'anonymous';
    }
    if (offerSelected.currentPrice === 0) {
      isClient = true;
      clientClass = 'anonymous';
    }

    return (
      <div className="">
        <Modal
          idModal="lbPaymentType"
          title="Elige la forma de pago"
          mainButton={mainButtonModal}
          secondaryButton={secondaryButtonModal}
          linksArray={linksArray}
          store={store}
        >
          <div>
            <span className="typePay">Pago a plazos:</span>
            Solo para clientes Movistar con internet y lnea mvil, incluido Fusin. Envo a domicilio.
          </div>
          <div>
            <span className="typePay">Pago con tarjeta:</span>
             Envo a domicilio en 48 horas o recogida en tienda.
          </div>        
        </Modal>
        <RowGrid>
          <ColGrid mobileCols={12}>
            {views && views > 50
            && (
              <div className="terminalFileMainInfo_information-bar-padding">
                <Advice theme={adviceThemes.INFO} title={terminalBarViews} iconClass={iconLibrary.usuario} />
              </div>
            )}
            {stock < 10 && stock > 0
            && (
              <div className="terminalFileMainInfo_information-bar-padding">
                <Advice theme={adviceThemes.ERROR} title={terminalBarStock} />
              </div>
            )}
            {stock < 50 && stock >= 10
            && (
              <div className="terminalFileMainInfo_information-bar-padding">
                <Advice theme={adviceThemes.ERROR} title={terminalBarStockLimited} />
              </div>
            )}
            {stock === 0
            && (
              <div className="terminalFileMainInfo_information-bar-padding">
                <Advice theme={adviceThemes.ERROR} title={terminalBarNoStock} />
              </div>
            )}
          </ColGrid>
        </RowGrid>
        <RowGrid>
          <ColGrid mobileCols={12}>
            <div className={`terminalFileMainInfo_wrapper-segmented-input ${clientClass}`}>
              <SegmentedInput
                id={`PA-ficha-movil-selector-cliente-${alias}`}
                taggingType={taggingButtonTypes.BUTTON}
                defaultValue={clientValue}
                optionSize="double"
                onChange={this.handleClick}
                options={segmentedOptions}
              />
            </div>
          </ColGrid>
        </RowGrid>
        
        {(isClient && clientClass == 'anonymous') && (
          <RowGrid>
            <ColGrid mobileCols={12}>
              <div className={`terminalFileMainInfo_wrapper-segmented-input monthlyPaymentConditions_funding-text terminalFileMainInfoBorder ${clientClass}`}>
            <div>Solo para clientes </div>
            </div>
            </ColGrid>
          </RowGrid>
        )}  
        
        <RowGrid>
          <ColGrid mobileCols={12}>
            <div className="terminalFileMainInfo_wrapper-module">
              {(storageList && storageList.length > 0) && (
                <div className="capacitySelectorFile_customWrapper">
                  <CapacityList
                    idTag={`PA-ficha-movil-selector-capacity${alias}`}
                    storageList={storageList}
                    storage={storage}
                    onClick={this.changeStorage}
                  />
                </div>
              )}
              {(colorList && colorList.length > 0) && (
                <div className="colorSelectorFile_customWrapper">
                  <ColorSelectorList
                    idTag={`PA-ficha-movil-selector-color${alias}`}
                    colorList={colorList}
                    colorHexSelected={colorHex}
                    onclick={this.changeColor}
                  />
                </div>
              )}
              <div className="priceModule">
                <TerminalFilePricesModule
                  clientPrice={clientPrice}
                  noClientPrice={noClientPrice}
                  originalPrice={originalPrice}
                  stock={stock}
                  isClient={isClient}
                  quota={quota}
                  quotaMonths={quotaMonths}
                  deliveryHome={deliveryHome}
                  deliveryNoReserve={deliveryNoReserve}
                  deliveryShop={deliveryShop}
                  terminal={terminal}
                />
                { stock > 0 && (
                  <div className="terminalFilePriceModule_btn-wrapper">
                  <Button
                    type={buttonTypes.SOLID}
                    theme={buttonThemes.GREEN}
                    label="LO QUIERO"
                    taggingType={taggingButtonTypes.BUTTON}
                    id={`PA-ficha-movil-loquiero-${alias}`}
                    wrapperClass="terminalFilePriceModule_buy-button-wrapper"
                    onClick={this.launchIsClientModalClick}
                  />
                </div>
                )}
                { stock <= 0 && (
                  <div className="terminalFilePriceModule_btn-wrapper-finish">
                  <Button
                    type={buttonTypes.SOLID}
                    theme={buttonThemes.GREEN}
                    label="AGOTADO ONLINE"
                    taggingType={taggingButtonTypes.BUTTON}
                    id={`PA-ficha-movil-loquiero-${alias}`}
                    wrapperClass="terminalFilePriceModule_buy-button-wrapper"
                  />
                </div>
                )}
              </div>
            </div>
          </ColGrid>
        </RowGrid>
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  updateOfferSelected: offerSelected => dispatch(productActions.updateOfferSelected(offerSelected)),
  setFlyOutVisibility: idFlyOut => dispatch(flyOutActions.setFlyOutVisibility(idFlyOut)),
  setModalVisibility: idModal => dispatch(modalActions.setModalVisibility(idModal)),
  updateIsClient: isClient => dispatch(productActions.updateIsClient(isClient)),
})

const mapStateToProps = state => ({
  offerSelected: state.products.offerSelected,
  isClient: state.products.isClient,
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalFileMainInfo);
