import React from 'react';
import PropTypes from 'prop-types';
import './Star.css';

/**
* Stateless component to draw an icon.
* @param {string} url path of the image.
* @returns {JSX} Component image JSX.
*/

const Star = ({
  index,
  starClass,
  starVisualization,
  onClick,
}) => {
  const handleClick = () => {
    if (onClick && index) {
      onClick(index)
    }
  }

  /* keyboard handler */
  const handleKeyPress = (evt) => {
    const code = evt.keyCode || evt.which;
    if (code === 13) {
      handleClick(evt);
    }
  }


  return (
    <div
      className={`starWrapper-${starVisualization} icon-estrella-${starClass}`}
      onClick={onClick ? handleClick : null}
      onKeyPress={onClick ? handleKeyPress : null}
      role="button"
      tabIndex="-1"
    />
  )
}

Star.propTypes = {
  index: PropTypes.number,
  starClass: PropTypes.string,
  onClick: PropTypes.func,
}


Star.defaultProps = {
  index: 0,
  starClass: '',
  onClick: null,
}

export default Star;
