
import React from 'react';
import PropTypes from 'prop-types';
import StarList from '../../componentList/StarList/StarList';
import './Review.css';

const Review = ({ review }) => {
  const { name, date, rating, comment } = review;
  let composedText = 'Por';
  if (name) {
    composedText = `${composedText} ${name}`;
  }
  if (date) {
    composedText = `${composedText} ${date}`;
  }
  return (
    <div className="review_wrapper">
      { rating && (
        <StarList starVisualization="regular" rating={rating} />
      )}
      { composedText && (
        <div className="review_text">{composedText}</div>
      )}
      { comment && (
        <div className="review_text">{comment}</div>
      )}
    </div>
  )
}

Review.propTypes = {
  review: PropTypes.object,
}

Review.defaultProps = {
  review: {},
}

export default Review;
