
import React from 'react';
import PropTypes from 'prop-types';
import './Link.css';
import { sendButtonTagging } from '../../utils/taggingUtils';

const Link = ({
  text,
  alignText,
  wrapperLabel,
  url,
  target,
  handleClick,
  idTagging,
}) => { 
  const myHandleClick = () => {
    sendButtonTagging(idTagging);
    if (handleClick) {
      handleClick()
    }
  }

  return (
    <span>
      { handleClick && (
        <span
          className={`link_text label_${alignText} link_${wrapperLabel}`}
          onKeyPress={myHandleClick}
          onClick={myHandleClick}
          role="button"
          tabIndex="0"
        >
          {text}
        </span>
      )}
      { !handleClick && (
        <a
          href={url}
          className={`link_text label_${alignText} link_${wrapperLabel}`} 
          target={target}
        >
          {text}
        </a>
      )}
    </span>
  )
}

Link.propTypes = {
  text: PropTypes.string,
  alignText: PropTypes.string,
  wrapperLabel: PropTypes.string,
  url: PropTypes.string,
  target: PropTypes.string,
  handleClick: PropTypes.func,
  idTagging: PropTypes.string,
}

Link.defaultProps = {
  text: ' ',
  alignText: 'left',
  wrapperLabel: 'black',
  url: '',
  target: '',
  handleClick: null,
  idTagging: '',
}

export default Link;
