import React from 'react';
import PropTypes from 'prop-types';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import Stamp from '../../components/Stamp/Stamp';
import './StampList.css'

/**
* Stateless component to draw an icon and a label.
* @param {string} text path of the image.
* @returns {JSX} Component image JSX.
*/

const StampList = ({ stamps, stock, wrapperClass }) => {
  return (
    <div className="stampDiv">
      { stock > 0 && stamps.map((value, index) => {
        return (
          <RowGrid key={`StampRowItem${index.toString()}`}>
            <ColGrid mobileCols={12}>
              <div className={`stampLeft stamp_wrapper ${wrapperClass}`}>
                <Stamp
                  key={value.kind}
                  text={value.text}
                  typeStamp={`stamp_${value.kind}`}
                />
              </div>
            </ColGrid>
          </RowGrid>
        )
      })}
      { stock <= 0 && (
        <RowGrid>
          <ColGrid mobileCols={12}>
            <div className={`stampLeft stamp_wrapper ${wrapperClass}`}>
              <Stamp
                text="AGOTADO ONLINE"
                typeStamp="stamp_outOfStock"
              />
            </div>
          </ColGrid>
        </RowGrid>
      )}

    </div>
  )
}

StampList.propTypes = {
  stamps: PropTypes.array,
}

StampList.defaultProps = {
  stamps: [],
}

export default StampList;
