import React from 'react';
import LinkOverBox from '@bit/gerenciatd.react.link-over-box';
import { addCustomCssRule } from '../../../utils/cssUtils';
import Image from '../../../components/Image/Image';
import { getColorForBackgroundBanner } from '../../../utils/commonUtils';
import './BannerRegularApple.scss';

const BannerRegularApple = ({ ...props }) => {
  const {
    backgroundImage,
    backgroundImageMobile,
    backgroundColor,
    link,
    buttonDescription,
  } = props;

  let { priceHeaderText } = props;
  if (!priceHeaderText) {
    priceHeaderText = 'Desde';
  }

  addCustomCssRule('.BannerRegularApple_background', `background-image:url(${backgroundImageMobile})`, false);
  addCustomCssRule('.BannerRegularApple_background', `background-image:url(${backgroundImage})`, true, 768);
  const backgroundClassColor = getColorForBackgroundBanner(backgroundColor);
  return (
    <div className={`${backgroundClassColor}`}>
      <div className="BannerApple container">
        <img
          src={backgroundImageMobile}
          className="visible-xs"
          alt="pic-mobile"
        />
        <img
          src={backgroundImage}
          className="hidden-xs"
          alt="pic-pc"
        />
        <LinkOverBox link={link} alt={buttonDescription} />
      </div>
    </div>
  )
}
export default BannerRegularApple;
