import React from 'react';
import { createMarkup } from '@bit/gerenciatd.utils.react-utils';
import Image from '../../../components/Image/Image';
import './CommercialInfoCompactOriginalStaticRight.css';

const CommercialInfoCompactOriginalStaticRight = ({ commercialInfo }) => {
  const {
    subTitle,
    secondaryDescription,
    mediaURL,
    layout,
    domId,
  } = commercialInfo;
  return (
    <div id={domId} className="commercialInfoCompactOriginalStaticRight_wrapper commercialInfo_background-color">
      <div className="container">
        <div className="commercialInfoFlexWrapper">
          <div className="commercialInfoFlexCaracs">
            <div className="commercialInfoCompactOriginalStaticRight_text-wrapper">
              <div className="commercialInfoCompactOriginalStaticRight_block-wrapper">
                <div className="commercialInfoCompactOriginalStaticRight_subtitle">
                  {subTitle}
                </div>
              </div>
              <div className="commercialInfoCompactOriginalStaticRight_block-wrapper">
                { secondaryDescription.map((value, index)=>{
                  const key = `commercialInfoCompactOriginalStaticRight_secondary_${layout}_${index}`;
                  return (
                    <div
                      key={key}
                      dangerouslySetInnerHTML={createMarkup(value)}
                      className="commercialInfoCompactOriginalStaticRight_paragraph_small"
                    />
                  )
                })}
              </div>
            </div>
          </div>
          <div className="commercialInfoFlexImg">
            <div className="commercialInfoCompactOriginalStaticRight_block-wrapper">
              <Image wrapperClass="commercialInfoCompactOriginalStaticRight_image-wrapper" alt={subTitle} url={mediaURL} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default CommercialInfoCompactOriginalStaticRight;
