import React ,{ Component } from 'react';
import PropTypes from 'prop-types';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import { pillTypes, pillThemes } from '@bit/gerenciatd.react.pill';
import ImgSlider from '../../../components/ImgSlider/ImgSlider';
import Stamp from '../../../components/Stamp/Stamp';
import Label from '../../../components/Label/Label';
import StampList from '../../../componentList/StampList/StampList';
import StarList from '../../../componentList/StarList/StarList';
import {
  findMainOffer,
  findQueryOffer,
  evaluateStock,
  getMainTerminalImage,
  getImagesForAnimation,
} from '../../../utils/commonUtils';
import './DeviceItemOriginalSimple.css';
import DeviceItemExtraData from '../DeviceItemExtraData/DeviceItemExtraData';

class DeviceItemOriginalSimple extends Component {
  constructor(props) {
    super(...props);
    const { terminal } = props;
    const { offers } = terminal;
    const offer = findMainOffer(offers);
    props.updateOfferInfo(offer);
    this.state = {
      offer,
    }
  }

  tryToUpdateOfferSelected = (newProductSpecs) => {
    const { terminal } = this.props;
    const { offers } = terminal;
    const queryOffer = findQueryOffer(offers, newProductSpecs);
    if (queryOffer) {
      this.setState({ offer: queryOffer });
      this.props.updateOfferInfo(queryOffer);
    }
  }


  render() {
    const { terminal, extraInfoActive, hovered } = this.props;
    const { name, layout, alias } = terminal;
    const { offer } = this.state;
    if (!offer) {
      return false;
    }
    const {
      stamps,
      imgs,
      stock,
      showFragmentedQuota,
      fragmentedQuota,
      fragmentedQuotaMonths,
    } = offer;
    const imgsForAnimation = getImagesForAnimation(imgs, name);
    const configuration = {
      arrows: false,
      autoplay: true,
      autoplaySpeed: 1000,
      fade: true,
    }
    const mainTerminalImage = getMainTerminalImage(imgs);
    const terminalOpacity = evaluateStock(stock);
    let extraClasses = '';
    if (!extraInfoActive) {
      extraClasses = 'deviceItemOriginalSimple-not_extra_border';
    }
    const { storage, currentPrice, originalPrice, clientPrice } = offer;
    const hoveredClass = hovered ? 'hovered' : '';
    let noStockOpacityVeil = 'withStock';
    if (terminalOpacity && terminalOpacity !== 1) {
      noStockOpacityVeil = 'withNoStock';
    }
    const cndRating = terminal.filters && terminal.filters.rating > 0;
    let bestPriceToPay = currentPrice;
    if (clientPrice > 0) {
      bestPriceToPay = clientPrice;
    }


    return (
      <div>
        {terminalOpacity && terminalOpacity !== 1 && (
          <div className="noStockOpacityVeil" />
        )}
        <div className={`deviceItemOriginalSimple ${extraClasses} ${hoveredClass}`}>
          <StampList
            stock={stock}
            stamps={stamps}
          />
          <div className="">
            <RowGrid>
              <ColGrid mobileCols={12}>
                <div className="deviceItem_center">
                  <ImgSlider
                    arrayImgs={imgsForAnimation}
                    customSettings={configuration}
                    autoplay={hovered}
                    wrapperClass="imgSliderWrapper"
                  />
                </div>
              </ColGrid>
            </RowGrid>
          </div>
          <div className="deviceItemOriginalSimple_row deviceItemOriginalSimple_text-wrapper">
            <RowGrid>
              <ColGrid mobileCols={12}>
                <Label text={name} wrapperLabel={`${layout}`} />
              </ColGrid>
            </RowGrid>
          </div>
          <div className="deviceItemOriginalSimple_row deviceItemOriginalSimple_text-wrapper">
            <RowGrid>
              <ColGrid mobileCols={12}>
                {((bestPriceToPay !== originalPrice) && (originalPrice > 0) )&&  (
                  <Label
                    basicTextStyle="label_price_text"
                    text={`${originalPrice} `}
                    wrapperLabel={`${terminal.layout}CrossOut`}
                  />
                )}
                <Label
                  basicTextStyle="label_text"
                  text={`${bestPriceToPay} `}
                  wrapperLabel={`${terminal.layout}Price`}
                />
                {(offer.discountPercentage > 0) && (
                  <Stamp
                    text={`${offer.discountPercentage} %`}
                    typeStamp="stamp_discountPercentage"
                  />
                )}
              </ColGrid>
            </RowGrid>
          </div>
          {(parseFloat (offer.quota) > 0) && (
            <div className="deviceItemOriginalSimple_row deviceItemOriginalSimple_text-wrapper">
              <RowGrid>
                <ColGrid mobileCols={12}>
                  {(showFragmentedQuota) && (
                    <Label text={`${fragmentedQuota} /mes en ${fragmentedQuotaMonths} plazos`} wrapperLabel={`${terminal.layout}Quota`} />
                  )}
                  {(!showFragmentedQuota) && (
                    <Label text={`${offer.quota} /mes en ${offer.quotaMonths} plazos`} wrapperLabel={`${terminal.layout}Quota`} />
                  )}
                </ColGrid>
              </RowGrid>
            </div>
          )}
          {cndRating && (
            <div className="deviceItemOriginalSimple_row deviceItemOriginalSimple_text-wrapper">
              <RowGrid>
                <ColGrid mobileCols={12}>
                  <StarList starVisualization="regular" rating={terminal.filters.rating} numComments={terminal.numComments} />
                </ColGrid>
              </RowGrid>
            </div>
          )}
          { extraInfoActive && (
            <div className={`deviceItemOriginalSimple-desplegable deviceItemExtraData_padding_wrapper ${noStockOpacityVeil}`}>
              <DeviceItemExtraData
                updateOffer={this.tryToUpdateOfferSelected}
                offerSelected={offer}
                type={pillTypes.SOLID}
                terminal={terminal}
                pillThemes={pillThemes.BLUE}
                wrapperLabel="spec_regular"
                storage={storage}
                terminalOpacity={terminalOpacity}
              />
            </div>
          )}
        </div>
      </div>
    )
  }
}

DeviceItemOriginalSimple.propTypes = {
  terminal: PropTypes.object,
  hovered: PropTypes.bool,
  extraInfoActive: PropTypes.bool,
  updateOfferInfo: PropTypes.func.isRequired,
};

DeviceItemOriginalSimple.defaultProps = {
  terminal: {},
  hovered: false,
  extraInfoActive: true,
}

export default DeviceItemOriginalSimple;
