import React from 'react';
import { createMarkup } from '@bit/gerenciatd.utils.react-utils';
import Image from '../../../components/Image/Image';
import './CommercialInfoCompactOriginalStaticLeft.css';

const CommercialInfoCompactOriginalStaticLeft = ({ commercialInfo }) => {
  const {
    subTitle,
    secondaryDescription,
    mediaURL,
    layout,
    domId,
  } = commercialInfo;
  return (
    <div id={domId} className="commercialInfoCompactOriginalStaticLeft_wrapper">
      <div className="container">
        <div className="commercialInfoFlexWrapper-reverse">
          <div className="commercialInfoFlexImg">
            <div className="commercialInfoCompactOriginalStaticLeft_block-wrapper">
              <Image wrapperClass="commercialInfoCompactOriginalStaticLeft_image-wrapper" alt={subTitle} url={mediaURL} />
            </div>
          </div>
          <div className="commercialInfoFlexCaracs">
            <div className="commercialInfoCompactOriginalStaticLeft_text-wrapper">
              <div className="commercialInfoCompactOriginalStaticLeft_block-wrapper">
                <div className="commercialInfoCompactOriginalStaticLeft_subtitle">
                  {subTitle}
                </div>
              </div>
              <div className="commercialInfoCompactOriginalStaticLeft_block-wrapper">
                { secondaryDescription.map((value, index)=>{
                  const key = `commercialInfoCompactOriginalStaticLeft_secondary_${layout}_${index}`;
                  return (
                    <div
                      key={key}
                      dangerouslySetInnerHTML={createMarkup(value)}
                      className="commercialInfoCompactOriginalStaticLeft_paragraph_small"
                    />
                  )
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default CommercialInfoCompactOriginalStaticLeft;
