import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import LinkOverBox from '@bit/gerenciatd.react.link-over-box';
import Label from '../../components/Label/Label';
import Image from '../../components/Image/Image';
import Link from '../../components/Link/Link';
import './SpaceSeacher.css';
import { createDeviceUrl, getCommercialNameOfProductCategory } from '../../utils/commonUtils';
import { applyClickOnTerminals } from '../../utils/dataHandlers/filtersDataHandler';
import { actions as productActions } from '../../reducers/products';

class SpaceSeacher extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  handleClickSearcher = () => {
    const {
      productsSearchered,
      products,
      productCategory,
    } = this.props;
    const terminalSearchedFiltered = applyClickOnTerminals(
      products,
      productsSearchered,
      productCategory,
    );
    this.props.updateProductsSearchedFiltered(terminalSearchedFiltered);
  }


  componentWillUnmount = () => {
    this.props.updateProductsSearchedFiltered([]);
  }

  obtainMinPrice = (clientPrice, currentPrice, originalPrice) => {
    let minPrice = clientPrice;
    if (currentPrice < minPrice || minPrice === 0) {
      minPrice = currentPrice;
    }
    if (originalPrice < minPrice) {
      minPrice = originalPrice;
    }
    return (minPrice);
  }

  render() {
    const {
      productsSearchered,
      productCategory,
      productSegment,
    } = this.props;

    const productCategoryCommercialName = getCommercialNameOfProductCategory(productCategory);
    return (
      <div className="spaceSeacherClass">
        <RowGrid>
          <ColGrid mobileCols={12}>
            <div className="spaceSeacherClassFlex">
              { productsSearchered.map((item) => {     
                const { name, offers, alias } = item;
                const newLink = createDeviceUrl({
                  productCategory,
                  alias,
                  productSegment,
                });
                return (
                  <Fragment>
                    <div className="spaceSeacherFlex">
                      <RowGrid>
                        <ColGrid mobileCols={12}>
                          <LinkOverBox link={newLink} alt={name}>
                            <RowGrid>
                              <ColGrid mobileCols={2}>
                                <Image url={offers[0].imgs[0]} wrapperClass="image-searched" />
                              </ColGrid>
                              <ColGrid mobileCols={10}>
                                <RowGrid>
                                  <ColGrid mobileCols={12}>
                                    <span className="terminalLinkInfo">{name}</span>
                                  </ColGrid>
                                  <ColGrid mobileCols={12}>
                                    <Label text={`Desde ${this.obtainMinPrice(item.offers[0].clientPrice, item.offers[0].currentPrice, item.offers[0].originalPrice) }`} wrapperLabel="priceSeacher" />
                                  </ColGrid>
                                </RowGrid>
                              </ColGrid>
                            </RowGrid>
                          </LinkOverBox>
                        </ColGrid>
                      </RowGrid>
                    </div>
                  </Fragment>
                )
              })}
              <RowGrid>
                <ColGrid>
                  <div>
                    <Link 
                      handleClick={this.handleClickSearcher} 
                      text={`Ver ms ${productCategoryCommercialName}`} 
                      wrapperLabel="SpaceSeacherLink"
                    />
                  </div>
                </ColGrid>
              </RowGrid>
            </div>
          </ColGrid>
        </RowGrid>
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  updateProductsSearchedFiltered: terminalsSearched => dispatch(
    productActions.updateProductsSearchedFiltered(terminalsSearched),
  ),
})

const mapStateToProps = state => ({
  products: state.products.products,
  productsSearchered: state.products.productsSearchered,
  data: state.products.data,
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
})

export default connect(mapStateToProps, mapDispatchToProps)(SpaceSeacher);
