import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import { actions as productActions } from '../../../reducers/products';
import CounterFilter from '../../../components/CounterFilter/CounterFilter';
import { filtersTexts } from '../../../constants/filters';
import Filter from '../../../components/Filters/Filter';
import { ratingFilter } from '../../../utils/dataHandlers/layoutsDataHandler';
import './TerminalListFilters.css';

class TerminalListFilters extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  static getDerivedStateFromProps(props, state) {
    const {
      products,
      productCategory,
      filtersStatus,
      wrapperClass,
      numberOfFiltersApplied,
    } = props;

    return {
      ...state,
      products,
      wrapperClass,
      productCategory,
      filtersStatus,
      numberOfFiltersApplied,
    }
  }

  starClick = (starSelected) => {
    ratingFilter(starSelected, this.props);
  }

  render() {
    const {
      products,
      productCategory,
      filtersStatus,
      wrapperClass,
      numberOfFiltersApplied,
    } = this.state;

    if (!products || !productCategory || !filtersStatus || !filtersStatus[productCategory]) {
      return null;
    }

    const filtersKeys = Object.keys(filtersStatus[productCategory]);

    return (
      <div key={`terminalListFilterWrapper ${wrapperClass}`} className={`terminalListFilterWrapper ${wrapperClass}`}>
        <RowGrid>
          <ColGrid mobileCols={9}>
            <div className="numberfiltersApplied">
              <span>{filtersTexts.FILTER_GLOBAL_TITLE}</span>
              <CounterFilter counter={numberOfFiltersApplied[productCategory]} />
            </div>
          </ColGrid>
          <ColGrid mobileCols={3}>
            <div className="clearFilters">
              <span
                role="button"
                tabIndex="-1"
                onKeyPress={this.props.clearFiltersApplied}
                onClick={this.props.clearFiltersApplied}
              >
                {filtersTexts.FILTER_CLEAN_APPLIED}
              </span>
            </div>
          </ColGrid>
        </RowGrid>
        {filtersKeys.map((filterKey, index) => {
          let filterProps = filtersStatus[productCategory][filterKey];
          if (
            filterKey === 'pillCategory'
            || filterKey === 'color'
            || filterKey === 'soList'
            || filterKey === 'rating'
            || filterKey === 'brand'
            || filterKey === 'storage'
            || filterKey === 'connectivity') {
            filterProps = {}
            filterProps[filterKey] = filtersStatus[productCategory][filterKey];
          }
          if (filterKey === 'rating') {
            filterProps.onClick = this.starClick;
          }
          const id = `${filterKey}_${wrapperClass}_${index.toString()}`;
          return (
            <Filter
              idFilter={id}
              key={`${filterKey}_${wrapperClass}_${index.toString()}`}
              filterProps={{
                type: filterKey,
                ...filterProps,
              }}
              reduxProps={this.props}
            />
          )
        })}
      </div>
    )
  }
}

TerminalListFilters.propTypes = {
  wrapperClass: PropTypes.string,
}

TerminalListFilters.defaultProps = {
  wrapperClass: 'pc',
}

const mapDispatchToProps = dispatch => ({
  updateProductsFiltered: data => dispatch(
    productActions.updateProductsFiltered(data)
  ),
  clearFiltersApplied: () => dispatch(
    productActions.clearFiltersApplied(),
  ),
})

const mapStateToProps = state => ({
  products: state.products.products,
  productsFiltered: state.products.productsFiltered,
  productsError: state.products.productsError,
  productsFetching: state.products.productsFetching,
  filtersStatus: state.products.filtersStatus,
  numberOfFiltersApplied: state.products.numberOfFiltersApplied,
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalListFilters);
