import React from 'react';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import './TerminalFileTechnicalInformationElement.css';

const TerminalFileTechnicalInformationElement = ({ category, categoryDetail }) => {
  return (
    <div className="terminalFileTechnicalInformationElement_wrapper">
      <RowGrid>
        <ColGrid mobileCols={4}>
          <div className="terminalFileTechnicalInformationElement_single-wrapper">
            <div className="terminalFileTechnicalInformationElement_title">
              {category}
            </div>
          </div>
        </ColGrid>
        <ColGrid mobileCols={8}>
          <div className="terminalFileTechnicalInformationElement_single-wrapper">
            { categoryDetail.map((categoryDetailElement, index) => {
              const key = `terminalFileTechnicalInformationElement_${category}_${index}`;
              let classContent = 'terminalFileTechnicalInformationElement_element';
              if (index === 0 ) {
                classContent = `${classContent} terminalFileTechnicalInformationElement_first_element`;
              }
              return (
                <div key={key} className={classContent}>{categoryDetailElement}</div>
              )
            })}
          </div>
        </ColGrid>
      </RowGrid>
    </div>
  )
}
export default TerminalFileTechnicalInformationElement;