export const finishStates = {
  FINISHED_OK: 'FINISHED_OK',
  FINISHED_KO: 'FINISHED_KO',
  FINISHED_KO_TIMEOUT: 'FINISHED_KO_TIMEOUT',
  FINISHED_KO_JS: 'FINISHED_KO_JS',
}

export const sendLoadingService = (processName, myEvent) => {
  try {
    if (IC && IC.trackEvent) {
      IC.trackEvent('Started', myEvent.eventName, myEvent.id, '', '', processName);
    }
  } catch (err) {
    console.log("IC is not defined");
  }
}

export const sendLoadedService = (processName, myEvent, finishState, data) => {
  try {
    let dataToSend = data;
    dataToSend = JSON.stringify(dataToSend);
    if (IC && IC.trackEvent) {
      if (finishState === finishStates.FINISHED_OK) {
        IC.trackEvent('Loaded', myEvent.eventName, myEvent.id, 'ok', dataToSend, processName);
      } else if (finishState === finishStates.FINISHED_KO) {
        IC.trackEvent('Loaded', myEvent.eventName, myEvent.id, 'error', dataToSend, processName);
      } else if (finishState === finishStates.FINISHED_KO_TIMEOUT) {
        IC.trackEvent('Loaded', myEvent.eventName, myEvent.id, 'timeout', dataToSend, processName);
      } else if (finishState === finishStates.FINISHED_KO_JS) {
        IC.trackEvent('Loaded', myEvent.eventName, myEvent.id, 'js-error', dataToSend, processName);
      }
    }
  } catch (err) {
    console.log("IC is not defined");
  }
}

export const sendJSError = (processName, method, data) => {
  try {
    const dataToSend = JSON.stringify(data);
    if (IC && IC.trackErrorCtz) {
      IC.trackErrorCtz(processName, '', method, dataToSend);
    }
  } catch (err) {
    console.log("IC is not defined");
  }
}
