import { pillBarMenu, arrayBreadCrumbs } from '../../constants/setupBasics';
import { PRODUCT_CATEGORY, SEGMENTS, URLS_LIBRARY } from '../../constants/constants';

export const createBreadCrumbsOnTerminalFile = (productCategory, productSegment) => {
  let newArrayBreadCrumbs = [...arrayBreadCrumbs];
  if (productSegment === SEGMENTS.EMPLEADOS) {
    newArrayBreadCrumbs = [{
      id: 0,
      name: 'Listado',
      url: URLS_LIBRARY.URL_ALL_EMPLOYES,
    },
    ]
    return newArrayBreadCrumbs;
  }

  let currentPill = pillBarMenu.find(item => item.value === productCategory);
  if (currentPill) {
    if (productCategory !== PRODUCT_CATEGORY.ALL) {
      newArrayBreadCrumbs = [
        ...newArrayBreadCrumbs,
        {
          id: 2,
          name: `${currentPill.label}`,
          url: `${currentPill.link}`,
        },
      ]
    } else {
      currentPill = pillBarMenu.find(item => item.value === PRODUCT_CATEGORY.SMARTPHONES);
      newArrayBreadCrumbs = [
        ...newArrayBreadCrumbs,
        {
          id: 2,
          name: `${currentPill.label}`,
          url: `${currentPill.link}`,
        },
      ]
    }
  }

  return newArrayBreadCrumbs;
}
