import React from 'react';
import './Menu.css';
import Title from '../../components/Title/Title';
import BreadCrumbs from '../../components/BreadCrumbs/BreadCrumbs';
import { getCommercialNameOfProductCategory } from '../../utils/commonUtils';

/* jsx, proptypes SOY UN COMPONENTE, hazme escalable porfavor recibir el men por props... */
/*  */
const Menu = ({ productCategory, productSegment, arrayBreadCrumbs }) => {
  const productCategoryCommercialName = getCommercialNameOfProductCategory(productCategory, productSegment);
  return (
    <div className="container menuWrapper">
      <div className="menu_title">
        <Title text={productCategoryCommercialName} wrapperClass="title-h1" />
      </div>
      <div className="menu_breadcrumbs">
        <BreadCrumbs arrayBreadCrumbs={arrayBreadCrumbs} />
      </div>
    </div>
  )
}

export default Menu;
