import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import queryString from 'query-string';
import { renderHelmetDevice, createMicroDataForDevice } from '../metadata';
import { actions as commonsActions } from '../../reducers/commons';
import { actions as csActions } from '../../reducers/crossSelling';
import { actions as productsActions } from '../../reducers/products'
import TerminalFileNavigation from '../../containers/TerminalFile/TerminalFileNavigation/TerminalFileNavigation';
import TerminalFileMainSpec from '../../containers/TerminalFile/TerminalFileMainSpec/TerminalFileMainSpec';
import TerminalFileAdvantages from '../../containers/TerminalFile/TerminalFileAdvantages/TerminalFileAdvantages';
import TerminalFileCommercialInfo from '../../containers/TerminalFile/TerminalFileCommercialInfo/TerminalFileCommercialInfo';
import TerminalFileCaracs from '../../containers/TerminalFile/TerminalFileCaracs/TerminalFileCaracs';
import TerminalFileTechnicalInformation from '../../containers/TerminalFile/TerminalFileTechnicalInformation/TerminalFileTechnicalInformation';
import TerminalFileCrossSelling from '../../containers/TerminalFile/TerminalFileCrossSelling/TerminalFileCrossSelling';
import TerminalFileReviews from '../../containers/TerminalFile/TerminalFileReviews/TerminalFileReviews';
import TerminalFileStickyBar from '../../containers/TerminalFile/TerminalFileStickyBar/TerminalFileStickyBar';
import { getOptionsForStickyBar } from '../../utils/dataHandlers/anchorDataHandler';
import { createBreadCrumbsOnTerminalFile } from '../../utils/dataHandlers/breadCrumbsDataHandler';
import { findQueryOffer } from '../../utils/commonUtils';
import './TerminalFile.scss';

class TerminalFile extends Component {
  constructor(props) {
    super(...props);
    // NOTE: Change these lines when alias changes
    let { alias } = props.match.params;
    alias = `ntt_${alias}`;
    props.setCurrentAlias(alias);
    this.state = {
      productAlias: alias,
      fileProduct: null,
    }
  }

  static getDerivedStateFromProps(props, state) {
    let { productAlias, fileProduct } = state;
    const {
      productCategory,
      productSegment,
      productsDetailedFetching,
      productsDetailed,
      crossSellingList,
    } = props;


    let myCrossSellingList= null;
    let { alias } = props.match.params;
    alias = `ntt_${alias}`;
    if (productAlias !== alias) {
      productAlias = alias;
      fileProduct = null;
    }


    const { search } = props.location;
    const params = queryString.parse(search);

    let data = null
    const cndBaseInfo = productCategory && productSegment && productAlias;
    // if we have from store the proper info, we create input structure for fetching
    if (cndBaseInfo) {
      data = {
        productInfo: {
          alias: productAlias,
          segment: productSegment,
          type: productCategory,
        },
      }
    }

    if (crossSellingList && crossSellingList[productAlias]) {
      myCrossSellingList = crossSellingList[productAlias];
      // props.getCrossSelling(data);
    }
    // if we have data for fetching and we dont have info, we must request it
    if (data && !productsDetailed[productAlias] && !productsDetailedFetching) {
      props.getProductByAlias(data);
      props.getCrossSelling(data);
    } else if (productsDetailed[productAlias] && !fileProduct) {
      const queryOffer = findQueryOffer(productsDetailed[productAlias].offers, params);
      props.updateOfferSelected(queryOffer);
      return {
        ...state,
        fileProduct: productsDetailed[productAlias],
        productSpecs: productsDetailed[productAlias].productSpecs,
        myCrossSellingList,
      }
    }

    return {
      ...state,
      fileProduct,
      productAlias,
      myCrossSellingList,
      productSpecs: params,
      productCategory,
      productSegment,
    };
  }

  componentDidMount() {
    // this.props.setGridVisible(true);
  }

  shouldComponentUpdate(nextProps) {
    const {
      productAlias,
    } = this.state;

    if (
      nextProps
      && nextProps.productsDetailed
      && nextProps.productsDetailed[productAlias]
      && nextProps.productsDetailed[productAlias].offers && nextProps.productsDetailed[productAlias].offers.length > 0) {
      const {
        productOffering,
        productSpecs,
        codIon,
      } = nextProps.productsDetailed[productAlias].offers[0];
      if (JSON.stringify(this.props.productsDetailed) !== JSON.stringify(nextProps.productsDetailed)) {
        // Datalayer
        if (
          productOffering === null
          || productOffering === ''
          || productSpecs.color === null
          || productSpecs.color === ''
          || productSpecs.storage === null
          || productSpecs.storage === ''
        ) {
          if (dataLayerGTM) {
            dataLayerGTM.push({
              event: 'asyncGTM',
              GAcategory: 'Seguimiento de errores',
              GAaction: `codION_${codIon}_PO_${productOffering}_CAPACIDAD_${productSpecs.storage}_COLOR_${productSpecs.color}`,
              GAlabel: 'Errores en dispositivo o terminal sin informacin Telco',
            });
          }
        }
      }
    }
    return true;
  }

  render() {
    const { getProductByAlias } = this.props;
    const {
      productSpecs,
      productCategory,
      productSegment,
      myCrossSellingList,
      fileProduct,
    } = this.state;

    if (!fileProduct) {
      return null;
    }
    /* Creating breadcrumb for Terminal File */
    const breadCrumbs = createBreadCrumbsOnTerminalFile(productCategory, productSegment);
    const options = getOptionsForStickyBar(fileProduct, myCrossSellingList);
    
    return (
      <Fragment>
        {renderHelmetDevice(fileProduct)}
        <div className="terminalFilePage">
          <TerminalFileNavigation terminal={fileProduct} arrayBreadCrumbs={breadCrumbs} segment={productSegment} />
          <TerminalFileMainSpec terminal={fileProduct} productSpecs={productSpecs} />
          <TerminalFileAdvantages />
          <TerminalFileStickyBar terminal={fileProduct} options={options} />
          <TerminalFileCaracs terminal={fileProduct} />
          <TerminalFileCommercialInfo terminal={fileProduct} />
          <TerminalFileTechnicalInformation terminal={fileProduct} />
          <TerminalFileCrossSelling
            productSegment={productSegment}
            productCategory={productCategory}
            getProductByAlias={getProductByAlias}
            terminal={fileProduct}
            list={myCrossSellingList}
          />
          <TerminalFileReviews terminal={fileProduct} />
        </div>
        {createMicroDataForDevice(fileProduct, productCategory)}
      </Fragment>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  getCrossSelling: params => dispatch(csActions.getCS(params)),
  getProductByAlias: params => dispatch(productsActions.getProductByAlias(params)),
  setCurrentAlias: alias => dispatch(productsActions.setCurrentAlias(alias)),
  setGridVisible: visible => dispatch(commonsActions.setGridVisible(visible)),
  updateOfferSelected: offerSelected => dispatch(
    productsActions.updateOfferSelected(offerSelected)
  ),
})

const mapStateToProps = state => ({
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
  productsDetailed: state.products.productsDetailed,
  productsDetailedFetching: state.products.productsDetailedFetching,
  offerSelected: state.products.offerSelected,
  crossSellingList: state.crossSelling.list,
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalFile);
