import React from 'react';
import Slider from 'react-slick';
import Icon from '@bit/gerenciatd.react.icon';
import Pill, { pillTypes, pillThemes } from '@bit/gerenciatd.react.pill';
import IconLibrary from '@bit/gerenciatd.react.icon-library';
import { taggingButtonTypes } from '@bit/gerenciatd.utils.tagging-utils';
import { pillBarMenu } from '../../constants/setupBasics';
import './PillsBar.css';

const PillsBar = ({ productCategory }) => {
  const settings = {
    dots: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 2,
    arrows: true,
    centerMode: false,
    variableWidth: true,
    infinite: false,
    draggable: false,
    prevArrow: <Icon classIcon={IconLibrary.cheuronizq} wrapperClass="pillsBar_wrapper-prev" />,
    nextArrow: <Icon classIcon={IconLibrary.cheurondcha} wrapperClass="pillsBar_wrapper-next" />,
    responsive: [
      {
        breakpoint: 767,
        settings: {
          draggable: true,
          centerMode: false,
        },
      },
    ],
  };
  return (
    <div className="container">
      <Slider {...settings}>
        {
          pillBarMenu.map((pillItem) => {
            const linkAux = pillItem.link ? `${(pillItem.link).toLowerCase()}` : null;
            return (
              <Pill
                taggingType={taggingButtonTypes.BUTTON}
                theme={pillThemes.BLUEGREY}
                type={pillTypes.SOFT}
                dopped
                key={pillItem.id}
                title={pillItem.name}
                id={pillItem.id}
                link={linkAux}
                label={pillItem.label}
                active={productCategory === pillItem.value}
                wrapperClass="pillBarComponent"
              />
            )
          })
        }
      </Slider>
    </div>
  )
}

export default PillsBar;
