/* It gives us new name and kind of data */
export const normalizeUrlPropertyName = (propertyKey) => {
  switch (propertyKey) {
    // slider filters
    case 'precio':
    case 'price':
      return {
        name: 'priceSliderStatus',
        kind: 'interval',
      };
    case 'bateria':
      return {
        name: 'batterySliderStatus',
        kind: 'interval',
      }
    case 'camara':
    case 'camera':
      return {
        name: 'backsideCameraSliderStatus',
        kind: 'interval',
      }
    case 'pantalla':
    case 'pulgadas':
      return {
        name: 'screenSliderStatus',
        kind: 'interval',
      }
    // pills filters
    case 'rating':
    case 'valoracion':
      return {
        name: 'rating',
        kind: 'stars',
      }
    case 'category':
    case 'categoria':
      return {
        name: 'pillCategory',
        kind: 'pills',
      }
    case 'marca':
      return {
        name: 'brand',
        kind: 'pills',
      }
    case 'color':
      return {
        name: 'color',
        kind: 'pills',
      }
    case 'capacidad':
    case 'storage':
      return {
        name: 'storage',
        kind: 'pills',
      }
    case 'so':
    case 'soList':
      return {
        name: 'soList',
        kind: 'pills',
      }
    default: return null;
  }
}

/* About intervals ... */
export const normalizeUrlInteval = (propertyValue, name, allFiltersUpdated) => {
  let res = '';
  /* Taking interval limits from below */
  const { limits } = allFiltersUpdated[name];
  const tokkens = propertyValue.split('-');
  const numberOfTokkens = tokkens ? tokkens.length : 0;
  if (numberOfTokkens === 1) {
    res = {
      min: 0,
      max: parseInt(propertyValue, 10),
      limits,
    };
  } else if (numberOfTokkens === 2) {
    tokkens.sort((a, b) => parseInt(a, 10) - parseInt(b, 10));
    res = {
      min: parseInt(tokkens[0], 10),
      max: parseInt(tokkens[1], 10),
      limits,
    }
  }
  return res;
}
export const normalizeUrlPills = (propertyValue, name, allFiltersUpdated) => {
  // trocear string
  let urlCategories = propertyValue.toLowerCase();
  urlCategories = urlCategories.split('-');
  const categories = allFiltersUpdated[name].map((categoryItem) => {
    const newItem = {
      ...categoryItem,
    }
    if (urlCategories.includes(`${categoryItem.name}`)) {
      newItem.active = true;
    }
    return newItem;
  });
  return categories;
}
export const normalizeUrlStars = (propertyValue, name, allFiltersUpdated) => {
  const urlNumberOfStars = parseInt(propertyValue, 10);
  const currentRating = allFiltersUpdated[name];
  const newStarsRating = setRestOfStars(currentRating, urlNumberOfStars);
  return newStarsRating;
}
/* name could be usefull for future single handling */
export const normalizeUrlPropertyValue = (name, kind, propertyValue, allFiltersUpdated) => {
  switch (kind) {
    case 'interval':
      return normalizeUrlInteval(propertyValue, name, allFiltersUpdated)
    case 'pills':
      return normalizeUrlPills(propertyValue, name, allFiltersUpdated);
    case 'stars':
      return normalizeUrlStars(propertyValue, name, allFiltersUpdated);
    default: return null;
  }
}

export const extractFiltersFromUrl = (allFiltersUpdated) => {
  const url = window.location.href;
  const res = queryString.parseUrl(url);
  const { query } = res;
  const queryKeys = query ? Object.keys(query) : [];
  const filters = {};
  queryKeys.forEach((queryKey) => {
    const newKeyObj = normalizeUrlPropertyName(queryKey);
    if (newKeyObj) {
      const { name, kind } = newKeyObj;
      const newValue = normalizeUrlPropertyValue(name, kind, query[queryKey], allFiltersUpdated);
      filters[name] = newValue;
    }
    return null;
  })
  return filters;
}


