import React from 'react';
import CommercialInfoCompleteOriginalStatic from '../TerminalFileCommercialInfoLayouts/CommercialInfoCompleteOriginalStatic';
import CommercialInfoCompactOriginalStaticLeft from '../TerminalFileCommercialInfoLayouts/CommercialInfoCompactOriginalStaticLeft';
import CommercialInfoCompactOriginalStaticRight from '../TerminalFileCommercialInfoLayouts/CommercialInfoCompactOriginalStaticRight';
import CommercialInfoCompleteOriginalDynamic from '../TerminalFileCommercialInfoLayouts/CommercialInfoCompleteOriginalDynamic';
import CommercialInfoPromoCompactOriginalStaticLeft from '../TerminalFileCommercialInfoLayouts/ComercialInfoPromoCompactOriginalStaticLeft';
import ComercialInfoPromoCompactOriginalStaticRight from '../TerminalFileCommercialInfoLayouts/ComercialInfoPromoCompactOriginalStaticRight';
import { commerSectionsLayouts } from '../../../constants/terminalFileTexts';
import { deleteAccentsAndChars } from '../../../utils/commonUtils';
import './TerminalFileCommercialInfo.css';

const TerminalFileCommercialInfo = ({ terminal }) => {
  const { commercialInfo } = terminal;
  return (
    <div className="comercial_info">
      { commercialInfo.map((singleCommercialInfo, index) => {
        let { layout } = singleCommercialInfo;
        layout = deleteAccentsAndChars(layout);
        const key = `section_key_${layout}_${index}`;
        switch (layout) {
          case commerSectionsLayouts.COMPLETE_ORIGINAL_STATIC: {
            return (
              <div key={key} className="terminalFileCommercialInfo_section-wrapper">
                <CommercialInfoCompleteOriginalStatic
                  commercialInfo={singleCommercialInfo}
                />
              </div>
            )
          }
          case commerSectionsLayouts.COMPLETE_ORIGINAL_DYNAMIC: {
            return (
              <div key={key} className="terminalFileCommercialInfo_section-wrapper">
                <CommercialInfoCompleteOriginalDynamic
                  commercialInfo={singleCommercialInfo}
                />
              </div>
            )
          }
          case commerSectionsLayouts.COMPACT_ORIGINAL_STATIC_RIGHT: {
            return (
              <div key={key} className="terminalFileCommercialInfo_section-wrapper">
                <CommercialInfoCompactOriginalStaticRight
                  commercialInfo={singleCommercialInfo}
                />
              </div>
            )
          }
          case commerSectionsLayouts.COMPACT_ORIGINAL_STATIC_LEFT: {
            return (
              <div key={key} className="terminalFileCommercialInfo_section-wrapper">
                <CommercialInfoCompactOriginalStaticLeft
                  commercialInfo={singleCommercialInfo}
                />
              </div>
            )
          }
          case commerSectionsLayouts.PROMO_COMPACT_ORIGINAL_STATIC_LEFT: {
            return (
              <div key={key} className="terminalFileCommercialInfo_section-wrapper">
                <CommercialInfoPromoCompactOriginalStaticLeft
                  commercialInfo={singleCommercialInfo}
                />
              </div>
            )
          }
          case commerSectionsLayouts.PROMO_COMPACT_ORIGINAL_STATIC_RIGHT: {
            return (
              <div key={key} className="terminalFileCommercialInfo_section-wrapper">
                <ComercialInfoPromoCompactOriginalStaticRight
                  commercialInfo={singleCommercialInfo}
                />
              </div>
            )
          }
          default: {
            return (
              <div key={key} className="terminalFileCommercialInfo_invalid_section-wrapper">
                <div className={`not_valid_template ${layout}`} />
              </div>
            )
          }
        }
      })}
    </div>
  )
}
export default TerminalFileCommercialInfo;
