import { FRACTIONALQUOTAMONTHS, FRACTIONALLIMITPRICES } from '../../constants/constants';

export const normalizeNumber = (dirtyNumber) => {
  let numberToNormalize = dirtyNumber.toString().replace(',', '.');
  const numberToNormalizeSplitted = numberToNormalize.split('.');
  let leftSide = '';
  if (numberToNormalizeSplitted[0]) {
    leftSide = parseInt(numberToNormalizeSplitted[0], 10).toString();
  }
  let rightSide = '';
  if (numberToNormalizeSplitted[1]) {
    rightSide = parseInt(numberToNormalizeSplitted[1], 10).toString();
  }
  numberToNormalize = `${leftSide}`;
  if (rightSide) {
    numberToNormalize = `${numberToNormalize}.${rightSide}`
  }
  return parseFloat(numberToNormalize);
}

export const normalizeSingleStamp = (response) => {
  let normalizedItem = {};
  if (response) {
    const {
      kind,
      text,
    } = response
    if (kind && text) {
      normalizedItem = {
        kind,
        text,
      }
    } else {
      normalizedItem = null;
    }
  }
  return normalizedItem;
}

export const normalizeAllStamps = (response) => {
  let responseNormalized = [];
  let normalizedStamp = {};
  if (response) {
    response.forEach((item) => {
      normalizedStamp = normalizeSingleStamp(item);
      if (normalizedStamp) {
        responseNormalized = [
          ...responseNormalized,
          normalizedStamp,
        ]
      }
    })
  }
  return responseNormalized;
}

export const normalizeProductSpec = (response) => {
  let normalizedItem = {};
  if (response) {
    const {
      color,
    } = response;
    const storage = normalizeNumber(response.storage);

    normalizedItem = {
      color,
      storage,
    }
  }
  return normalizedItem;
}

export const normalizeAllImgs = (response) => {
  let responseNormalized = [];
  let normalizedImage = {};

  if (response) {
    response.forEach((item) => {
      normalizedImage = item;
      if (window.location.host.indexOf('movistar.es') === -1
        && window.location.host.indexOf('col10mgmt.tsm.inet') === -1
      ) {
        normalizedImage = `https://www.movistar.es/${normalizedImage}`
      }
      responseNormalized = [
        ...responseNormalized,
        normalizedImage,
      ]
    })
  }
  return responseNormalized;
}


export const normalizeSingleOffer = (response) => {
  let normalizedItem = {};
  let normalizedImgs = [];
  let normalizedProductSpecs = {};
  let normalizedStamps = {};
  if (response) {
    const {
      clientPrice,
      codIon,
      colorHex,
      colorName,
      currentPrice,
      deliveryHome,
      deliveryNoReserve,
      deliveryShop,
      main,
      discount,
      discountPercentage,
      imgs,
      productOffering,
      productSpecs,
      quotaMonths,
      stamps,
      stock,
    } = response;
    let {
      quota,
    } = response;
    const fragmentedQuotaMonths = FRACTIONALQUOTAMONTHS;
    let showFragmentedQuota = false;
    let fragmentedQuota = 0
    if ((clientPrice > 0) && (clientPrice <= FRACTIONALLIMITPRICES)) {
      fragmentedQuota = parseFloat(clientPrice / fragmentedQuotaMonths).toFixed(2).replace('.', ',');
      showFragmentedQuota = true;
    }
    if (quota > 0) {
      quota = quota.toFixed(2).replace('.', ',');
    }
    let { originalPrice } = response;
    const storage = normalizeNumber(response.storage);
    normalizedImgs = normalizeAllImgs(imgs);
    normalizedProductSpecs = normalizeProductSpec(productSpecs);
    normalizedStamps = normalizeAllStamps(stamps);

    if (!originalPrice && originalPrice <= 0) {
      originalPrice = currentPrice;
    }

    normalizedItem = {
      clientPrice,
      codIon,
      colorHex,
      colorName,
      currentPrice,
      deliveryHome,
      deliveryNoReserve,
      deliveryShop,
      main,
      discount,
      discountPercentage,
      imgs: normalizedImgs,
      originalPrice,
      productOffering,
      productSpecs: normalizedProductSpecs,
      quota,
      quotaMonths,
      stamps: normalizedStamps,
      stock,
      storage,
      showFragmentedQuota,
      fragmentedQuota,
      fragmentedQuotaMonths,
    }
  }
  return normalizedItem;
}

export const normalizeAllOffers = (response) => {
  let responseNormalized = [];
  let normalizedItemOffer = {};

  if (response) {
    response.forEach((item) => {
      normalizedItemOffer = normalizeSingleOffer(item);
      responseNormalized = [
        ...responseNormalized,
        normalizedItemOffer,
      ]
    })
  }
  return responseNormalized;
}


export const filterTypeCategories = (typeName) => {
  if (
    typeName === 'mvil'
    || typeName === 'mviles'
    || typeName === 'movil'
    || typeName === 'tablet'
    || typeName === 'equipamiento'
    || typeName === 'equipamiento'
    || typeName === 'tv'
    || typeName === 'app'
    || typeName === 'particulares'
    || typeName === 'smartphone'
    || typeName === 'smartwatch'
    || typeName === 'smartwatches'
    || typeName === 'equipamiento wifi'
    || typeName === 'pack') {
    return false;
  }
  return true;
}
export const otherFixOnCategories = (categoryName) => {
  // seminuevo --> seminuevos
  let itemCategoryAux = categoryName.toLowerCase();
  itemCategoryAux = itemCategoryAux === 'seminuevo' ? 'seminuevos' : itemCategoryAux;


  return itemCategoryAux;
}

export const normalizeBothCategories = (category, offerCategory) => {
  const res = [];
  // looping category
  category.forEach((itemCategory) => {
    let itemCategoryAux = itemCategory.toLowerCase();
    itemCategoryAux = otherFixOnCategories(itemCategoryAux);
    const cnd = filterTypeCategories(itemCategoryAux);
    if (!res.includes(itemCategoryAux) && cnd) {
      res.push(itemCategoryAux);
    }
  })
  // looping offerCategory
  offerCategory.forEach((itemCategory) => {
    let itemCategoryAux = itemCategory.toLowerCase();
    itemCategoryAux = otherFixOnCategories(itemCategoryAux);
    const cnd = filterTypeCategories(itemCategoryAux);
    if (!res.includes(itemCategoryAux) && cnd) {
      res.push(itemCategoryAux);
    }
  })
  return res;
}


export const normalizeFilters = (response) => {
  let normalizedItem = {};
  if (response) {
    const {
      battery,
      brand,
      category,
      offerCategory,
      connectivity,
      color,
      colorHex,
      minPrice,
      order,
      os,
      storage,
    } = response
    const totalCategories = normalizeBothCategories(category, offerCategory);
    let rating = response.averageRating;
    if (!rating > 0) {
      rating = 0;
    }
    let camera = normalizeNumber(response.mainCamera);
    camera = camera === -1 ? null : camera;
    let screen = normalizeNumber(response.screen);
    screen = screen === -1 ? null : screen;
    let newBattery = normalizeNumber(battery);
    newBattery = newBattery === -1 ? null : newBattery;
    // const storage = response.storage && response.storage.map(item => normalizeNumber(item));
    normalizedItem = {
      battery: newBattery,
      brand,
      camera,
      category: totalCategories,
      connectivity,
      color,
      colorHex,
      minPrice,
      order,
      os,
      rating,
      screen,
      storage,
    }
  }
  return normalizedItem;
}

export const normalizeSingleTerminal = (response, index, productCategory = '') => {
  let normalizedItem = null;
  let filterNormalized = {};
  let offersNormalized = [];
  if (response) {
    let { layout } = response;
    const {
      alias,
      name,
      filter,
      numComments,
      offers,
    } = response;

    if (!layout) {
      layout = 'normal';
    }
    filterNormalized = normalizeFilters(filter);
    offersNormalized = normalizeAllOffers(offers);
    if (offersNormalized.length > 0) {
      normalizedItem = {
        productCategory,
        alias,
        name,
        filters: filterNormalized,
        numComments,
        layout,
        offers: offersNormalized,
        relevanceId: index,
      }
    }
  }
  return normalizedItem;
}

export const normalizeKeys = (key) => {
  switch (key) {
    case 'mobile': {
      return 'smartphone';
    }
    case 'smartphone': {
      return 'smartphone';
    }
    case 'tablet': {
      return 'tablet';
    }
    case 'tv': {
      return 'tv';
    }
    case 'equipment': {
      return 'equipment';
    }
    case 'equipement': {
      return 'equipment';
    }
    case 'pack': {
      return 'pack';
    }
    case 'smartwatch': {
      return 'smartwatch';
    }
    case 'preowned': {
      return 'preowned';
    }
    default: {
      return '';
    }
  }
}

export const normalizerAllTerminals = (response) => {
  let responseNormalized = [];
  let normalizedItem = {};
  const globalResponseNormalized = {};
  Object.keys(response).forEach((key) => {
    if (response[key]) {
      normalizedItem = {};
      responseNormalized = [];
      response[key].forEach((item, index) => {
        normalizedItem = normalizeSingleTerminal(item, index, key);
        if (normalizedItem) {
          responseNormalized = [
            ...responseNormalized,
            normalizedItem,
          ]
        }
      })
      if (responseNormalized) {
        const normalizedKey = normalizeKeys(key);
        globalResponseNormalized[normalizedKey] = responseNormalized;
      }
    }
  })
  return globalResponseNormalized;
}
