import React, { Fragment, Component } from 'react';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import { findMainOffer, prepareStorageAndColor } from '../../../utils/commonUtils';
import ColorSelectorList from '../../../componentList/ColorSelectorList/ColorSelectorList';
import CapacityList from '../../../componentList/CapacityList/CapacityList';
import SpecsList from '../../../componentList/SpecsList/SpecsList';
import './DeviceItemExtraData.css';

class DeviceItemExtraData extends Component {
  constructor(props) {
    super(...props);
    const { offers } = props;
    //const offer = findMainOffer(offers);
    this.state = {}
  }

  changeColor = (colorHex) => {
    const { offerSelected } = this.props;
    const { storage } = offerSelected;
    const { updateOffer } = this.props;
    const newProductSpecs = {
      storage,
      colorHex,
    }
    updateOffer(newProductSpecs);
  }

  changeStorage = (storage) => {
    const { offerSelected } = this.props;
    const { colorHex } = offerSelected;
    const { updateOffer } = this.props;
    const newProductSpecs = {
      storage,
      colorHex,
    }
    updateOffer(newProductSpecs);
  }

  render() {
    const {
      terminal,
      wrapperLabel,
      storage,
      offerSelected,
    } = this.props;
    const { offers, filters } = terminal;
    if (!offerSelected) {
      return null
    }
    const storageAndColorList = prepareStorageAndColor(offers);
    const storageList = Object.keys(storageAndColorList);
    const colorList = storageAndColorList[storage];
    const { colorHex } = offerSelected;
    const alias = terminal.alias.replace('ntt_', '');
    const idTagCapacity = `PA-ficha-movil-selector-capacity${alias}`;
    const idTagColor = `PA-ficha-movil-selector-color${alias}`;
    return (
      <Fragment>
        { storageList && (
          <RowGrid>
            <ColGrid mobileCols={12}>
              <CapacityList
                idTag={idTagCapacity}
                storageList={storageList}
                storage={storage}
                onClick={this.changeStorage}
              />
            </ColGrid>
          </RowGrid>
        )}
        { colorList && (
          <RowGrid>
            <ColGrid mobileCols={12}>
              <ColorSelectorList
                idTag={idTagColor}
                colorList={colorList}
                colorHexSelected={colorHex}
                onclick={this.changeColor}
              />
            </ColGrid>
          </RowGrid>
        )}
        <RowGrid>
          <ColGrid mobileCols={12}>
            <SpecsList filters={filters} wrapperLabel={wrapperLabel} />
          </ColGrid>
        </RowGrid>
      </Fragment>
    )
  }

}

export default DeviceItemExtraData;
