export const types = {
  GET_BANNER_HOME: 'GET_BANNER_HOME',
  GET_BANNER_HOME_OK: 'GET_BANNER_HOME_OK',
  GET_BANNER_HOME_KO: 'GET_BANNER_HOME_KO',
}

export const initialState = {
  bannerHome: null,
  fetchingBannerHome: false,
  bannerHomeError: false,
}

const Customizer = (state = initialState, action) => {
  switch (action.type) {
    case types.GET_BANNER_HOME: {
      return {
        ...state,
        crossFetching: true,
        crossError: false,
      }
    }
    default: return { ...state }
  }
}

export const actions = {
  getBannerHome: data => ({ type: types.GET_BANNER_HOME, data }),
  getBannerHomeOK: (data, alias) => ({ type: types.GET_BANNER_HOME_OK, payload: { data, alias } }),
  getBannerHomeKO: () => ({ type: types.GET_BANNER_HOME_KO }),
}

export default Customizer;
