import React from 'react';
import Pill, { paddingSizes } from '@bit/gerenciatd.react.pill';
import { taggingButtonTypes } from '@bit/gerenciatd.utils.tagging-utils';
import InputSlider from '../InputSlider/InputSlider';
import StarList from '../../componentList/StarList/StarList';
import MainStructure from './MainStructure';
import { filtersTexts } from '../../constants/filters';
import { applyFilterMaster } from '../../utils/dataHandlers/filtersDataHandler';
import { handleClickOnCategoryPill } from '../../utils/dataHandlers/layoutsDataHandler';


const Filter = ({ idFilter, filterProps, reduxProps }) => {
  // filter props
  const { type } = filterProps;
  // redux props
  const {
    products,
    productCategory,
    filtersStatus,
    updateProductsFiltered,
  } = reduxProps;

  /* for sliders */
  const onChangeArgs = {
    idSlider: '',
    products,
    productCategory,
    filtersStatus,
    updateProductsFiltered,
  }

  switch (type) {
    case 'priceSliderStatus': {
      onChangeArgs.idSlider = 'priceSliderStatus';
      return (
        <MainStructure title={filtersTexts.PRICE.FILTER_TITLE}>
          <InputSlider
            inputData={{
              idTagging: 'PA-listado-terminales-filtros-precio',
              ...filterProps,
            }}
            onChange={applyFilterMaster}
            onChangeArgs={{
              ...onChangeArgs,
            }}
          />
        </MainStructure>
      )
    }
    case 'batterySliderStatus': {
      onChangeArgs.idSlider = 'batterySliderStatus';
      return (
        <MainStructure title={filtersTexts.BATTERY.FILTER_TITLE}>
          <InputSlider
            inputData={{
              idTagging: 'PA-listado-terminales-filtros-bateria',
              ...filterProps,
            }}
            onChange={applyFilterMaster}
            onChangeArgs={{
              ...onChangeArgs,
            }}
          />
        </MainStructure>
      )
    }
    case 'screenSliderStatus': {
      onChangeArgs.idSlider = 'screenSliderStatus';
      return (
        <MainStructure title={filtersTexts.SCREEN.FILTER_TITLE}>
          <InputSlider
            inputData={{
              idTagging: 'PA-listado-terminales-filtros-pantalla',
              ...filterProps,
            }}
            onChange={applyFilterMaster}
            onChangeArgs={{
              ...onChangeArgs,
            }}
          />
        </MainStructure>
      )
    }
    case 'backsideCameraSliderStatus': {
      onChangeArgs.idSlider = 'backsideCameraSliderStatus';
      return (
        <MainStructure title={filtersTexts.CAMERA.FILTER_TITLE}>
          <InputSlider
            inputData={{
              idTagging: 'PA-listado-terminales-filtros-camara-trasera',
              ...filterProps,
            }}
            onChange={applyFilterMaster}
            onChangeArgs={{
              ...onChangeArgs,
            }}
          />
        </MainStructure>
      )
    }
    case 'pillCategory': {
      const { pillCategory } = filterProps;
      if (pillCategory && pillCategory.length > 0) {
        return (
          <MainStructure title={filtersTexts.CATEGORY.FILTER_TITLE}>
            {pillCategory.map(pillItem => (
              <Pill
                taggingType={taggingButtonTypes.BUTTON}
                key={`${pillItem.id}${Math.random() * 100}}`}
                paddingSize={paddingSizes.REDUCED}
                id={pillItem.id}
                label={pillItem.label}
                active={pillItem.active}
                onClick={() => handleClickOnCategoryPill(pillItem, reduxProps)}
                canBeUnselected
              />
            ))}
          </MainStructure>
        )
      }
      return null;
    }
    case 'color': {
      const { color } = filterProps;
      if (color && color.length > 0) {
        return (
          <MainStructure title={filtersTexts.COLOR.FILTER_TITLE}>
            {color.map(colorItem => (
              <Pill
                key={`${productCategory}${Math.random() * 100}`}
                paddingSize={paddingSizes.REDUCED}
                id={colorItem.id}
                active={colorItem.active}
                label={colorItem.label}
                onClick={() => applyFilterMaster(
                  'color',
                  colorItem,
                  products,
                  productCategory,
                  filtersStatus,
                  updateProductsFiltered,
                )}
                canBeUnselected
              />
            ))}
          </MainStructure>
        )
      }
      return null;
    }
    case 'soList': {
      const { soList } = filterProps;
      if (soList && soList.length > 0) {
        return (
          <MainStructure title={filtersTexts.SO.FILTER_TITLE}>
            {soList.map(pillItem => (
              <Pill
                key={`${pillItem.id}${Math.random() * 100}}`}
                paddingSize={paddingSizes.REDUCED}
                id={pillItem.id}
                label={pillItem.label}
                active={pillItem.active}
                onClick={() => applyFilterMaster(
                  'soList',
                  pillItem,
                  products,
                  productCategory,
                  filtersStatus,
                  updateProductsFiltered,
                )}
                canBeUnselected
              />
            ))}
          </MainStructure>
        )
      }
      return null;
    }
    case 'rating': {
      const { rating, onClick } = filterProps;
      const selectedStar = rating.filter(star => star.active === true);
      const numberOfRating = selectedStar.length;
      return (
        <MainStructure title={filtersTexts.RATING.FILTER_TITLE}>
          <StarList
            id="rating-filter"
            starVisualization="regular"
            rating={numberOfRating}
            numComments={0}
            starClickEventFunction={onClick}
          />
        </MainStructure>
      )
    }
    case 'brand': {
      const { brand } = filterProps;
      if (brand && brand.length > 0) {
        return (
          <MainStructure title={filtersTexts.BRAND.FILTER_TITLE}>
            {brand.map((brandItemLabel, index) => {
              return (
                <Pill
                  key={`${brandItemLabel.id}${Math.random() * 100}}`}
                  paddingSize={paddingSizes.REDUCED}
                  wrapperClass={`${index === 0 ? 'pillFilterComponent' : ''}`}
                  id={brandItemLabel.id}
                  label={brandItemLabel.label}
                  active={brandItemLabel.active}
                  onClick={() => applyFilterMaster(
                    'brand',
                    brandItemLabel,
                    products,
                    productCategory,
                    filtersStatus,
                    updateProductsFiltered,
                  )}
                  canBeUnselected
                />
              )
            })
            }
          </MainStructure>
        )
      }
      return null;
    }
    case 'storage': {
      const { storage } = filterProps;
      if (storage && storage.length > 0) {
        return (
          <MainStructure title={filtersTexts.CAPACITY.FILTER_TITLE}>
            {storage.map((storageItemLabel) => {
              const amount = parseInt(storageItemLabel.label, 10);
              const labelAux = (amount >= 1024) ? `${parseInt((amount / 1024), 10)} TB` : `${amount} GB`;
              return (
                <Pill
                  key={`${productCategory}${Math.random() * 100}`}
                  label={labelAux}
                  paddingSize={paddingSizes.REDUCED}
                  active={storageItemLabel.active}
                  onClick={() => applyFilterMaster(
                    'storage',
                    storageItemLabel,
                    products,
                    productCategory,
                    filtersStatus,
                    updateProductsFiltered,
                  )}
                  canBeUnselected
                />
              )
            })}
          </MainStructure>
        )
      }
      return null;
    }
    case 'connectivity': {
      const { connectivity } = filterProps;
      if (connectivity && connectivity.length > 0) {
        return (
          <MainStructure title={filtersTexts.CONNECTIVITY.FILTER_TITLE}>
            {connectivity.map((item) => {
              return (
                <Pill
                  key={`${item.id}${Math.random() * 100}`}
                  label={item.label}
                  paddingSize={paddingSizes.REDUCED}
                  active={item.active}
                  onClick={() => applyFilterMaster(
                    'connectivity',
                    item,
                    products,
                    productCategory,
                    filtersStatus,
                    updateProductsFiltered,
                  )}
                  canBeUnselected
                />
              )
            })}
          </MainStructure>
        )
      }
      return null;
    }
    default: return null;
  }
}

export default Filter;
