import React from 'react';
import PropTypes from 'prop-types';
import './Stamp.css';

/**
* Stateless component to draw an icon.
* @param {string} text path of the image.
* @returns {JSX} Component image JSX.
*/

const Stamp = ({ text, alignText, typeStamp }) => {
  const textUpped = text.toUpperCase();
  return (
    <span
      className={`stamp_text stamp_${alignText} ${typeStamp}`}>
      {textUpped}
    </span>
  )
}

Stamp.propTypes = {
  text: PropTypes.string,
  alignText: PropTypes.string,
  typeStamp: PropTypes.string,
}


Stamp.defaultProps = {
  text: 'Stamp',
  alignText: 'left',
  typeStamp: 'stam_new',
}

export default Stamp;
