import React from 'react';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import { advantajesModule } from '../../../constants/terminalFileTexts';
import './TerminalFileAdvantages.css';

const TerminalFileAdvantages = ({ props }) => {
  const { elements, h2 } = advantajesModule;
  return (
    <div className="terminalFileAdvantages_wrapper">
      <div className="container">
        <div className="terminalFileAdvantages_title-wrapper">
          <h2>{h2}</h2>
        </div>
        <div className="terminalFileAdvantages_advantajes-list-wrapper">
          <div className="terminalFileAdvantages_advantajes-flex-list-wrapper">
            { elements.map((value, index) => {
              const key = `terminal_advantage_item_${index}`;
              return (
                <div
                  key={key}
                  className="terminalFileAdvantages_advantajes-flex-element-wrapper">
                  <div className="terminalFileAdvantages_element-wrapper">
                    <div className={`icon ${value.icon}`} />
                    <div className="terminalFileAdvantages_element-text">{value.text}</div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    </div>
  )
}
export default TerminalFileAdvantages;
