import React, { Component } from 'react';
import { connect } from 'react-redux';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import { actions as productActions } from '../../../reducers/products';
import './TerminalFileMainSpec.css';
import TerminalFileImages from '../TerminalFileImages/TerminalFileImages';
import TerminalFileMainInfo from '../TerminalFileMainInfo/TerminalFileMainInfo';
import { prepareStorageAndColor } from '../../../utils/commonUtils';

class TerminalFileMainSpec extends Component {

  constructor(props) {
    super(...props);
    this.state = {

    }
  }

  render() {
    const {
      terminal,
      productSpecs,
      offerSelected,
    } = this.props;

    const {
      offers,
      name,
      views,
    } = terminal;
    if (!offerSelected) {
      return (<p>NO existe oferta para este modelo</p>)
    }
    const {
      stamps,
      imgs,
      stock,
      videos,
    } = offerSelected;

    const storageAndColorList = prepareStorageAndColor(offers);
    return (
      <div className="container terminalFileMainSpec_wrapper-padding padding0">
        <RowGrid>
          <ColGrid desktopCols={6} mobileCols={12}>
            <div className="termimalFileMainSpec_wrapper-module">
              <TerminalFileImages
                imgs={imgs}
                stamps={stamps}
                stock={stock}
                name={name}
                videos={videos}
              />
            </div>
          </ColGrid>
          <ColGrid desktopCols={6} mobileCols={12}>
            <div className="termimalFileMainSpec_wrapper-module">
              <TerminalFileMainInfo
                offerSelected={offerSelected}
                terminal={terminal}
                views={views}
                storageAndColorList={storageAndColorList}
                productSpecs={productSpecs}
              />
            </div>
          </ColGrid>
        </RowGrid>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  offerSelected: state.products.offerSelected,
})

const mapDispatchToProps = dispatch => ({
  updateOfferSelected: offerSelected => dispatch(productActions.updateOfferSelected(offerSelected)),
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalFileMainSpec);
