import React, { Fragment, Component } from 'react';
import { connect } from 'react-redux';
import { actions as productActions } from '../../reducers/products';
import BannerRegularGeneric from './BannerLayouts/BannerRegularGeneric';
import BannerRegularGenericWide from './BannerLayouts/BannerRegularGenericWide';
import BannerRegularApple from './BannerLayouts/BannerRegularApple';
import { BANNER_LAYOUTS } from '../../constants/constants';
import './Banner.css';

class Banner extends Component {
  constructor(props) {
    super(...props);
    props.getTerminalListBanner();
    this.state = {}
  }

  render() {
    const { productCategory, terminalListBanner } = this.props;
    if (!productCategory) {
      return false;
    }
    if (!terminalListBanner) {
      return false;
    }
    const bannerInfo = terminalListBanner[productCategory];
    const {
      backgroundImage,
      backgroundImageMobile,
      backgroundColor,
      bannerLayout,
      bannerTextContentAlign,
      terminalImage,
      terminalAlt,
      stampText,
      stampAlign,
      stampStyle,
      stampSecondaryText,
      stampSecondaryAlign,
      stampSecondaryStyle,
      bannerTitle,
      bannerDescription,
      priceHeaderText,
      mainPrice,
      originalPrice,
      buttonText,
      buttonType,
      buttonTheme,
      buttonDescription,
      link,
      conditions,
    } = bannerInfo;

    return (
      <Fragment>
        {(bannerLayout === BANNER_LAYOUTS.REGULAR_GENERIC) && (
          <BannerRegularGeneric
            backgroundImage={backgroundImage}
            backgroundImageMobile={backgroundImageMobile}
            backgroundColor={backgroundColor}
            bannerTextContentAlign={bannerTextContentAlign}
            terminalImage={terminalImage}
            terminalAlt={terminalAlt}
            stampText={stampText}
            stampAlign={stampAlign}
            stampStyle={stampStyle}
            stampSecondaryText={stampSecondaryText}
            stampSecondaryAlign={stampSecondaryAlign}
            stampSecondaryStyle={stampSecondaryStyle}
            bannerTitle={bannerTitle}
            bannerDescription={bannerDescription}
            priceHeaderText={priceHeaderText}
            mainPrice={mainPrice}
            originalPrice={originalPrice}
            buttonText={buttonText}
            buttonType={buttonType}
            buttonTheme={buttonTheme}
            buttonDescription={buttonDescription}
            link={link}
            conditions={conditions}
          />
        )}
        {(bannerLayout === BANNER_LAYOUTS.REGULAR_APPLE) && (
          <BannerRegularApple
            backgroundImage={backgroundImage}
            backgroundImageMobile={backgroundImageMobile}
            backgroundColor={backgroundColor}
            link={link}
            buttonDescription={buttonDescription}
          />
        )}
        {(bannerLayout === BANNER_LAYOUTS.REGULAR_GENERIC_WIDE) && (
          <BannerRegularGenericWide
            backgroundImage={backgroundImage}
            backgroundImageMobile={backgroundImageMobile}
            backgroundColor={backgroundColor}
            bannerTextContentAlign={bannerTextContentAlign}
            terminalImage={terminalImage}
            terminalAlt={terminalAlt}
            stampText={stampText}
            stampAlign={stampAlign}
            stampStyle={stampStyle}
            stampSecondaryText={stampSecondaryText}
            stampSecondaryAlign={stampSecondaryAlign}
            stampSecondaryStyle={stampSecondaryStyle}
            bannerTitle={bannerTitle}
            bannerDescription={bannerDescription}
            priceHeaderText={priceHeaderText}
            mainPrice={mainPrice}
            originalPrice={originalPrice}
            buttonText={buttonText}
            buttonType={buttonType}
            buttonTheme={buttonTheme}
            link={link}
            conditions={conditions}
          />
        )}
      </Fragment>
    )
  }
}


const mapDispatchToProps = dispatch => ({
  updateProductCategory: category => dispatch(productActions.updateProductCategory(category)),
  getTerminalListBanner: () => dispatch(productActions.getTerminalListBanner()),
})

const mapStateToProps = state => ({
  productCategory: state.products.productCategory,
  terminalListBanner: state.products.terminalListBanner,
})

export default connect(mapStateToProps, mapDispatchToProps)(Banner);
