/* eslint-disable import/order */
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PillsBar from '../../PillsBar/PillsBar';
import { actions as productActions } from '../../../reducers/products';
import { applySearcherOnTerminals, applyClickOnTerminals } from '../../../utils/dataHandlers/filtersDataHandler';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import Pill, { pillTypes, pillThemes } from '@bit/gerenciatd.react.pill';
import FlyOut, { flyOutActions } from '@bit/gerenciatd.react.fly-out';
import Button, { buttonThemes, buttonTypes } from '@bit/gerenciatd.react.button';
import iconLibrary from '@bit/gerenciatd.react.icon-library';
import SpaceSeacher from '../../SpaceSeacher/SpaceSeacher';
import TerminalListFilters from '../TerminalListFilters/TerminalListFilters';
import TerminalListGrid from '../TerminalListGrid/TerminalListGrid';
import Select from '@bit/gerenciatd.react.select';
import './TerminalListCore.css';
import Label from '../../../components/Label/Label';
import store from '../../../app/store';
import { getCommercialNameOfProductCategory } from '../../../utils/commonUtils';
import { SEGMENTS, GRID_SYSTEM } from '../../../constants/constants';
import { sortMenuOptions } from '../../../constants/filters';

class TerminalListCore extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  handleClickSearcher = () => {
    const {
      productsSearchered,
      products,
      productCategory,
    } = this.props;

    const terminalSearchedFiltered = applyClickOnTerminals(
      products,
      productsSearchered,
      productCategory,
    );
    if (terminalSearchedFiltered) {
      this.props.updateProductsSearchedFiltered({ terminalSearchedFiltered });
    }
  }

  showFilters = () => {
    document.body.classList.add('modal-open');
    this.props.setFlyOutVisibility('idFlyoutFinancing');
  }

  closeFilters = () => {
    this.props.setFlyOutVisibility(null);
    document.body.classList.remove('modal-open');
  }


  handleSearcher = (data) => {
    this.state.data = data;
    const { products, productCategory } = this.props;
    const productsSearchered = applySearcherOnTerminals(products, productCategory, data);
    if (productsSearchered) {
      const productSeacher = {
        productsSearchered,
        data,
      }
      this.props.updateProductsSearched({ productSeacher });
    }
  }

  handleSelectChange = (item) => {
    const { currentTarget } = item;
    const { value } = currentTarget;
    this.props.changeSorType(value);
  }

  render() {
    const {
      /* data props */
      productsFiltered,
      productsSearchered,
      productCategory,
      sortType,
      productSegment,
      productDetailed,
      productsFetching,
      elementsToShow,
      /* functions for childrens */
      getProductByAlias,
      updateElementsToShow,
    } = this.props;
    const productCategoryCommercialName = getCommercialNameOfProductCategory(productCategory, productSegment);
    let showFilters = true;
    let grid = GRID_SYSTEM[SEGMENTS.PARTICULARES]
    if (productSegment === SEGMENTS.EMPLEADOS) {
      showFilters = false;
      grid = GRID_SYSTEM[SEGMENTS.EMPLEADOS];
    }
    return (
      <Fragment>
        <div className="topSearcher container">
          {grid.pillsBar && (
            <RowGrid>
              <ColGrid mobileCols={12}>
                <div className="container">
                  <div className="terminalListBarWrapper">
                    <PillsBar productCategory={productCategory} />
                  </div>
                </div>
              </ColGrid>
            </RowGrid>
          )}
          {(grid.sortMenu || grid.searcher) && (
            <RowGrid>
              <ColGrid desktopCols={6} mobileCols={12}>
                {grid.sortMenu && (
                  <div />
                  /* <Searcher
                    id="PA-listado-terminales-buscador"
                    taggingType={taggingButtonTypes.BUTTON}
                    placeholder={`Buscar ${productCategoryCommercialName.toLowerCase()}`}
                    handleOnChange={this.handleSearcher}
                    handleOnSubmit={this.handleClickSearcher}
                    wrapperClass="hidden-xs"
                  /> */
                )}
              </ColGrid>
              <ColGrid desktopCols={6} mobileCols={12}>
                {grid.sortMenu && (
                  <div className="terminalListCore_counter-wrapper">
                    <Select
                      title=""
                      name="sortType"
                      options={sortMenuOptions}
                      onChange={this.handleSelectChange}
                      defaultValue={sortType}
                    />
                    <div className="counterContainer">
                      <Label
                        text={`${productsFiltered ? productsFiltered.length : 0}`}
                        wrapperLabel="_spec_regular_number"
                      />
                      <Label
                        text={`${productCategoryCommercialName.toLowerCase()}`}
                        wrapperLabel="_spec_regular"
                      />
                    </div>
                  </div>
                )}
              </ColGrid>
            </RowGrid>
          )}
        </div>
        <div className="terminalListCoreWrapper">
          <div className="container">
            {grid.searcher && (
              <div className="container">
                <RowGrid>
                  <ColGrid mobileCols={12}>
                    <Fragment>
                      {(productsSearchered && productsSearchered.length !== 0) && (
                        <SpaceSeacher />
                      )}
                    </Fragment>
                  </ColGrid>
                </RowGrid>
              </div>
            )}
            <div className="filterContentWrapper">
              <RowGrid>
                { showFilters && (
                  <ColGrid
                    desktopCols={grid.filter.desktopCols}
                    mobileCols={grid.filter.mobileCols}
                  >
                    <div className="filterPill">
                      <Pill
                        type={pillTypes.SOLID}
                        theme={pillThemes.BLUE}
                        label="Filtrar"
                        classIcon={iconLibrary.filterandroid}
                        onClick={this.showFilters}
                        onClose={this.closeFilters}
                      />
                    </div>
                    <TerminalListFilters wrapperClass="pc" />
                  </ColGrid>
                )}
                <ColGrid desktopCols={grid.list.desktopCols} mobileCols={grid.list.mobileCols}>
                  <TerminalListGrid
                    productsFiltered={productsFiltered}
                    productCategory={productCategory}
                    productSegment={productSegment}
                    productDetailed={productDetailed}
                    productsFetching={productsFetching}
                    elementsToShow={elementsToShow}
                    getProductByAlias={getProductByAlias}
                    updateElementsToShow={updateElementsToShow}
                  />
                </ColGrid>
              </RowGrid>
            </div>
          </div>
        </div>
        {showFilters && (
          <FlyOut
            idFlyOut="idFlyoutFinancing"
            title={productCategoryCommercialName}
            store={store}
            onClose={this.closeFilters}
          >
            <TerminalListFilters wrapperClass="mobile" />
            <Button
              id="PA-listado-terminales-boton-aplica-flyout"
              managementValue="aplicar-filtros-flyout"
              type={buttonTypes.SOLID}
              theme={buttonThemes.BLUE}
              label="Aplicar"
              onClick={this.closeFilters}
            />
          </FlyOut>
        )}
      </Fragment>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  updateProductsSearched: terminalsSearched => dispatch(
    productActions.updateProductsSearched(terminalsSearched)
  ),
  updateProductsSearchedFiltered: terminalsSearched => dispatch(
    productActions.updateProductsSearchedFiltered(terminalsSearched)
  ),
  setFlyOutVisibility: idFlyOut => dispatch(
    flyOutActions.setFlyOutVisibility(idFlyOut)
  ),
  changeSorType: sortType => dispatch(
    productActions.changeSorType(sortType),
  ),
  getProductByAlias: params => dispatch(productActions.getProductByAlias(params)),
  updateElementsToShow: elementsToShow => dispatch(
    productActions.updateElementsToShow(elementsToShow),
  ),
})

const mapStateToProps = state => ({
  products: state.products.products,
  elementsToShow: state.products.elementsToShow,
  productsFiltered: state.products.productsFiltered,
  productsSearchered: state.products.productsSearchered,
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
  sortType: state.products.sortType,
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalListCore);
