import React from 'react';
import PropTypes from 'prop-types';
import cssVars from 'css-vars-ponyfill';
import customProps from '@bit/gerenciatd.react.custom-properties'
import './Title.css';

cssVars({
  // Options ...
  onlyLegacy: false,
  variables: customProps,
});

/**
* Stateless component to draw an icon.
* @param {string} text path of the image.
* @returns {JSX} Component image JSX.
*/

const Title = ({ text, wrapperClass }) => {
  return (
    <h1 className={wrapperClass}>{text}</h1>
  )
}

Title.propTypes = {
  text: PropTypes.string,
  wrapperClass: PropTypes.string,
}


Title.defaultProps = {
  text: 'Ttulo',
  wrapperClass: '',
}

export default Title;
