export const renderHelmetDevice = (device) => {
  const { name } = device;

  return (
    <Helmet>
      <title>{name}</title>
    </Helmet>
  );
}

export const createMicrodataForList = (productCategory, productsList) => {
  const data = {};
  data['@context'] = 'http://schema.org';
  data['@type'] = 'ItemList';
  // ojo a esto
  data.url = `https://movistar.es${getUrlByProductCategory(productCategory)}`;
  data.numberOfItems = `${productsList.length}`;
  data.itemListElement = productsList.map((item, index) => {
    const { name, alias, offers } = item;
    const mainOffer = findMainOffer(offers);
    const { imgs } = mainOffer;
    const picture = imgs[0];
    const res = {}
    res.name = name;
    res.image = picture;
    res['@type'] = 'ListItem';
    res.url = `https://movistar.es${getUrlByProductCategory(productCategory)}${createDeviceUrl({
      alias,
    })}`
    res.position = (index + 1);
    return res;
  })
  return (
    <Helmet>
      <script className="structured-data-list" type="application/ld+json">
        {JSON.stringify(data)}
      </script>
    </Helmet>
  )
}

export const createMicroDataForDevice = (device, productCategory) => {
  // NOTE: Change these lines when alias changes
  let { alias } = device;
  alias = alias.replace('ntt_', '');

  const data = {}
  data['@context'] = 'http://schema.org';
  data['@type'] = 'Product';
  data.description = device.name;
  data.name = device.name;
  data.sku = alias;
  data.mpn = alias;
  const { offers } = device;
  const mainOffer = findMainOffer(offers);
  const { imgs } = mainOffer;
  const picture = imgs[0];
  data.image = picture;
  data.brand = {};
  data.brand['@type'] = 'Thing';
  data.brand.name = (device && device.metadata && device.metadata.brand) ? device.metadata.brand : '';
  // trustivity
  const { rating, comments } = device;
  const numComments = comments && comments.length ? comments.length : 0;
  data.aggregateRating = {};
  data.aggregateRating['@type'] = 'AggregateRating';
  data.aggregateRating.reviewCount = numComments;
  if (rating) {
    data.aggregateRating.ratingValue = rating;
  }
  // reviews
  if (numComments) {
    data.reviews = comments.map((review) => {
      const itemData = {};
      itemData['@type'] = 'Review';
      const { comment, date, name } = review;   
      itemData.description = comment ? `${comment}` : '';
      itemData.author = name;
      itemData.datePublished = date;
      itemData.reviewRating = {}
      itemData.reviewRating['@type'] = 'Rating';
      itemData.reviewRating.bestRating = review.rating;
      itemData.reviewRating.ratingValue = rating;
      itemData.reviewRating.worstRating = review.rating;
      return itemData;
    })
  }
  // offers
  data.offers = offers.map((offer) => {
    const { storage, colorHex } = offer;
    const newOffer = {};
    newOffer['@type'] = 'Offer';
    newOffer.availability = 'http://schema.org/InStock';
    newOffer.price = 909;
    // fecha validez offerta
    newOffer.priceValidUntil = '';
    newOffer.url = `https://movistar.es${getUrlByProductCategory(productCategory)}${createDeviceUrl({
      alias,
      colorHex: colorHex.replace('#', ''),
      storage,
    })}`
    newOffer.priceCurrency = 'EUR';
    return newOffer;
  });

  return (
    <Helmet>
      <script type="application/ld+json">
        {JSON.stringify(data)}
      </script>
    </Helmet>
  )
}
