/* eslint-disable no-lone-blocks */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import LinkOverBox from '@bit/gerenciatd.react.link-over-box';
import { createDeviceUrl } from 'utils/commonUtils';
import { PRELOAD_ITEM_ON_HOVER } from 'constants/setupBasics';
import DeviceItemOriginalSimple from './DeviceItemLayouts/DeviceItemOriginalSimple';
import DeviceItemOutstandingMainSimple from './DeviceItemLayouts/DeviceItemOutstandingMainSimple';
import DeviceItemOutstandingSecondarySimple from './DeviceItemLayouts/DeviceItemOutstandingSecondarySimple';
import './DeviceItem.css';

const deviceItemLayouts = {
  ORIGINAL_SIMPLE: 'normal',
  OUTSTANDING_MAIN_SIMPLE: 'destacado_principal',
  OUTSTANDING_SECONDARY_SIMPLE: 'destacado_secundario',
}
class DeviceItem extends Component {
  constructor(props) {
    super(...props);
    const { terminal, productSegment } = props;

    const { alias, productCategory } = terminal;
    const newLink = createDeviceUrl({
      productCategory,
      alias,
      productSegment,
    });

    this.state = {
      hovered: false,
      link: newLink,
    }
  }

  enterHover = () => {
    const { extraInfoActive } = this.props;
    let pcOrMobile = 0;
    const pcMobileElement = document.getElementById('pcOrMobile');
    if (pcMobileElement) {
      pcOrMobile = pcMobileElement.offsetWidth;
    }

    if (PRELOAD_ITEM_ON_HOVER && pcOrMobile) {
      const { productSegment, productCategory, terminal } = this.props;
      const data = {
        productInfo: {
          productCategory,
          alias: terminal.alias,
          segment: productSegment,
          type: productCategory,
        },
      }
      this.props.getProductByAlias(data);
    }

    if (pcOrMobile && extraInfoActive) {
      this.setState({
        hovered: true,
      })
    }
  }

  outHover = () => {
    this.setState({
      hovered: false,
    })
  }

  updateOfferInfo = (offer) => {
    const { terminal, productSegment } = this.props;
    const { alias, productCategory } = terminal;
    const { storage, colorHex } = offer;
    // xiaomi_redmi_7?storage=128&colorHex=FFFF00

    const newLink = createDeviceUrl({
      alias,
      productCategory,
      colorHex: colorHex.replace('#', ''),
      storage,
      productSegment,
    });
    this.setState({ link: newLink });
  }


  render() {
    const { terminal } = this.props;
    const { hovered, link } = this.state;
    const { extraInfoActive } = this.props;
    const { layout } = terminal;
  
    switch (layout) {
      case deviceItemLayouts.OUTSTANDING_MAIN_SIMPLE: {
        return (
          <div className="deviceItem" onMouseEnter={this.enterHover} onMouseLeave={this.outHover}>
            <LinkOverBox link={link} alt={terminal.name}>
              <span className="terminalLinkInfo">{terminal.name}</span>
            </LinkOverBox>
            <DeviceItemOutstandingMainSimple
              terminal={terminal}
              hovered={hovered}
              extraInfoActive={extraInfoActive}
              updateOfferInfo={this.updateOfferInfo}
            />
          </div>
        )
      }
      case deviceItemLayouts.OUTSTANDING_SECONDARY_SIMPLE: {
        return (
          <div className="deviceItem" onMouseEnter={this.enterHover} onMouseLeave={this.outHover}>
            <LinkOverBox link={link} alt={terminal.name}>
              <span className="terminalLinkInfo">{terminal.name}</span>
            </LinkOverBox>
            <DeviceItemOutstandingSecondarySimple
              terminal={terminal}
              hovered={hovered}
              extraInfoActive={extraInfoActive}
              updateOfferInfo={this.updateOfferInfo}
            />
          </div>
        )
      }
      // deviceItemLayouts.ORIGINAL_SIMPLE case
      default: {
        return (
          <div className="deviceItem" onMouseEnter={this.enterHover} onMouseLeave={this.outHover}>
            <LinkOverBox link={link} alt={terminal.name}>
              <span className="terminalLinkInfo">{terminal.name}</span>
            </LinkOverBox>
            <DeviceItemOriginalSimple
              terminal={terminal}
              hovered={hovered}
              extraInfoActive={extraInfoActive}
              updateOfferInfo={this.updateOfferInfo}
            />
          </div>
        )
      }
    }
  }
}

DeviceItem.propTypes = {
  extraInfoActive: PropTypes.bool,
}

DeviceItem.defaultProps = {
  extraInfoActive: true,
}


export default DeviceItem;
