import React, { Fragment } from 'react';
import AnchorSmooth from '@bit/gerenciatd.react.anchor-smooth';
import RowGrid from '@bit/gerenciatd.react.row-grid';
import ColGrid from '@bit/gerenciatd.react.col-grid';
import BreadCrumbs from '../../../components/BreadCrumbs/BreadCrumbs';
import Title from '../../../components/Title/Title';
import StarList from '../../../componentList/StarList/StarList';

import './TerminalFileNavigation.css';

const TerminalFileNavigation = ({ terminal, arrayBreadCrumbs }) => {
  const {
    name,
    rating,
    numComments,
    alias,
  } = terminal;


  let starsVisible = true;
  if (!rating > 0) {
    starsVisible = false;
  }

  const urlAlias = alias.replace('ntt_', '');
  const newArrayBreadCrumbs = [
    ...arrayBreadCrumbs,
    {
      id: arrayBreadCrumbs.length,
      name,
      url: urlAlias,
    },
  ]


  const idToScroll = 'opiniones';

  const stickyOffset = 0;


  return (
    <div className="container">
      <div className="terminalFileNavigation_wrapper-padding">
        <RowGrid>
          <ColGrid desktopCols={6} mobileCols={12}>
            <BreadCrumbs arrayBreadCrumbs={newArrayBreadCrumbs} />
          </ColGrid>
          <ColGrid desktopCols={6} mobileCols={12}>
            <div className="terminalFileNavigation_wrapper-module">
              <Title text={name} wrapperClass="title-h1" />
            </div>
          </ColGrid>
        </RowGrid>
        <RowGrid>
          <ColGrid desktopCols={6} mobileCols={12}>
            <Fragment />
          </ColGrid>
          <ColGrid desktopCols={6} mobileCols={12}>
            { starsVisible && (
              <AnchorSmooth
                wrapperClass="stickyBar_AnchorSmoothList-font"
                idToScroll={idToScroll}
                offset={stickyOffset}
              >
                <div className="terminalFileNavigation_wrapper-module">
                  <StarList starVisualization="regular" rating={rating} numComments={numComments} />
                </div>
              </AnchorSmooth>
            )}
          </ColGrid>
        </RowGrid>
      </div>
    </div>
  )
}
export default TerminalFileNavigation;
