import { cloneStructure, findMainOffer } from '../utils/commonUtils';
// import { allFilters } from '../constants/filters';
import {
  applyFilters2Products,
  sortResultsFiltered,
  compareFiltersStructure,
} from '../utils/dataHandlers/filtersDataHandler';
import { sortTypes } from '../constants/filters';

export const types = {
  /* FLOAT MENU */
  GET_PRODUCTS: 'PRODUCTS/GET_PRODUCTS',
  GET_PRODUCTS_ERROR: 'PRODUCTS/GET_PRODUCTS_ERROR',
  GET_PRODUCTS_GENERIC: 'PRODUCTS/GET_PRODUCTS_GENERIC',
  GET_PRODUCTS_GENERIC_SUCCESS: 'PRODUCTS/GET_PRODUCTS_GENERIC_SUCCESS',
  GET_PRODUCTS_GENERIC_FAILURE: 'PRODUCTS/GET_PRODUCTS_GENERIC_FAILURE',
  UPDATE_PRODUCTS_FILTERED: 'PRODUCTS/UPDATE_PRODUCTS_FILTERED',
  CREATE_PRODUCT_FILTER_STATUS: 'PRODUCTS/CREATE_PRODUCT_FILTER_STATUS',
  CLEAR_FILTERS_APPLIED: 'PRODUCTS/CLEAR_FILTERS_APPLIED',
  UPDATE_PRODUCTS_SEARCHED: 'PRODUCTS/UPDATE_PRODUCTS_SEARCHED',
  UPDATE_PRODUCT_SEARCHED_FILTERED: 'PRODUCTS/UPDATE_PRODUCT_SEARCHED_FILTERED',
  UPDATE_PRODUCT_SEGMENT: 'PRODUCT/UPDATE_PRODUCT_SEGMENT',
  UPDATE_PRODUCT_CATEGORY: 'PRODUCT/UPDATE_PRODUCT_CATEGORY',
  UPDATE_ELEMENTS_TO_SHOW: 'PRODUCT/UPDATE_ELEMENTS_TO_SHOW',
  CHANGE_SORT_TYPE: 'PRODUCT/CHANGE_SORT_TYPE',
  SET_CURRENT_ALIAS: 'PRODUCT/SET_CURRENT_ALIAS',

  /* DATA FOR FILE */
  GET_PRODUCT_BY_ALIAS: 'PRODUCTS/GET_PRODUCT_BY_ALIAS',
  GET_PRODUCT_BY_ALIAS_ERROR: 'PRODUCTS/GET_PRODUCT_BY_ALIAS_ERROR',
  GET_PRODUCT_BY_ALIAS_SUCCESS: 'PRODUCTS/GET_PRODUCT_BY_ALIAS_SUCCESS',
  UPDATE_OFFER_SELECTED: 'PRODUCT/UPDATE_OFFER_SELECTED',
  UPDATE_ISCLIENT: 'PRODUCT/UPDATE_ISCLIENT',

  /* DATA FOR BANNER */
  GET_TERMINAL_LIST_BANNER: 'PRODUCTS/GET__TERMINAL_LIST_BANNER',
  GET_TERMINAL_LIST_BANNER_ERROR: 'PRODUCTS/GET_TERMINAL_LIST_BANNER_ERROR',
  GET_TERMINAL_LIST_BANNER_SUCCESS: 'PRODUCTS/GET_TERMINAL_LIST_BANNER_SUCCESS',

  /* JSON LEGAL CONDITIONS */
  GET_LEGAL_CONDITION: 'PRODUCTS/GET_LEGAL_CONDITION',
  GET_LEGAL_CONDITION_SUCCESS: 'PRODUCTS/GET_LEGAL_CONDITION_SUCCESS',
  GET_LEGAL_CONDITION_ERROR: 'PRODUCTS/GET_LEGAL_CONDITION_ERROR',

}

export const initialState = {
  /* LISTADO */
  products: {
    smartphone: null,
    tv: null,
    smartwatch: null,
    equipment: null,
    pack: null,
    tablet: null,
  },
  productsFetching: false,
  productsError: false,
  /* FILTROS */
  productsFiltered: null,
  productsSearchered: null,
  data: null,
  sortType: sortTypes.RELEVANCE,
  filtersStatus: null,
  // we need those to compare and know what filters have been used
  filterStatusAtbeginning: null,
  numberOfFiltersApplied: {},
  /* DETALLE */
  productsDetailed: {},
  productsDetailedError: false,
  productsDetailedFetching: false,
  /* TAGEO */
  currentAlias: '',
  productCategory: null,
  productSegment: null,
  offerSelected: null,
  isClient: null,
  elementsToShow: null,
  /* BANNER LISTADO */
  terminalLisBannerError: false,
  terminalLisBannerFetching: false,
  terminalListBanner: null,
}

const ProductsReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.GET_PRODUCTS_ERROR:
      return {
        ...state,
        productsError: true,
        productsFetching: false,
      }
    case types.GET_PRODUCTS_GENERIC: {
      return {
        ...state,
        productsFetching: true,
      }
    }
    case types.GET_PRODUCTS_GENERIC_SUCCESS: {
      const { data, type, filter } = action.payload;
      const newProducts = { ...state.products };
      newProducts[type] = data;
      /* WARNING WE HAVE TO APPLY FILTERS */
      let productsFiltered = data;
      if (filter) {
        const filterToUse = {}
        filterToUse[type] = filter;
        const { sortType } = state;
        productsFiltered = applyFilters2Products(newProducts, type, filterToUse);
        productsFiltered = sortResultsFiltered(productsFiltered, sortType);
      }

      return {
        ...state,
        products: newProducts,
        productCategory: type,
        productsFiltered,
        currentAlias: null,
      }
    }
    case types.UPDATE_PRODUCTS_SEARCHED: {
      const { productSeacher } = action.payload;
      const { data } = productSeacher;
      let { productsSearchered } = productSeacher;
      productsSearchered = cloneStructure(productsSearchered);

      return {
        ...state,
        productsSearchered,
        data,
      }
    }
    case types.UPDATE_PRODUCT_SEARCHED_FILTERED: {
      const { productsFiltered } = action.payload;
      return {
        ...state,
        productsFiltered,
        productsSearchered: null,
      }
    }
    case types.UPDATE_PRODUCTS_FILTERED: {
      const data = action.payload;
      const { params } = data;
      const { sortType } = state;
      const { products, productCategory, newFilterToApply } = params;
      /* Applying this filter */
      let productsFiltered = applyFilters2Products(products, productCategory, newFilterToApply);
      productsFiltered = sortResultsFiltered(productsFiltered, sortType);
      const newFilters = {
        ...state.filtersStatus,
        ...newFilterToApply,
      }
      const numberOfFiltersApplied = compareFiltersStructure(
        state.filterStatusAtbeginning,
        newFilters,
        productCategory,
      );
      const newNumberOfFiltersApplied = {
        ...state.numberOfFiltersApplied,
        ...numberOfFiltersApplied,
      }
      // new products structure
      const newProductsStructure = [...productsFiltered];
      return {
        ...state,
        productsFiltered: newProductsStructure,
        filtersStatus: newFilters,
        numberOfFiltersApplied: newNumberOfFiltersApplied,
      }
    }
    case types.CREATE_PRODUCT_FILTER_STATUS: {
      const { productFilterStatus, kind, w2modified } = action.payload;
      const newFilterStatus = {}
      newFilterStatus[kind] = productFilterStatus;

      // generating new state depends on w2modified
      const { beggining, current } = w2modified
      const newState = {
        ...state,
      }
      if (beggining) {
        newState.filterStatusAtbeginning = {
          ...state.filterStatusAtbeginning,
          ...newFilterStatus,
        };
      }
      if (current) {
        newState.filtersStatus = {
          ...state.filtersStatus,
          ...newFilterStatus,
        };
        const numberOfFiltersApplied = compareFiltersStructure(
          state.filterStatusAtbeginning,
          newFilterStatus,
          kind,
        );
        newState.numberOfFiltersApplied = { ...numberOfFiltersApplied }
      }
      return {
        ...newState,
      }
    }
    case types.CLEAR_FILTERS_APPLIED: {
      const { filterStatusAtbeginning, numberOfFiltersApplied, productCategory } = state;
      // tomo la configuracin basca
      const structureForReplace = filterStatusAtbeginning[productCategory];
      const newFiltersStatus = {
        ...state.filtersStatus,
      }
      // la reemplazo
      newFiltersStatus[productCategory] = structureForReplace;
      // reseteo la cuenta de filtros aplicados
      const newNumberOfFiltersApplied = {
        ...numberOfFiltersApplied,
      }
      newNumberOfFiltersApplied[productCategory] = 0;
      return {
        ...state,
        productsFiltered: [...state.products[productCategory]],
        filtersStatus: { ...newFiltersStatus },
        numberOfFiltersApplied: { ...newNumberOfFiltersApplied },
      }
    }
    case types.UPDATE_OFFER_SELECTED: {
      const { offerSelected } = action.payload;
      return {
        ...state,
        offerSelected,
      }
    }
    case types.UPDATE_ISCLIENT: {
      const { isClient } = action.payload;
      return {
        ...state,
        isClient,
      }
    }
    case types.GET_PRODUCT_BY_ALIAS:
      return {
        ...state,
        productsDetailedError: false,
        productsDetailedFetching: true,
      }
    case types.GET_PRODUCT_BY_ALIAS_SUCCESS: {
      const { product } = action.payload;
      const { alias } = product;
      const newProductsDetailed = cloneStructure(state.productsDetailed);
      newProductsDetailed[alias] = product;
      const { offers } = product;
      const mainOffer = findMainOffer(offers);
      return {
        ...state,
        productsDetailed: newProductsDetailed,
        productsDetailedError: false,
        productsDetailedFetching: false,
        offerSelected: mainOffer,
      }
    }
    case types.GET_PRODUCT_BY_ALIAS_ERROR: {
      return {
        ...state,
        productsDetailedError: true,
        productsDetailedFetching: false,
      }
    }

    case types.GET_TERMINAL_LIST_BANNER:
      return {
        ...state,
        terminalLisBannerError: false,
        terminalLisBannerFetching: true,
      }
    case types.GET_TERMINAL_LIST_BANNER_SUCCESS: {
      const { product } = action.payload;
      const { data } = product;
      return {
        ...state,
        terminalListBanner: data,
      }
    }
    case types.GET_TERMINAL_LIST_BANNER_ERROR: {
      const { product } = action.payload;
      return {
        ...state,
        terminalListBanner: product,
        terminalLisBannerError: true,
        terminalLisBannerFetching: false,
      }
    }

    case types.GET_LEGAL_CONDITION_SUCCESS: {
      const { legalConditions } = action.payload;
      return {
        ...state,
        legalConditions,
      }
    }
    case types.GET_LEGAL_CONDITION:
      return {
        ...state,
        legalConditionsError: false,
        legalConditionsFetching: true,
      }
    case types.GET_LEGAL_CONDITION_ERROR: {
      const { legalConditions } = action.payload;
      return {
        ...state,
        legalConditions,
        legalConditionsError: true,
        legalConditionsFetching: false,
      }
    }
    case types.UPDATE_PRODUCT_CATEGORY: {
      const { productCategory } = action.payload;
      return {
        ...state,
        productCategory,
      }
    }
    case types.UPDATE_PRODUCT_SEGMENT: {
      const { productSegment } = action.payload;
      return {
        ...state,
        productSegment,
      }
    }
    case types.UPDATE_ELEMENTS_TO_SHOW: {
      const { elementsToShow } = action.payload;
      return {
        ...state,
        elementsToShow,
      }
    }
    case types.CHANGE_SORT_TYPE: {
      const { sortType } = action.payload;
      const { productsFiltered } = state;
      const newProductsFiltered = [...sortResultsFiltered(productsFiltered, sortType)];
      return {
        ...state,
        sortType,
        productsFiltered: newProductsFiltered,
      }
    }
    case types.SET_CURRENT_ALIAS: {
      return {
        ...state,
        currentAlias: action.payload.alias,
      }
    }
    default:
      return state
  }
}

export const actions = {
  updateIsClient: isClient => ({
    type: types.UPDATE_ISCLIENT,
    payload: { isClient },
  }),
  updateOfferSelected: offerSelected => ({
    type: types.UPDATE_OFFER_SELECTED,
    payload: { offerSelected },
  }),
  updateProductsSearchedFiltered: productsFiltered => ({
    type: types.UPDATE_PRODUCT_SEARCHED_FILTERED,
    payload: { productsFiltered },
  }),
  updateProductsSearched: productsSearchered => ({
    type: types.UPDATE_PRODUCTS_SEARCHED,
    payload: { ...productsSearchered },
  }),
  getProducts: () => ({
    type: types.GET_PRODUCTS,
  }),
  updateProductsFiltered: params => ({
    type: types.UPDATE_PRODUCTS_FILTERED,
    payload: { params },
  }),
  clearFiltersApplied: () => ({
    type: types.CLEAR_FILTERS_APPLIED,
  }),
  createProductFilterStatus: (
    productFilterStatus,
    kind,
    w2modified = {
      beggining: true,
      current: true,
    },
  ) => ({
    type: types.CREATE_PRODUCT_FILTER_STATUS,
    payload: { productFilterStatus, kind, w2modified },
  }),
  failureGetMyProducts: () => ({
    type: types.GET_PRODUCTS_ERROR,
  }),
  getProductsGeneric: data => ({
    type: types.GET_PRODUCTS_GENERIC,
    data,
  }),
  sucessGetProductsGeneric: (data, type, filter) => ({
    type: types.GET_PRODUCTS_GENERIC_SUCCESS,
    payload: { data, type, filter },
  }),
  getProductByAlias: params => ({
    type: types.GET_PRODUCT_BY_ALIAS,
    ...params,
  }),
  failureGetProductByAlias: () => ({
    type: types.GET_PRODUCT_BY_ALIAS_ERROR,
  }),
  sucessGetProductByAlias: product => ({
    type: types.GET_PRODUCT_BY_ALIAS_SUCCESS,
    payload: { product },
  }),
  updateProductCategory: productCategory => ({
    type: types.UPDATE_PRODUCT_CATEGORY,
    payload: { productCategory },
  }),
  updateProductSegment: productSegment => ({
    type: types.UPDATE_PRODUCT_SEGMENT,
    payload: { productSegment },
  }),
  updateElementsToShow: elementsToShow => ({
    type: types.UPDATE_ELEMENTS_TO_SHOW,
    payload: { elementsToShow },
  }),
  changeSorType: sortType => ({
    type: types.CHANGE_SORT_TYPE,
    payload: { sortType },
  }),
  setCurrentAlias: alias => ({
    type: types.SET_CURRENT_ALIAS,
    payload: { alias },
  }),
  getTerminalListBanner: terminalListBanner => ({
    type: types.GET_TERMINAL_LIST_BANNER,
    ...terminalListBanner,
  }),
  failureTerminalListBanner: product => ({
    type: types.GET_TERMINAL_LIST_BANNER_ERROR,
    payload: { product },
  }),
  sucessTerminalListBanner: product => ({
    type: types.GET_TERMINAL_LIST_BANNER_SUCCESS,
    payload: { product },
  }),
  sucessLegalConditions: legalConditions => ({
    type: types.GET_LEGAL_CONDITION_SUCCESS,
    payload: { legalConditions },
  }),
  getLegalConditions: legalConditions => ({
    type: types.GET_LEGAL_CONDITION,
    ...legalConditions,
  }),
  failureLegalConditions: legalConditions => ({
    type: types.GET_LEGAL_CONDITION_ERROR,
    payload: { legalConditions },
  }),
}

export default ProductsReducer;
