import React from 'react';
import { createMarkup } from '@bit/gerenciatd.utils.react-utils';
import Image from '../../../components/Image/Image';
import './ComercialInfoPromoCompactOriginalStaticLeft.css';

const ComercialInfoPromoCompactOriginalStaticLeft = ({ commercialInfo }) => {
  const {
    subTitle,
    secondaryDescription,
    mediaURL,
    layout,
    domId,
  } = commercialInfo;
  return (
    <div id={domId} className="commercialInfoPromoCompactOriginalStaticLeft_wrapper">
      <div className="container">
        <div className="commercialInfoFlexWrapper-reverse">
          <div className="commercialInfoFlexImg">
            <div className="commercialInfoPromoCompactOriginalStaticLeft_block-wrapper commercialInfoPromoCompactOriginalStaticLeft_image-wrapper">
              <Image
                wrapperClass="commercialInfoPromoCompactOriginalStaticLeft_image-wrapper"
                alt={subTitle}
                url={mediaURL}
              />
            </div>
          </div>
          <div className="commercialInfoFlexCaracs">
            <div className="commercialInfoPromoCompactOriginalStaticLeft_text-wrapper">
              <div className="commercialInfoPromoCompactOriginalStaticLeft_block-wrapper">
                <div className="commercialInfoPromoCompactOriginalStaticLeft_subtitle">
                  {subTitle}
                </div>
              </div>
              <div className="commercialInfoPromoCompactOriginalStaticLeft_block-wrapper">
                {secondaryDescription.map((value, index) => {
                  const key = `commercialInfoPromoCompactOriginalStaticLeft_secondary_${layout}_${index}`;
                  return (
                    <div
                      key={key}
                      dangerouslySetInnerHTML={createMarkup(value)}
                      className="commercialInfoPromoCompactOriginalStaticLeft_paragraph_small"
                    />
                  )
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default ComercialInfoPromoCompactOriginalStaticLeft;
