import React from 'react';
import PropTypes from 'prop-types';
import './BannerTitle.css';

/**
* Stateless component to draw an icon.
* @param {string} text path of the image.
* @returns {JSX} Component image JSX.
*/

const BannerTitle = ({ text }) => {
  return (
    <h2 className="bannerTitle-h2">{text}</h2>
  )
}

BannerTitle.propTypes = {
  text: PropTypes.string,
}


BannerTitle.defaultProps = {
  text: 'Ttulo',
}

export default BannerTitle;
