import React from 'react';
import Button from '@bit/gerenciatd.react.button';
import { addCustomCssRule } from '../../../utils/cssUtils';
import Stamp from '../../../components/Stamp/Stamp';
import BannerTitle from '../../../components/BannerTitle/BannerTitle';
import Description from '../../../components/Description/Description';
import { getColorForBackgroundBanner } from '../../../utils/commonUtils';
import './BannerRegularGeneric.css';

const BannerRegularGeneric = ({ ...props }) => {
  const {
    backgroundImage,
    backgroundImageMobile,
    backgroundColor,
    bannerTextContentAlign,
    terminalImage,
    terminalAlt,
    stampText,
    stampAlign,
    stampStyle,
    stampSecondaryText,
    stampSecondaryAlign,
    stampSecondaryStyle,
    bannerTitle,
    bannerDescription,
    mainPrice,
    originalPrice,
    buttonText,
    buttonType,
    buttonTheme,
    link,
    conditions,
  } = props;

  const handleClicked = () => {
    window.location.href = link;
  }

  let { priceHeaderText } = props;
  if (!priceHeaderText) {
    priceHeaderText = 'Desde';
  }
  const backgroundClassColor = getColorForBackgroundBanner(backgroundColor);

  addCustomCssRule('.bannerRegularGeneric_background', `background-image:url(${backgroundImageMobile})`, false);
  addCustomCssRule('.bannerRegularGeneric_background', `background-image:url(${backgroundImage})`, true, 768);

  return (
    <div className={`${backgroundClassColor} containerBanner`}>
      <div className="container">
        <div className="bannerRegularGeneric_flex-container">
          <div className="bannerRegularGeneric_flex-content_text">
            <div className="bannerRegularGeneric_wrapper">
              <div className={`banner-content-wrapper banner-content-align-${bannerTextContentAlign}`}>
                {stampText && (
                  <div className="bannerRegularGeneric_stamp-wrapper">
                    <Stamp text={stampText} alignText={stampAlign} typeStamp={stampStyle} />
                  </div>
                )}
                {stampSecondaryText && (
                  <div className="bannerRegularGeneric_stamp-wrapper">
                    <Stamp text={stampSecondaryText} alignText={stampSecondaryAlign} typeStamp={stampSecondaryStyle} />
                  </div>
                )}
                {bannerTitle && (
                  <BannerTitle text={bannerTitle} />
                )}
              </div>
              <div className="visible-xs">
                <div className="bannerRegularGeneric_image-display">
                  <img alt={terminalAlt} src={terminalImage} />
                </div>
              </div>
              <div className="bannerRegularGeneric_button-wrapper">
                {bannerDescription && (
                  <div className="">
                    <Description text={bannerDescription} />
                  </div>
                )}
                {conditions && (
                  <div className="bannerRegularGeneric_conditions">
                    {conditions}
                  </div>
                )}
                {mainPrice && (
                  <div className="bannerRegularGeneric_price-module">
                    <div className="bannerRegularGeneric_price_module_text">
                      {priceHeaderText}
                    </div>
                    <div className="bannerRegularGeneric_price-module_wrapper">
                      {originalPrice && (
                        <div className="bannerRegularGeneric_price-module_original">
                          {originalPrice}
                        </div>
                      )}
                      <div className="bannerRegularGeneric_price-module_main">
                        {mainPrice}
                      </div>
                    </div>
                    <div className="bannerRegularGeneric_price-module_taxes">
                      Impuestos incl.
                    </div>
                  </div>
                )}
                <Button wrapperClass="bannerRegularGeneric_button" type={buttonType} theme={buttonTheme} onClick={handleClicked} label={buttonText} />
              </div>
            </div>
          </div>
          <div className="bannerRegularGeneric_flex-content_image">
            <div className="bannerRegularGeneric_wrapper">
              <div className="bannerRegularGeneric_image-display hidden-xs">
                <img alt={terminalAlt} src={terminalImage} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default BannerRegularGeneric;
