import React from 'react';
import ColorSelector from '../../components/ColorSelector/ColorSelector';
import './ColorSelectorList.css';

const ColorSelectorList = ({ colorList, colorHexSelected, onclick }) => {
  return (
    <div className="colorSelectorList_wrapper">
      { colorList.map((item, index) => {
        const colorListKey = `color_${index}`;
        const { colorHex, colorName } = item;
        let activeStatus = 'unselected';
        if (colorHex === colorHexSelected) {
          activeStatus = 'selected';
        }
        return (
          <ColorSelector
            key={colorListKey}
            colorHex={colorHex}
            colorName={colorName}
            active={activeStatus}
            onclick={onclick}
          />
        )
      })}
    </div>
  )
}
export default ColorSelectorList;
