import React from 'react';
import DeviceItem from '../../DeviceItem/DeviceItem';
import './TerminalFileCrossSelling.css';

const TerminalFileCrossSelling = ({
  terminal,
  list,
  productSegment,
  productCategory,
  getProductByAlias,
}) => {
  if (!list || !(list.length > 0)) {
    return null;
  }
  const { name } = terminal;
  const composedTitle = `Alternativas al ${name}`;
  return (
    <div className="terminalFileCrossSelling_background">
      <div id="complements" className="container terminalFileCrossSelling_wrapper">
        <h3>{composedTitle}</h3>
        <div className="terminalFileCrossSelling_flex-container">
          { list.map((product, index) => {
            return (
              <div key={`${product.alias}-${index.toString()}`} className="terminalFileCrossSelling_flex-device-module">
                <div className={`deviceItem_padding deviceItem_deviceItem_layout_${product.layout}`}>
                  <DeviceItem
                    terminal={product}
                    extraInfoActive={false}
                    productSegment={productSegment}
                    productCategory={productCategory}
                    getProductByAlias={getProductByAlias}
                  />
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  )
}
export default TerminalFileCrossSelling;
