import React from 'react';
import Pill, { pillTypes, pillThemes, paddingSizes } from '@bit/gerenciatd.react.pill'
import './CapacityList.css';

const CapacityList = ({ storageList, storage, onClick }) => {
  return (
    <div className="capacityList_wrapper">
      { storageList.map((item, index) => {
        const capacityListKey = `capacity_${index}`;
        if (Number.isNaN(parseInt(item, 10))) {
          return false;
        }
        let isActive = false;
        const capacityNumber = parseInt(item, 10);
        const storageComposed = (capacityNumber >= 1024) ? `${parseInt((capacityNumber / 1024), 10)} TB` : `${capacityNumber} GB`;
        if (`${capacityNumber}` === storage.toString()) {
          isActive = true;
        }
        return (
          <Pill
            key={capacityListKey}
            paddingSize={paddingSizes.REDUCED}
            id={`PA-listado-termnales-selector-capacidad-dispositivo-${item.toString()}`}
            type={pillTypes.SOFT}
            theme={pillThemes.WHITE}
            label={storageComposed}
            onClick={() => onClick(capacityNumber)}
            active={isActive}
          />
        )
      })}
    </div>
  )
}

export default CapacityList;
