import { cloneStructure, makeItPlainArray } from '../commonUtils';
import { PRODUCT_CATEGORY } from '../../constants/constants';
import { sortTypes } from '../../constants/filters';
import {
  categoryFilter,
  priceFilter,
  batteryFilter,
  backSideCameraFilter,
  screenFilter,
  colorFilter,
  storageFilter,
  soFilter,
  brandFilter,
  connectivityFiler,
  pictureQualityFilter,
  screenQualityFilter,
  ratingFilter,
} from './filtersCatalog';

/* Those returns 0 or 1                     0           mutada */
export const notEqualsIntervalFilter = (filterDataA, filterDataB) => {
  let res = 0;
  if (
    (filterDataB.min > filterDataA.min)
  || (filterDataB.max < filterDataA.max)) {
    res = 1;
  }
  return res;
}
/* we only need mutated structure to know if some is activated */
export const notEmptyPillRatingCollection = (filterDataB) => {
  let res = 0;
  if (Array.isArray(filterDataB)) {
    const active = filterDataB.filter(item => item.active === true);
    if (active && active.length > 0) {
      // res = 1;
      res = active.length;
    }
  }
  return res;
}
/* DEDUCE HOW MANY FILTERS HAS BEEN APPLIED */
export const compareFiltersStructure = (structure0, mutatedstructue, productCategory) => {
  const structureA = structure0[productCategory];
  const structureAKeys = Object.keys(structureA);
  const structureB = mutatedstructue[productCategory];
  const structureBKeys = Object.keys(structureB);
  let res = 0;
  /* Remember those must have exactly the same filter keys */
  if (structureAKeys.length !== structureBKeys.length) {
    console.warn('STRANGER THING');
  }

  structureAKeys.forEach((filterKey) => {
    const filterDataA = structureA[filterKey];
    const filterDataB = structureB[filterKey];
    switch (filterKey) {
      case 'backsideCameraSliderStatus':
      case 'batterySliderStatus':
      case 'priceSliderStatus':
      case 'screenSliderStatus':
        res += notEqualsIntervalFilter(filterDataA, filterDataB);
        break;
      default:
        res += notEmptyPillRatingCollection(filterDataB);
        break;
    }
  })

  const resAux = {}
  resAux[productCategory] = res
  return resAux;
}

/* SEARCHER FUNCTIONS */
export const applyClickOnTerminals = (products, productsSearchered, productCategory) => {
  let productsFiltered = null;
  if (productsSearchered && productsSearchered.length !== 0) {
    productsFiltered = cloneStructure(productsSearchered);
  } else {
    productsFiltered = cloneStructure(products[productCategory]);
  }

  if (productsFiltered) {
    const terminalSearchedFiltered = [
      ...productsFiltered,
    ]
    return terminalSearchedFiltered
  }
  return null;
}

export const applySearcherOnTerminals = (products, productCategory, data) => {
  if (products && products[productCategory]) {
    let productsSearchered = cloneStructure(products[productCategory]);
    productsSearchered = productsSearchered.filter((terminal) => {
      let find = false;
      if (data !== '' && terminal.name.toUpperCase().indexOf(data.toUpperCase()) !== -1) find = true;
      return find;
    });
    return productsSearchered
  }
  return [];
}


/* FILTERING METHODS */
/* Create filter structure depends on productCategory=tv,smartphone and filterproperties */
export const createNewFilter = (
  newPropertyKey,
  newPropertyValue,
  productCategory,
  filtersStatus,
) => {
  // Creating new filter structure
  const lastFilters = filtersStatus[productCategory];
  const newFilterToApply = {
    ...lastFilters,
  };

  if (Array.isArray(newFilterToApply[newPropertyKey]) && newPropertyKey !== 'rating' && newPropertyKey !== 'pillCategory') {
    // if the filter is an array we have to change active
    const newValuesCollection = newFilterToApply[newPropertyKey].map((item) => {
      const newItem = { ...item }
      const { label, active } = newItem;
      if (label === newPropertyValue.label) {
        newItem.active = !active;
      }
      return newItem;
    });
    newFilterToApply[newPropertyKey] = newValuesCollection;
  } else {
    // regular behaviour
    newFilterToApply[newPropertyKey] = newPropertyValue;
  }

  const res = {}
  res[productCategory] = { ...newFilterToApply }
  return res;
}

export const checkFilter = (oneProduct, filters) => {
  /* ALL KIND OF FILTERS */
  const productFeaturesForFilter = oneProduct && oneProduct.filters ? oneProduct.filters : [];
  const filtersKey = Object.keys(filters);
  // PILL CATEGORY FILTER (oportunidades, seminuevos...) BEGINS
  if (filtersKey.includes('pillCategory')) {
    const { pillCategory } = filters;
    const pillCategoryCnd = categoryFilter(pillCategory, productFeaturesForFilter);
    if (!pillCategoryCnd) {
      return false;
    }
  }
  // RATING
  if (filtersKey.includes('rating')) {
    const { rating } = filters;
    const cndRating = ratingFilter(rating, productFeaturesForFilter);
    if (!cndRating) {
      return false;
    }
  }
  // BRAND
  if (filtersKey.includes('brand')) {
    const { brand } = filters
    const cndBrand = brandFilter(brand, productFeaturesForFilter);
    if (!cndBrand) {
      return false;
    }
  }
  // CONNECTIVITY
  if (filtersKey.includes('connectivity')) {
    const { connectivity } = filters;
    const cndConnectivity = connectivityFiler(connectivity, productFeaturesForFilter);
    if (!cndConnectivity) {
      return false;
    }
  }
  // PICTURE QUALITY
  if (filtersKey.includes('pictureQuality')) {
    const { pictureQuality } = filters;
    const cndPictureQuality = pictureQualityFilter(pictureQuality, productFeaturesForFilter);
    if (!cndPictureQuality) {
      return false;
    }
  }
  // SCREEN QUALITY
  if (filtersKey.includes('screenQuality')) {
    const { screenQuality } = filters;
    const cndScreenQuality = screenQualityFilter(screenQuality, productFeaturesForFilter);
    if (!cndScreenQuality) {
      return false;
    }
  }

  // PRICE
  if (filtersKey.includes('priceSliderStatus')) {
    const { priceSliderStatus } = filters;
    const cndPrice = priceFilter(priceSliderStatus, productFeaturesForFilter);
    if (!cndPrice) {
      return false;
    }
  }
  // BATTERY
  if (filtersKey.includes('batterySliderStatus')) {
    const { batterySliderStatus } = filters;
    const cndBattery = batteryFilter(batterySliderStatus, productFeaturesForFilter)
    if (!cndBattery) {
      return false;
    }
  }
  // SCREEN SLIDER
  if (filtersKey.includes('screenSliderStatus')) {
    const { screenSliderStatus } = filters;
    const cndScreen = screenFilter(screenSliderStatus, productFeaturesForFilter);
    if (!cndScreen) {
      return false;
    }
  }
  // BACKSIDECAMERA
  if (filtersKey.includes('backsideCameraSliderStatus')) {
    const { backsideCameraSliderStatus } = filters;
    const cndBacksideCamera = backSideCameraFilter(
      backsideCameraSliderStatus,
      productFeaturesForFilter,
    )
    if (!cndBacksideCamera) {
      return false;
    }
  }
  // COLOR
  if (filtersKey.includes('color')) {
    if (filters.color && Array.isArray(productFeaturesForFilter.color)) {
      const cndColor = colorFilter(filters, productFeaturesForFilter);
      if (!cndColor) {
        return false;
      }
    }
  }

  // STORAGE
  if (filtersKey.includes('storage')) {
    const cndStorage = storageFilter(filters, productFeaturesForFilter);
    if (!cndStorage) {
      return false;
    }
  }
  // SO LIST
  if (filtersKey.includes('soList')) {
    const cndSoList = soFilter(filters, productFeaturesForFilter);
    if (!cndSoList) {
      return false;
    }
  }
  // BRAND LIST

  return true;
}

export const filterProcess = (products, filters) => products.filter(
  item => checkFilter(item, filters)
)

export const applyFilters2Products = (products, productCategory, filters) => {
  let productsFiltered = products[productCategory];
  const currentSetOfFilters = filters[productCategory];
  productsFiltered = filterProcess(productsFiltered, currentSetOfFilters);
  return productsFiltered;
}

export const applyFilterMaster = (
  newPropertyKey,
  newPropertyValue,
  products,
  productCategory,
  filtersStatus,
  reduxUpdateFunction,
) => {
  const newFilterToApply = createNewFilter(
    newPropertyKey,
    newPropertyValue,
    productCategory,
    filtersStatus,
  );
  /* Applying this filter */
  // const productsFiltered = applyFiltersOnTerminals();
  /* we should update on redux results and filters that we have applied */
  reduxUpdateFunction({
    products,
    productCategory,
    newFilterToApply,
  });
}

/* SORTING METHODS */
export const lowStorageFirst = (itemA, itemB) => {
  return parseInt(itemA.name, 10) - parseInt(itemB.name, 10)
}

export const lowPriceSort = (itemA, itemB) => {
  return itemA.filters.minPrice - itemB.filters.minPrice
}

export const highPriceSort = (itemA, itemB) => {
  return itemB.filters.minPrice - itemA.filters.minPrice
}

export const alphabeticSort = (itemA, itemB) => {
  return itemA.alias > itemB.itemB;
}

export const relevanceId = (itemA, itemB) => {
  return itemA.relevanceId - itemB.relevanceId;
}

export const sortResultsFiltered = (productsFiltered, sortType) => {
  let newProductsFilteredAndSorted = null;
  switch (sortType) {
    case sortTypes.LOW_PRICE:
      newProductsFilteredAndSorted = productsFiltered.sort(lowPriceSort);
      break;
    case sortTypes.HIGH_PRICE:
      newProductsFilteredAndSorted = productsFiltered.sort(highPriceSort);
      break;
    case sortTypes.ALPHABETIC:
      newProductsFilteredAndSorted = productsFiltered.sort(alphabeticSort);
      break;
    default:
      newProductsFilteredAndSorted = productsFiltered.sort(relevanceId);
      break;
  }
  return newProductsFilteredAndSorted;
}
