import React, { Component } from 'react';
import FlyOut, { flyOutActions } from '@bit/gerenciatd.react.fly-out';
import './LayerFlyout.css';
import { connect } from 'react-redux';
import Button from '@bit/gerenciatd.react.button';
import { createMarkup } from '@bit/gerenciatd.utils.react-utils';
import { actions as productActions } from '../../reducers/products';
import Link from '../../components/Link/Link';
import store from '../../app/store';
import { URLSPURCHASEPROCESS } from '../../constants/constants';
import { launchIsClientModal } from '../../utils/dataHandlers/purchaseDataHandler';

class LayerFlyout extends Component {
  constructor(props) {
    super(...props);
    props.getLegalConditions();
    this.state = {}
  }

  fundedButtonClicked = () => {
    const { productsSelected, terminal, isClient } = this.props;
    if (!launchIsClientModal(productsSelected, terminal, isClient)) {
      if (productsSelected.productSpecs === null
        || productsSelected.productSpecs === ''
        || productsSelected.productSpecs.storage === null
        || productsSelected.productSpecs.storage === ''
        || productsSelected.productSpecs.color === null
        || productsSelected.productSpecs.color === '') {
        window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-libre-cliente&codION=${productsSelected.codIon}&solicitarFinanciacion=si&codTerm=${terminal.metadata.cloudId}`;
      } else {
        window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-libre-cliente&codION=${productsSelected.codIon}&codTerm=${terminal.metadata.cloudId}&solicitarFinanciacion=si&productOffering=${productsSelected.productOffering}&idCapacidadTelco=${productsSelected.productSpecs.storage}&idColorTelco=${productsSelected.productSpecs.color}`;
      }
    }
  }

  launchIsClientFlyout = () => {
    const { typeOfFlyout } = this.props;
    const { legalConditions } = this.props;
    const flyoutInfo = legalConditions[typeOfFlyout];
    const { nameFlyout } = flyoutInfo;
    this.props.setFlyOutVisibility(nameFlyout);
  }

  render() {
    const { stock } = this.props;
    const { legalConditions } = this.props;
    if (!legalConditions) {
      return null;
    }
    const { typeOfFlyout } = this.props;
    const flyoutInfo = legalConditions[typeOfFlyout];
    const {
      nameFlyout,
      titleFlyout,
      subTitleFlyout,
      subTitleThreeFlyout,
      text1,
      body,
    } = flyoutInfo;
    const fullTitleFlyout = (
      <h2>{titleFlyout}</h2>
    )
    const fullSubTitleFlyout = (
      <h3>{subTitleFlyout}</h3>
    )
    const bodyFlyout = (
      <div className="bodyFlyout">
        <div className="layerFlyoutConditions_store-subtitle">
          {subTitleThreeFlyout}
        </div>
        <div
          className="spanSpace"
          dangerouslySetInnerHTML={createMarkup(text1)}
        />
        <div
          className="spanSpace"
          dangerouslySetInnerHTML={createMarkup(body)}
        />
        { stock > 0 && (
          <Button
            onClick={this.fundedButtonClicked}
            label="PAGO A PLAZOS"
            theme="Green"
          />
        )}
        <Link
          text="Preguntas Frecuentes"
          alignText="left"
          wrapperLabel="layerFlyoutConditions_funding-conditions"
          url="https://atencionalcliente.movistar.es/?f=1380"
          target ="_blank"
        />
      </div>
    )

    return (
      <div className="layerFlyoutConditions_quota-wrapper">
        <FlyOut
          idFlyOut={nameFlyout}
          title={fullTitleFlyout}
          subtitle={fullSubTitleFlyout}
          store={store}
        >
          {bodyFlyout}
        </FlyOut>
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  setFlyOutVisibility: idFlyOut => dispatch(flyOutActions.setFlyOutVisibility(idFlyOut)),
  getLegalConditions: () => dispatch(productActions.getLegalConditions()),
})

const mapStateToProps = state => ({
  productsSelected: state.products.offerSelected,
  isClient: state.products.isClient,
  legalConditions: state.products.legalConditions,
})

export default connect(mapStateToProps, mapDispatchToProps)(LayerFlyout);
