import React from 'react';
import Icon from '@bit/gerenciatd.react.icon';
import './AdvantagesBar.css';
import { iconBarMenu } from '../../constants/setupBasics'

/* No soy un container...., soy un componente y muy reutilizable */
/* Este componente debera de recibir un array de advantages
y ser capaz de retornarlas con un map ya que actualmente estoy
desaprovechado soy un componente que dibuja informacin esttica (hay que pensar en el futuro)

Proptypes !!!  y jsdoc definiendo las caracteristicas de este componente
*/

const AdvantagesBar = () => {
  return (
    <div className="container advantagesContainer">
      <div className="advantagesBar_wrapper">
        { iconBarMenu.map(iconItem => (
          <Icon
            key={iconItem.id}
            wrapperClass="advantagesBar_icon"
            id={iconItem.id}
            classIcon={iconItem.classIcon}
            label={iconItem.label}
            title={iconItem.label}
          />
        ))}
      </div>
    </div>
  )
}

export default AdvantagesBar;
