import { howAppIsRunning } from './commonUtils';
import { DEV } from '../constants/constants';

export const taggingButtonTypes = {
  MENU: 'menu',
  BUTTON: 'boton',
  LINK: 'enlace',
  TAB: 'tab',
}

/* this is used by pageviewVirtual */
export const addEvent2GTM = (events) => {
  if (howAppIsRunning() !== DEV) {
    const { dataLayerGTM } = window;
    const numberOfEvents = dataLayerGTM.length;
    const lastEvent = dataLayerGTM[(numberOfEvents - 1)];
    const { APpageName, APpage } = events;
    // push only if the last event: pageviewVirtual is not the same
    if (!(APpageName && APpageName === lastEvent.APpageName)) {
      window.dataLayerGTM.push(events);
    }
  }
}
export const push2DataLayer = (event = '', GAcategory = '', GAaction = '', GAlabel = '') => {
  if (howAppIsRunning() !== DEV) {
    const { dataLayerGTM } = window;
    const numberOfEvents = dataLayerGTM.length;
    let eventCounter = 0;
    let pushConfirmation = true;
    for (let dlindex = (numberOfEvents - 1); (dlindex >= 0 && (eventCounter < 5)); dlindex -= 1) {
      const currentEvent = dataLayerGTM[dlindex];
      if ((GAcategory === currentEvent.GAcategory) && (GAaction === currentEvent.GAaction)) {
        pushConfirmation = false;
        break;
      }
      eventCounter += 1;
    }

    // only push one time
    if (pushConfirmation) {
      dataLayerGTM.push({
        event,
        GAcategory,
        GAaction,
        GAlabel,
      });
    }
  }
}

export const sendButtonTagging = (eventType, value) => {
  if (howAppIsRunning() !== DEV) {
    const { dataLayerGTM } = window;
    if (dataLayerGTM) {
      dataLayerGTM.push({
        event: 'boton',
        APeventAction: eventType,
        APeventLabel: value,
      });
    }
  }
}

export const sendManagementTagging = (actionValue, labelValue) => {
  if (howAppIsRunning() !== DEV) {
    const { dataLayerGTM } = window;
    dataLayerGTM.push({
      GAcategory: 'eServicing',
      GAaction: actionValue,
      GAlabel: labelValue,
      event: 'asyncGTM',
    });
  }
}

export const detectCorrectButtonType = (typeToTest) => {
  let correctType = false;
  Object.keys(taggingButtonTypes).forEach((typeName) => {
    if (typeToTest === taggingButtonTypes[typeName]) {
      correctType = true;
    }
  });
  return correctType;
}

export const sendPriorityTagging = (clientePriority) => {
  const { dataLayerGTM } = window;
  if (dataLayerGTM) {
    dataLayerGTM.push({ clientePriority });
  }
}


export const sendTagging = (
  taggingType,
  id,
  managementValue
) => {
  if (howAppIsRunning() !== DEV) {
    if (!managementValue && taggingType && id && detectCorrectButtonType(taggingType)) {
      sendButtonTagging(taggingType, id);
    }
    if (managementValue && id) {
      sendManagementTagging(managementValue, id);
    }
  }
}
