import React from 'react';
import PropTypes from 'prop-types';
import './CounterFilter.css';

/**
* Stateless component to draw an icon.
* @param {number} counter Number of filters applied
* @returns {JSX} Component image JSX.
*/

const CounterFilter = ({ counter }) => {
  const filter = ` (${counter})`;
  return (<span>{filter}</span>)
}

CounterFilter.propTypes = {
  counter: PropTypes.number,
}


CounterFilter.defaultProps = {
  counter: 0,
}

export default CounterFilter;
