export const types = {
  /* SET Properties */
  SET_API_RECORD: 'COMMONS/SET_API_RECORD',
}

export const initialState = {
  apiRecords: {
    getAllItem: {},
    getItem: {},
    getTerminalListBanner: {},
  },
}

const UIReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SET_GRID_VISIBLE: {
      const newGridVisible = action.payload.visible;
      return {
        ...state,
        gridVisible: newGridVisible,
      }
    }
    case types.SET_API_RECORD: {
      const { structure, now } = action.payload;
      const tokkenStructure = structure.split('-|-');
      const tokkenStructureLength = tokkenStructure.length;
      const obj2Add = {}
      if (tokkenStructureLength === 2) {
        obj2Add[tokkenStructure[0]] = {
          ...state.apiRecords[tokkenStructure[0]],
        }
        obj2Add[tokkenStructure[0]][tokkenStructure[1]] = now;
      }
      const newApiRecords = {
        ...state.apiRecords,
        ...obj2Add,
      }
      return {
        ...state,
        apiRecords: newApiRecords,
      }
    }
    default:
      return state
  }
}

export const actions = {
  setApiRecord: (structure, now) => ({ type: types.SET_API_RECORD, payload: { structure, now } }),
}

export default UIReducer;
