import React from 'react';

const BannerPriceInfo = ({ claim, price }) => {

  if (!price) {
    return null;
  }
  return (
    <div>
      <div className="claim"><span>{claim}</span></div>
      <div className="price">
        <div className="number"><span>{price}</span></div>
        <div className="currencyMonthTax">
          <span className="currency"></span>
          <span className="month">/mes</span>
          <span className="tax">
            <span className="par">(</span>
            <span>IVA incl.</span>
            <span className="par">)</span>
          </span>
        </div>
      </div>
    </div>
  )
};

export default BannerPriceInfo;