import React, { Component } from 'react';
import { connect } from 'react-redux';
import Button, { buttonTypes, buttonThemes } from '@bit/gerenciatd.react.button';
import DeviceItem from '../../DeviceItem/DeviceItem';
import { actions as productActions } from '../../../reducers/products';
import { ELEMENTS_TO_SHOW } from '../../../constants/constants';
import NotFoundSvg from './notFoundsvg';
import { getCommercialNameOfProductCategory } from '../../../utils/commonUtils';
import './TerminalListGrid.css';

class TerminalListGrid extends Component {
  constructor(props) {
    super(...props);
    this.state = {}
  }

  seeMoreTerminals = () => {
    const { INCREMENT } = ELEMENTS_TO_SHOW;
    const { elementsToShow } = this.props;
    const newElementsToShow = elementsToShow + INCREMENT;
    this.props.updateElementsToShow(newElementsToShow);
  }

  render() {
    const {
      productsFiltered,
      productsError,
      elementsToShow,
      productCategory,
      productSegment,
      getProductByAlias,
    } = this.props;

    if (!productsFiltered) {
      return null;
    }
    if (productsError) {
      return (
        <div>
          Error
        </div>
      )
    }
    const totalNumber = productsFiltered.length;
    const productsFilteredToShow = productsFiltered.slice(0, elementsToShow);
    const todosListados = (totalNumber !== productsFilteredToShow.length);
    const productCategoryCommercialName = getCommercialNameOfProductCategory(productCategory);

    return (
      <div className="TerminalListGrid">
        <div className="terminalListGrid_flex-container">
          { productsFilteredToShow && productsFilteredToShow.map((product, index) => {
            return (
              <div key={`${product.alias}-${index.toString()}`} className={`terminalListGrid_flex-device-module-${productSegment}`}>
                <div className={`deviceItem_padding deviceItem_deviceItem_layout_${product.layout}`}>
                  <DeviceItem
                    terminal={product}
                    productSegment={productSegment}
                    productCategory={productCategory}
                    getProductByAlias={getProductByAlias}
                  />
                </div>
              </div>
            )
          })}
        </div>
        <div className="terminalListGrid_flex-button-container">
          {(productsFilteredToShow && productsFilteredToShow.length > 0)
            && (todosListados)
            && (
              <Button
                id="PA-listado-terminales-mas-resultados"
                magnamentValue="mas-resultados"
                type={buttonTypes.SOLID}
                theme={buttonThemes.GREEN}
                label={`VER MS ${productCategoryCommercialName.toUpperCase()}`}
                wrapperClass="terminalListGrid_seemore-display"
                onClick={this.seeMoreTerminals}
              />
            )}
          {(productsFilteredToShow && productsFilteredToShow.length === 0) && (
            <div className="terminalListGrid_noresults">
              <NotFoundSvg />
              <h3>
                No hemos encontrado ningn dispositivo.
                Prueba a eleminar algn filtro
              </h3>
            </div>
          )}
        </div>
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  getProductByAlias: params => dispatch(productActions.getProductByAlias(params)),
  updateElementsToShow: elementsToShow => dispatch(
    productActions.updateElementsToShow(elementsToShow),
  ),
})

const mapStateToProps = state => ({
  productsFiltered: state.products.productsFiltered,
  productCategory: state.products.productCategory,
  productSegment: state.products.productSegment,
  productDetailed: state.products.productDetailed,
  productsError: state.products.productsError,
  productsFetching: state.products.productsFetching,
  elementsToShow: state.products.elementsToShow,
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalListGrid);
