import axios from 'axios';
import { call, put } from 'redux-saga/effects';
import { API } from '../constants/api'
import { normalizeSingleTerminal } from './normalizers/productNormalizer';
import { actions as customizerActions } from '../reducers/customizer';

export const fetchBannerHome = (data) => {
  return axios({
    method: 'POST',
    withCredentials: true,
    data: JSON.stringify(data),
    url: 'https://customizer-dot-mov-prod10.appspot.com/rules/evaluate/activitie/advanced',
  })
}

export function* getBannerHome(args) {
  try {
    const { data } = args;
    const response = yield call(fetchBannerHome, data);
    console.log(response);
  } catch (err) {
    yield put(customizerActions.getBannerHomeKO())
  }
}
