
import { URLSPURCHASEPROCESS, FRACTIONALLIMITPRICES } from '../../constants/constants'

export const launchIsClientModal = (offerSelected, terminal, isClient) => {
  if (isClient === undefined || isClient === true) {
    if (offerSelected.clientPrice <= FRACTIONALLIMITPRICES) {
      if (!offerSelected.productSpecs) {
        window.location.href = `${URLSPURCHASEPROCESS.processCanje}?pid=clic-boton-meinteresa-libre-cliente&codION=${offerSelected.codIon}&codTerm=${terminal.metadata.cloudId}`;
      } else {
        const { clientPrice, quota } = offerSelected;
        sessionStorage.setItem('vValue', clientPrice);
        sessionStorage.setItem('vQuota', quota);
        window.location.href = `${URLSPURCHASEPROCESS.processTelco}?pid=clic-boton-meinteresa-libre-cliente&codION=${offerSelected.codIon}&codTerm=${terminal.metadata.cloudId}&productOffering=${offerSelected.productOffering}&idCapacidadTelco=${offerSelected.productSpecs.storage}&idColorTelco=${offerSelected.productSpecs.color}`;
      }
    } else {
      return false;
    }
  } else if (offerSelected.clientPrice === 0) {
    window.location.href = `${URLSPURCHASEPROCESS.processVTL}&pid=clic-boton-meinteresa-libre&codION=${offerSelected.codIon}&codTerm=${terminal.metadata.cloudId}&productOffering=${offerSelected.productOffering}&idCapacidadTelco=${offerSelected.productSpecs.storage}&idColorTelco=${offerSelected.productSpecs.color}`;
  } else {
    window.location.href = `${URLSPURCHASEPROCESS.processDirectVTL}&pid=clic-boton-meinteresa-libre&codION=${offerSelected.codIon}&codTerm=${terminal.metadata.cloudId}`;
  }
  return true;
}
